import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { ArrowButton, Modal } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { localizeDate } from '../../Utils/localizeUtils';

function SupplierView({ data, onRequestClose, open }) {
  const { t } = useTranslation();
  const [currPage, setCurrPage] = useState(1);
  const onLastPage = currPage * 10 >= data.length;
  return (
    <Modal
      width={600}
      open={open}
      title="Email modal"
      onRequestClose={onRequestClose}
    >
      <h3 style={{ textAlign: 'center' }}>{t('fields.businessUnits')}</h3>
      <p>
        {t('supplierView.showingPaginationWithCount', {
          first: (currPage - 1) * 10 + 1,
          last: onLastPage ? data.length : currPage * 10,
          totalCount: data.length,
        })}
      </p>
      <table>
        <thead>
          <tr style={{ fontWeight: 600 }}>
            <td>{t('fields.number')}</td>
            <td>{t('fields.name')}</td>
            <td>{t('supplierView.initialized')}</td>
          </tr>
        </thead>
        <tbody>
          {data.slice((currPage - 1) * 10, currPage * 10).map(item => {
            return (
              <tr key={item.number}>
                <td style={{ fontWeight: 600 }}>{item.number}</td>
                <td>{item.name}</td>
                <td style={{ fontStyle: 'italic' }}>
                  {localizeDate(item.dateCreated)}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <ArrowButton
        disabled={currPage === 1}
        direction="left"
        onClick={() => setCurrPage(currPage - 1)}
      />
      <ArrowButton
        disabled={onLastPage}
        direction="right"
        onClick={() => setCurrPage(currPage + 1)}
      />
    </Modal>
  );
}

export default SupplierView;

SupplierView.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};
