import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Checkbox,
  Button,
  Field,
  FieldLabel,
  FileUpload,
  FieldErrorMap,
  Progress,
  Select,
  Tabs,
  Textarea,
  Tooltip,
} from 'taulia-ui';
import { Trans, withTranslation } from 'react-i18next';
import {
  downloadSampleSupplierInviteList,
  parseStringToArray,
} from '../../Utils/utils';
import { RequestContext } from '../../contexts';

class AddSupplierForm extends Component {
  static propTypes = {
    addSupplierResultsCallback: PropTypes.func,
    erpClients: PropTypes.arrayOf(PropTypes.shape()),
    params: PropTypes.shape(),
    segments: PropTypes.arrayOf(PropTypes.shape()),
    t: PropTypes.func.isRequired,
    viewSegments: PropTypes.func.isRequired,
  };

  static defaultProps = {
    addSupplierResultsCallback: () => null,
    erpClients: [],
    params: {},
    segments: [],
  };

  state = {
    activeTab: 'upload',
    erpClientId: false,
    file: null,
    supplierNumbers: { value: '', error: null },
    upload: { value: null, error: null },
    loading: false,
    previewOnly: false,
    replaceExistingAssociations: false,
    associationSummary: {
      countNewAssociations: 0,
      countExistingAssociations: 0,
      suppliersMissingDetails: [],
    },
    defaultAssociationSummary: {
      countNewAssociations: 0,
      countExistingAssociations: 0,
      suppliersMissingDetails: [],
    },
  };

  onSubmit = event => {
    event.preventDefault();

    const { activeTab } = this.state;

    if (activeTab === 'upload') {
      this.onUploadSubmit();
    } else {
      this.onInputSubmit();
    }
  };

  onSupplierNumberInputChange = event => {
    const { supplierNumbers, defaultAssociationSummary } = this.state;
    this.setState({
      supplierNumbers: { ...supplierNumbers, value: event.target.value },
      associationSummary: defaultAssociationSummary,
    });
  };

  onSupplierNumberInputBlur = () => {
    const { supplierNumbers } = this.state;

    if (this.isMultiErp()) {
      this.setState({
        supplierNumbers: {
          ...supplierNumbers,
          value: parseStringToArray(supplierNumbers.value).join(', '),
        },
      });
      return;
    }

    this.setState(
      {
        supplierNumbers: {
          ...supplierNumbers,
          value: parseStringToArray(supplierNumbers.value).join(', '),
        },
        previewOnly: true,
        loading: true,
      },
      this.onInputSubmit
    );
  };

  onUploadChange = file => {
    const { defaultAssociationSummary } = this.state;

    if (this.isMultiErp()) {
      this.setState({
        file,
        upload: { value: file.name, error: null },
        associationSummary: defaultAssociationSummary,
      });
      return;
    }

    this.setState(
      {
        file,
        upload: { value: file.name, error: null },
        previewOnly: true,
        loading: true,
        associationSummary: defaultAssociationSummary,
      },
      this.onUploadSubmit
    );
  };

  onUploadSubmit = () => {
    const {
      erpClientId,
      file,
      previewOnly,
      replaceExistingAssociations,
      upload,
    } = this.state;
    const { params } = this.props;
    const request = this.context;

    this.setState({ loading: true }, () => {
      const data = new FormData();
      data.append('file', file);
      data.append('segmentId', params.id);
      data.append('previewOnly', previewOnly);
      data.append('replaceExistingAssociations', replaceExistingAssociations);

      if (erpClientId) {
        data.append('erpClientId', erpClientId);
      }

      request('POST', '/api/segment/associateSuppliersUpload')
        .send(data)
        .then(res => this.addSuppliersToSegmentCompleted(res))
        .catch(err => {
          console.error('[associateSuppliersUpload]', err); // eslint-disable-line no-console
          this.setState({
            upload: {
              value: upload.value,
              error: err.response.body.code,
            },
            loading: false,
          });
        });
    });
  };

  onInputSubmit = () => {
    const {
      erpClientId,
      previewOnly,
      replaceExistingAssociations,
      supplierNumbers,
    } = this.state;

    const { params } = this.props;
    const request = this.context;

    const data = {
      segmentId: params.id,
      supplierNumbers: parseStringToArray(supplierNumbers.value),
      previewOnly,
      replaceExistingAssociations,
    };

    if (erpClientId) {
      data.erpClientId = erpClientId;
    }

    this.setState({ loading: true }, () => {
      request('POST', '/api/segment/associateSuppliers')
        .send(data)
        .then(res => this.addSuppliersToSegmentCompleted(res))
        .catch(err => {
          console.error('[associateSuppliers]', err); // eslint-disable-line no-console
          this.setState({
            previewOnly: false,
            loading: false,
            supplierNumbers: {
              value: supplierNumbers.value,
              error: err.response.body.code,
            },
          });
        });
    });
  };

  onTabChange = activeTab => {
    const { defaultAssociationSummary } = this.state;
    this.setState({
      activeTab,
      associationSummary: defaultAssociationSummary,
    });
  };

  updateReplaceExistingAssociations = () => {
    const { replaceExistingAssociations } = this.state;
    this.setState({
      replaceExistingAssociations: !replaceExistingAssociations,
    });
  };

  addSuppliersToSegmentCompleted = res => {
    const { addSupplierResultsCallback } = this.props;
    const { associationSummary, supplierSegment } = res.body;

    this.setState({
      associationSummary,
      loading: false,
      previewOnly: false,
    });

    if (!supplierSegment.previewOnly) {
      addSupplierResultsCallback(res.body);
    }
  };

  static contextType = RequestContext;

  isMultiErp() {
    const { erpClients } = this.props;
    return erpClients.length > 1;
  }

  isSubmitButtonDisabled() {
    const {
      supplierNumbers,
      erpClientId,
      upload,
      associationSummary,
    } = this.state;
    const {
      countNewAssociations,
      countExistingAssociations,
    } = associationSummary;
    return (
      (!supplierNumbers.value && !upload.value) ||
      (!erpClientId && this.isMultiErp()) ||
      (this.isMultiErp() === false &&
        countNewAssociations === 0 &&
        countExistingAssociations === 0)
    );
  }

  renderPreviewResultsSummary() {
    const { associationSummary } = this.state;
    const { t } = this.props;
    const {
      countNewAssociations,
      countExistingAssociations,
      suppliersMissingDetails,
    } = associationSummary;

    let missingSupplierDetails;

    if (suppliersMissingDetails.length) {
      missingSupplierDetails = (
        <Tooltip theme="light" align="center">
          <span data-testid="missing-details-tooltip">
            {t('supplierSegment.supplierSegmentList.viewNotFoundSuppliers')}
          </span>
          <div>{suppliersMissingDetails.join(', ')}</div>
        </Tooltip>
      );
    } else {
      missingSupplierDetails = (
        <Button theme="text" disabled>
          {t('supplierSegment.supplierSegmentList.viewNotFoundSuppliers')}
        </Button>
      );
    }

    const resultsSummary = (
      <div className="supplier-segment">
        <div className="results-summary-container">
          <table id="resultsSummary" className="add-supplier-summary">
            <thead>
              <tr className="header">
                <td colSpan="3">
                  <div>
                    {t(
                      'supplierSegment.segmentAddSupplierForm.supplierSummary'
                    )}
                  </div>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="label">
                  {t('supplierSegment.segmentAddSupplierForm.newAdditions')}
                  &#58;
                </td>
                <td>{countNewAssociations}</td>
                <td>{null}</td>
              </tr>
              <tr>
                <td className="label">
                  {t('supplierSegment.segmentAddSupplierForm.alreadyInSegment')}
                  &#58;
                </td>
                <td>{countExistingAssociations}</td>
                <td>{null}</td>
              </tr>
              <tr>
                <td className="label">
                  {t('supplierSegment.segmentAddSupplierForm.notFound')}
                  &#58;
                </td>
                <td>{suppliersMissingDetails.length}</td>
                <td>{missingSupplierDetails}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );

    return resultsSummary;
  }

  renderCheckboxItems() {
    const { replaceExistingAssociations } = this.state;
    const { t } = this.props;

    return (
      <div style={{ paddingTop: '15px' }}>
        <Field>
          <Checkbox
            data-testid="replace-existing-associations-checkbox"
            defaultChecked={replaceExistingAssociations === true}
            id="replace_existing_associations"
            name="replaceExistingAssociations"
            onChange={this.updateReplaceExistingAssociations}
          >
            <strong>
              {t('supplierSegment.segmentAddSupplierForm.clearBeforeAdding')}
            </strong>
            <div className="padding">
              {t(
                'supplierSegment.segmentAddSupplierForm.replaceSuppliersWarning'
              )}
            </div>
          </Checkbox>
        </Field>
      </div>
    );
  }

  renderErpClients() {
    const { erpClientId } = this.state;
    const { erpClients, t } = this.props;
    const erpOptions = erpClients.map(client => ({
      value: client.clientId,
      label: client.longDescription,
    }));
    return (
      <div style={{ marginBottom: '20px' }}>
        <FieldLabel htmlFor="erp-client">
          {t('supplierSegment.segmentAddSupplierForm.erpSystem')}
        </FieldLabel>
        <Select
          id="erp-client"
          multiple={false}
          noSearchResultsLabel={t(
            'bulkInvites.searchPage.noSearchResultsLabel'
          )}
          onChange={id =>
            this.setState({
              erpClientId: id,
            })
          }
          options={erpOptions}
          placeholder={t(
            'supplierSegment.segmentAddSupplierForm.selectErpSystem'
          )}
          searchPlaceholder={t('bulkInvites.searchPage.searchPlaceholder')}
          value={erpClientId}
        />
      </div>
    );
  }

  renderFormContent() {
    const {
      activeTab,
      defaultAssociationSummary,
      supplierNumbers,
      upload,
    } = this.state;
    const { t } = this.props;

    const tabData = ['upload', 'input'].map(tab => ({
      active: tab === activeTab,
      callback: () =>
        this.setState({
          activeTab: tab,
          associationSummary: defaultAssociationSummary,
        }),
      label: t(`bulkInvites.individualPage.options.${tab}.link`),
    }));

    return (
      <div>
        <Tabs data={tabData} />
        {activeTab === 'upload' && (
          <div style={{ display: 'flex' }}>
            <div className="supplier-numbers">
              {this.isMultiErp() && this.renderErpClients()}
              <Field validationState={upload.error ? 'error' : null}>
                <FieldLabel htmlFor="file-upload">
                  {t('supplierSegment.segmentAddSupplierForm.selectCsvFile')}
                </FieldLabel>
                <FileUpload
                  acceptedFileTypes={['.csv']}
                  onChange={this.onUploadChange}
                />
                <FieldErrorMap code={upload.error}>
                  <span key="invalidType">
                    {t(
                      'bulkInvites.individualPage.options.upload.error.invalidType'
                    )}
                  </span>
                  {t(
                    'bulkInvites.individualPage.options.upload.error.noContent'
                  )}
                </FieldErrorMap>
                <p className="padding">
                  <Trans
                    values={{
                      hereLink: t(
                        'bulkInvites.individualPage.options.upload.infoLink'
                      ),
                    }}
                    i18nKey="bulkInvites.individualPage.options.upload.info"
                    components={[
                      <Button
                        style={{ display: 'inline-block' }}
                        theme="text"
                        onClick={downloadSampleSupplierInviteList}
                      />,
                    ]}
                  />
                </p>
              </Field>
            </div>
            <div>
              {!this.isMultiErp() && this.renderPreviewResultsSummary()}
            </div>
          </div>
        )}
        {activeTab === 'input' && (
          <div style={{ display: 'flex' }}>
            <div className="supplier-numbers">
              {this.isMultiErp() && this.renderErpClients()}
              <Field>
                <FieldLabel htmlFor="textInput">
                  {t(
                    'supplierSegment.segmentAddSupplierForm.copyPasteSupplierNumbers'
                  )}
                </FieldLabel>
                <Textarea
                  id="textInput"
                  style={{ resize: 'vertical' }}
                  rows="8"
                  cols="45"
                  maxLength="1024"
                  name="supplierNumbers"
                  value={supplierNumbers.value}
                  onChange={this.onSupplierNumberInputChange}
                  onBlur={this.onSupplierNumberInputBlur}
                />
              </Field>
            </div>
            <div>
              {!this.isMultiErp() && this.renderPreviewResultsSummary()}
            </div>
          </div>
        )}
      </div>
    );
  }

  renderForm() {
    const { t, viewSegments } = this.props;
    return (
      <div>
        <form id="addSupplierForm" onSubmit={this.onSubmit}>
          {this.renderFormContent()}
          <div>
            {this.renderCheckboxItems()}
            <div style={{ float: 'right' }}>
              <Button
                id="add-suppliers-form-cancel-button"
                onClick={viewSegments}
                theme="text"
              >
                {t('standard.cancel')}
              </Button>
              <Button
                id="add-suppliers-form-submit-button"
                type="submit"
                theme="primary"
                disabled={this.isSubmitButtonDisabled()}
              >
                {t('supplierSegment.supplierSegmentList.addSuppliers')}
              </Button>
            </div>
          </div>
        </form>
      </div>
    );
  }

  render() {
    const { loading } = this.state;
    const { params, segments, t } = this.props;
    const segmentName =
      segments.length &&
      segments.filter(segment => segment.id === params.id)[0].name;
    return (
      <div className="supplier-segment">
        {loading && <Progress />}
        <h3>
          {t('supplierSegment.segmentAddSupplierForm.addSuppliersToSegment', {
            segmentName,
          })}
        </h3>
        <p style={{ marginBottom: '10px' }}>
          {t('supplierSegment.segmentAddSupplierForm.supplierFormSummary')}
        </p>
        <div className="form-summary-container">{this.renderForm()}</div>
      </div>
    );
  }
}
export default withTranslation()(AddSupplierForm);
