import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Checkbox,
  Field,
  FieldLabel,
  FileUpload,
  Modal,
} from 'taulia-ui';
import { Trans, useTranslation } from 'react-i18next';
import { downloadBlob, genericErrorToast } from '../../Utils/utils';
import { RequestContext } from '../../contexts';

function UploadContactsModal({ open, onRequestClose }) {
  const { t } = useTranslation();
  const request = useContext(RequestContext);
  const [file, setFile] = useState();
  const [isUploading, setIsUploading] = useState(false);
  const [overrideContacts, setOverrideContacts] = useState(false);
  const [results, setResults] = useState();
  const sampleCsvContentBlob = new Blob(
    [
      'SupplierNumber_Required,EmailAddress,FirstName,LastName,PhoneNumber,MobileNumber,FaxNumber,Department,Role,Notes,ErpClientId,"Note: The first row (header) is used to read the csv and needs to remain in the csv."\n000000TEST,jane.smith@example.com,Jane,Smith,555-555-5555,123-456-7890,111-111-1111,AP,CEO,Example,ExampleErpClientId',
    ],
    {
      type: 'text/csv;charset=utf-8',
    }
  );

  const beginUpload = () => {
    setIsUploading(true);
    const data = new FormData();
    data.append('file', file);
    data.append('overrideContacts', overrideContacts.toString());
    request('POST', '/api/batchUpload')
      .send(data)
      .then(response => {
        setResults(response.body.result);
        setIsUploading(false);
      })
      .catch(() => {
        genericErrorToast();
        setIsUploading(false);
      });
  };

  const downloadResults = () => {
    const headers = [
      'Department',
      'Email Address',
      'Erp Client Id',
      'Fax Number',
      'First Name',
      'Ignored Reason (if applicable)',
      'Last Name',
      'Mobile Number',
      'Notes',
      'Phone Number',
      'Role',
      'Supplier #',
      'Update Status',
    ];
    const updatedContactFields = [
      'department',
      'emailAddress',
      'erpClientId',
      'fax',
      'firstName',
      'ignoreReason',
      'lastName',
      'mobileNumber',
      'notes',
      'phoneNumber',
      'role',
      'supplierNumber',
      'updateStatus',
    ];
    let csv = '';
    csv += `${headers.join(',')}\n`;
    results.updatedContactList.forEach(item => {
      let row = '';
      updatedContactFields.forEach(key => {
        let fieldValue = `${item[key]}`;
        if (fieldValue === `undefined`) {
          fieldValue = '';
        }
        row += `${fieldValue},`;
      });
      csv += `${row}\n`;
    });
    const resultsToDownload = new Blob([csv], {
      type: 'text/csv;charset=utf-8',
    });
    downloadBlob(resultsToDownload, 'upload-contacts-details.csv');
  };

  const onClose = () => {
    setResults();
    onRequestClose();
  };

  return (
    <Modal width={800} open={open} onRequestClose={onRequestClose}>
      <div>
        <h3>{t('supplierSearch.uploadContacts')}</h3>
        {results ? (
          <section>
            <p style={{ margin: '20px 0' }}>
              {t('supplierSearch.uploadProcessed')}
            </p>
            <p>
              <strong>{results.totalAdded}</strong>
              &nbsp;
              {t('supplierSearch.added')}
            </p>
            <p>
              <strong>{results.totalUpdated}</strong>
              &nbsp;
              {t('supplierSearch.updated')}
            </p>
            <p>
              <strong>{results.totalIgnored}</strong>
              &nbsp;
              {t('supplierSearch.ignored')}
            </p>
            <div key="actions" style={{ float: 'right' }}>
              <Button
                data-testid="download-results"
                onClick={downloadResults}
                theme="primary"
              >
                {t('supplierSearch.downloadResults')}
              </Button>
              <Button data-testid="close-upload-modal" onClick={onClose}>
                {t('standard.close')}
              </Button>
            </div>
          </section>
        ) : (
          <section>
            <p>
              <Trans
                i18nKey="supplierSearch.uploadText"
                components={[
                  <Button
                    style={{ display: 'inline' }}
                    theme="text"
                    onClick={() => {
                      downloadBlob(
                        sampleCsvContentBlob,
                        'supplierUploadTemplateForMultiUserInvites.csv'
                      );
                    }}
                  />,
                ]}
              />
            </p>
            <Field>
              <FieldLabel htmlFor="file-upload">
                {t('bulkInvites.individualPage.options.upload.label')}
              </FieldLabel>
              <FileUpload acceptedFileTypes={['.csv']} onChange={setFile} />
            </Field>
            <Checkbox
              data-testid="override-contacts"
              checked={overrideContacts}
              onChange={() => setOverrideContacts(!overrideContacts)}
            >
              <strong>{t('supplierSearch.overrideLabel')}</strong>
              <p>{t('supplierSearch.overrideText')}</p>
            </Checkbox>
            <div key="actions" style={{ float: 'right' }}>
              <Button
                disabled={!file}
                onClick={beginUpload}
                spinning={isUploading}
                theme="primary"
              >
                {t('supplierSearch.beginUpload')}
              </Button>
            </div>
          </section>
        )}
      </div>
    </Modal>
  );
}

UploadContactsModal.propTypes = {
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
};

UploadContactsModal.defaultProps = {
  open: true,
};

export default UploadContactsModal;
