import React from 'react';
import PropTypes from 'prop-types';
import {
  BooleanCell,
  DateCell,
  FieldList,
  Modal,
  StatusIndicator,
  TextCell,
} from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { statusColorMap } from '../../Utils/SupplierUtils';

function ViewUserModal({ open, data, onRequestClose }) {
  const { t } = useTranslation();

  const statusIndicatorComponent = (
    <StatusIndicator color={statusColorMap[data.enrollmentStatus]}>
      {t(`fields.combinedEnrollmentStatus.enumValues.${data.enrollmentStatus}`)}
    </StatusIndicator>
  );

  const fieldListData = {
    colOneData: [
      {
        dataTestId: 'user-status',
        label: t('fields.status'),
        value: statusIndicatorComponent,
      },
      {
        dataTestId: 'user-title',
        label: t('profile.title'),
        value: <TextCell value={data.title} />,
      },
      {
        dataTestId: 'user-phone-number',
        label: t('profile.phoneNumber'),
        value: <TextCell value={data.phoneNumber} />,
      },
      {
        dataTestId: 'user-locale',
        label: t('supplierView.locale'),
        value: data.locale,
      },
    ],
    colTwoData: [
      {
        dataTestId: 'user-dateInvited',
        label: t('fields.dateInvited'),
        value: <DateCell value={data.dateInvited} />,
      },
      {
        dataTestId: 'user-lastLogin',
        label: t('fields.lastLogin'),
        value: <DateCell value={data.lastLogin} />,
      },
      {
        dataTestId: 'user-lastPasswordResetRequest',
        label: t('supplierView.lastPasswordReset'),
        value: <DateCell value={data.lastPasswordResetRequest} />,
      },
      {
        dataTestId: 'user-twoFactorAuthEnabled',
        label: t('supplierView.twoFactorAuthEnabled'),
        value: (
          <BooleanCell
            value={data.twoFactorAuthEnabled}
            column={{
              falseLabel: t('filters.falseLabel'),
              trueLabel: t('filters.trueLabel'),
            }}
          />
        ),
      },
    ],
    colOneWidth: 180,
    colTwoWidth: 180,
  };

  return (
    <Modal width={800} open={open} onRequestClose={onRequestClose}>
      <div className="supplier-view" data-testid="view-user-modal">
        <h3>{`${data.firstName} ${data.lastName}`}</h3>
        <p className="subtext">{data.emailAddress}</p>
        <hr style={{ margin: '20px -20px' }} />
        <FieldList {...fieldListData} />
      </div>
    </Modal>
  );
}

export default ViewUserModal;

ViewUserModal.propTypes = {
  data: PropTypes.shape({
    dateInvited: PropTypes.string,
    emailAddress: PropTypes.string,
    enrollmentStatus: PropTypes.string,
    firstName: PropTypes.string,
    lastLogin: PropTypes.string,
    lastName: PropTypes.string,
    lastPasswordResetRequest: PropTypes.string,
    locale: PropTypes.string,
    phoneNumber: PropTypes.string,
    title: PropTypes.string,
    twoFactorAuthEnabled: PropTypes.bool,
  }),
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

ViewUserModal.defaultProps = {
  data: {
    dateInvited: '',
    emailAddress: '',
    enrollmentStatus: '',
    firstName: '',
    lastLogin: '',
    lastName: '',
    lastPasswordResetRequest: '',
    locale: '',
    phoneNumber: '',
    title: '',
    twoFactorAuthEnabled: false,
  },
};
