import React, { useState } from 'react';
import { Button, Radio } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import CONSTANTS from '../../globals';

const StartPage = () => {
  const { t } = useTranslation();
  const [type, setType] = useState(null);
  const history = useHistory();

  return (
    <div>
      <h1>{t('bulkInvites.title')}</h1>
      <h3>{t('bulkInvites.startPage.title')}</h3>
      <p>{t('bulkInvites.startPage.text')}</p>
      <div className="type-pickers">
        <Radio
          checked={type === CONSTANTS.SUPPLIERS.BULK_INVITES.INDIVIDUAL}
          data-testid="individual-radio"
          name="type"
          onChange={() => setType(CONSTANTS.SUPPLIERS.BULK_INVITES.INDIVIDUAL)}
          value={CONSTANTS.SUPPLIERS.BULK_INVITES.INDIVIDUAL}
        >
          <p className="title">
            {t('bulkInvites.startPage.typePickers.individual.title')}
          </p>
          <p>{t('bulkInvites.startPage.typePickers.individual.text')}</p>
        </Radio>
        <Radio
          checked={type === CONSTANTS.SUPPLIERS.BULK_INVITES.SEARCH}
          data-testid="attribute-radio"
          name="type"
          onChange={() => setType(CONSTANTS.SUPPLIERS.BULK_INVITES.SEARCH)}
          value={CONSTANTS.SUPPLIERS.BULK_INVITES.SEARCH}
        >
          <p className="title">
            {t('bulkInvites.startPage.typePickers.search.title')}
          </p>
          <p>{t('bulkInvites.startPage.typePickers.search.text')}</p>
        </Radio>
      </div>
      <div className="submit">
        <Button
          theme="primary"
          onClick={() => history.push(type)}
          disabled={!type}
        >
          {t('bulkInvites.startPage.submit')}
        </Button>
      </div>
    </div>
  );
};

export default StartPage;
