import { useEffect, useState, useContext } from 'react';
import PropTypes from 'prop-types';
import qs from 'qs';
import { RequestContext } from '../contexts';

const useEnabledSettingsFetch = (buyerSettings, systemSettings) => {
  const [error, setError] = useState(false);
  const [isEnabled, setIsEnabled] = useState(false);
  const [loading, setLoading] = useState(false);
  const request = useContext(RequestContext);

  const query = qs.stringify(
    {
      buyerSettings,
      systemSettings,
    },
    { arrayFormat: 'brackets' }
  );

  const getData = () => {
    setLoading(true);
    request('GET', `/api/setting/enabled-settings?${query}`)
      .then(res => {
        setIsEnabled(res.body);
        setLoading(false);
      })
      .catch(() => {
        setError(true);
        setLoading(false);
      });
  };

  useEffect(() => getData(), []);

  return { error, isEnabled, loading };
};

export default useEnabledSettingsFetch;

useEnabledSettingsFetch.propTypes = {
  buyerSettings: PropTypes.shape(),
  systemSettings: PropTypes.shape(),
};

useEnabledSettingsFetch.defaultProps = {
  buyerSettings: null,
  systemSettings: null,
};
