/* eslint-disable no-param-reassign */
import React, { useContext, useEffect, useState } from 'react';
import { Header } from 'taulia-ui';
import { useLocation } from 'react-router-dom';
import { useFetchContext } from '../../hooks';
import { UserSettingsContext } from '../../contexts';
import { GenericErrorMessage } from '../../Utils/GenericErrorMessage';

const setActiveMenuItem = (navigationDirections, location) => {
  if (!navigationDirections || !navigationDirections.length) {
    return navigationDirections;
  }
  const pathname = location?.pathname ?? '';
  navigationDirections.forEach(menuItem => {
    menuItem.children = setActiveMenuItem(menuItem.children, location);
    menuItem.isActive =
      menuItem.path === pathname ||
      (menuItem.children &&
        !!menuItem.children.find(child => {
          return child.isActive;
        }));
  });
  return navigationDirections;
};

const HeaderWrapper = () => {
  const [directions, setDirections] = useState();
  const location = useLocation();
  const userInfo = useContext(UserSettingsContext);
  const { data: mainMenuData, error: mainMenuError } = useFetchContext(
    '/api/main-menu'
  );

  useEffect(() => {
    if (mainMenuData && location?.pathname) {
      setDirections(setActiveMenuItem([...mainMenuData], location));
    }
  }, [mainMenuData, location]);

  useEffect(() => {
    if (mainMenuError) {
      // eslint-disable-next-line no-console
      console.error('Error loading navigation', mainMenuError);
    }
  }, [mainMenuError]);

  if (mainMenuError) {
    return <GenericErrorMessage />;
  }

  if (directions) {
    return (
      <Header
        directions={directions}
        user={{ name: `${userInfo.userFirstName} ${userInfo.userLastName}` }}
        company={{ name: userInfo.companyName }}
      />
    );
  }

  return null;
};

export default HeaderWrapper;
