import React, { useContext, useEffect, useState } from 'react';
import { Alert, Link, Progress } from 'taulia-ui';
import { useTranslation, Trans } from 'react-i18next';
import CONSTANTS from '../../globals';
import { RequestContext } from '../../contexts';

const BulkInvitesSuccessPage = () => {
  const [loading, setLoading] = useState(true);
  const [showError, setShowError] = useState(false);
  const { t } = useTranslation();
  const request = useContext(RequestContext);

  useEffect(() => {
    request('DELETE', '/api/bulkInvites/criteria')
      .then(() => {
        setLoading(false);
        setShowError(false);
      })
      .catch(() => {
        setLoading(false);
        setShowError(true);
      });
  }, [request]);

  if (loading) {
    return <Progress />;
  }

  if (showError) {
    return (
      <Alert inline theme="danger">
        <strong>{t('bulkInvites.successPage.error.title')}</strong>
        <p>
          <Trans
            i18nKey="bulkInvites.successPage.error.text"
            components={[<Link to={CONSTANTS.SUPPLIERS.BULK_INVITES.START} />]}
          />
        </p>
      </Alert>
    );
  }
  return (
    <>
      <h1>{t('bulkInvites.title')}</h1>
      <h3>{t('bulkInvites.successPage.title')}</h3>
      <Trans
        components={[<Link to={CONSTANTS.SUPPLIERS.SEARCH} />]}
        i18nKey="bulkInvites.successPage.text"
      />
    </>
  );
};

export default BulkInvitesSuccessPage;
