import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { Alert, InfoTooltip } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { BulkInvitesContext } from '../../contexts';

const SupplierSummary = ({ review }) => {
  const { t } = useTranslation();
  const { summaryLoading, summary } = useContext(BulkInvitesContext);

  const summaryDetails = (
    <div>
      {!review && (
        <h4 className="title">{t('bulkInvites.supplierSummary.title')}</h4>
      )}
      {summaryLoading && <span data-testid="spinner" className="spinner" />}
      <p>
        <strong>{t('bulkInvites.supplierSummary.suppliers.title')}</strong>
      </p>
      <table>
        <tbody>
          <tr>
            <th>{summary.totalSuppliers}</th>
            <td>{t('bulkInvites.supplierSummary.suppliers.totalSuppliers')}</td>
          </tr>
          <tr>
            <th>{summary.suppliersPendingInvite}</th>
            <td>
              {t(
                'bulkInvites.supplierSummary.suppliers.suppliersPendingInvite'
              )}
            </td>
            {review ? (
              <td className="tooltip" data-testid="tooltip">
                <InfoTooltip align="left" theme="light">
                  <p>
                    {t(
                      'bulkInvites.supplierSummary.tooltips.suppliersPendingInvite'
                    )}
                  </p>
                </InfoTooltip>
              </td>
            ) : null}
          </tr>
          <tr>
            <th>{summary.previouslyInvitedSuppliers}</th>
            <td>
              {t(
                'bulkInvites.supplierSummary.suppliers.previouslyInvitedSuppliers'
              )}
            </td>
            {review ? (
              <td className="tooltip">
                <InfoTooltip align="left" theme="light">
                  <p>
                    {t(
                      'bulkInvites.supplierSummary.tooltips.previouslyInvitedSuppliers'
                    )}
                  </p>
                </InfoTooltip>
              </td>
            ) : null}
          </tr>
          <tr>
            <th>{summary.activeSuppliers}</th>
            <td>
              {t('bulkInvites.supplierSummary.suppliers.activeSuppliers')}
            </td>
            {review ? (
              <td className="tooltip">
                <InfoTooltip align="left" theme="light">
                  <p>
                    {t('bulkInvites.supplierSummary.tooltips.activeSuppliers')}
                  </p>
                </InfoTooltip>
              </td>
            ) : null}
          </tr>
        </tbody>
      </table>
    </div>
  );

  if (review) {
    return <div className="supplier-summary">{summaryDetails}</div>;
  }
  return (
    <div className="supplier-summary">
      <Alert>{summaryDetails}</Alert>
    </div>
  );
};

SupplierSummary.propTypes = {
  review: PropTypes.bool,
};

SupplierSummary.defaultProps = {
  review: false,
};

export default SupplierSummary;
