import React from 'react';
import PropTypes from 'prop-types';
import { Button, Field, FieldLabel, Link, Progress, Select } from 'taulia-ui';
import { withTranslation } from 'react-i18next';
import SupplierSummary from './SupplierSummary';
import CONSTANTS from '../../globals';
import { BulkInvitesContext } from '../../contexts';

class BulkInvitesSearchPage extends React.Component {
  state = {
    businessUnitIds: [],
    countries: [],
    loading: true,
    segmentIds: [],
  };

  static propTypes = {
    request: PropTypes.func.isRequired,
    settings: PropTypes.shape({ segmentsEnabled: PropTypes.bool }),
    t: PropTypes.func,
    history: PropTypes.shape().isRequired,
  };

  static defaultProps = {
    settings: { segmentsEnabled: false },
    t: null,
  };

  componentDidMount() {
    const { request } = this.props;

    request('GET', '/api/bulkInvites/criteria')
      .then(res => {
        const { criteria } = res.body;
        if (criteria) {
          this.setState(
            {
              loading: false,
              businessUnitIds: criteria.businessUnitIds || [],
              countries: criteria.countries || [],
              segmentIds: criteria.segmentIds || [],
            },
            this.fetchSummaryInitial
          );
        } else {
          this.setState({ loading: false });
        }
      })
      .catch(err => {
        if (CONSTANTS.DEBUG) {
          console.error('[BulkInvitesSearchPage]', err); // eslint-disable-line no-console
        }
        this.setState({ loading: false });
      });
  }

  onBusinessUnitIdsChange = businessUnitIds => {
    this.setState({ businessUnitIds }, this.fetchSummary);
  };

  onCountriesChange = countries => {
    this.setState({ countries }, this.fetchSummary);
  };

  onSegmentIdsChange = segmentIds => {
    this.setState({ segmentIds }, this.fetchSummary);
  };

  onSubmit = event => {
    const { request, history } = this.props;
    const { businessUnitIds, countries, segmentIds } = this.state;
    event.preventDefault();
    this.setState({ loading: true }, () => {
      request('POST', '/api/bulkInvites/criteria')
        .send({ businessUnitIds, countries, segmentIds })
        .then(() => history.push(CONSTANTS.SUPPLIERS.BULK_INVITES.REVIEW))
        .catch(() => history.push(CONSTANTS.SUPPLIERS.BULK_INVITES.REVIEW));
    });
  };

  fetchSummaryInitial = () => {
    const { businessUnitIds, countries, segmentIds } = this.state;
    if (
      businessUnitIds.length > 0 ||
      countries.length > 0 ||
      segmentIds.length > 0
    ) {
      this.fetchSummary();
    }
  };

  fetchSummary = () => {
    const { businessUnitIds, countries, segmentIds } = this.state;
    const { fetchSummary } = this.context;
    fetchSummary({ businessUnitIds, countries, segmentIds });
  };

  static contextType = BulkInvitesContext;

  render() {
    const { searchOptions, summary } = this.context;
    const { settings, t } = this.props;
    const { loading, businessUnitIds, countries, segmentIds } = this.state;
    const showSummary =
      businessUnitIds.length > 0 ||
      countries.length > 0 ||
      segmentIds.length > 0;

    if (!searchOptions || loading) {
      return <Progress />;
    }

    return (
      <>
        <h1>{t('bulkInvites.title')}</h1>
        <h3>{t('bulkInvites.searchPage.title')}</h3>
        <p>{t('bulkInvites.searchPage.text')}</p>
        <form onSubmit={this.onSubmit}>
          <div className="summary-container">
            <div>
              <Field>
                <FieldLabel htmlFor="business-unit-ids">
                  {t('bulkInvites.searchPage.businessUnitIds.label')}
                </FieldLabel>
                <Select
                  id="business-unit-ids"
                  name="businessUnitIds"
                  enableSearch={searchOptions.businessUnitIds.length > 10}
                  placeholder={t(
                    'bulkInvites.searchPage.businessUnitIds.placeholder'
                  )}
                  searchPlaceholder={t(
                    'bulkInvites.searchPage.searchPlaceholder'
                  )}
                  noSearchResultsLabel={t(
                    'bulkInvites.searchPage.noSearchResultsLabel'
                  )}
                  options={searchOptions.businessUnitIds}
                  onChange={this.onBusinessUnitIdsChange}
                  value={businessUnitIds}
                />
              </Field>
              <Field>
                <FieldLabel htmlFor="countries">
                  {t('bulkInvites.searchPage.countries.label')}
                </FieldLabel>
                <Select
                  id="countries"
                  name="countries"
                  enableSearch={searchOptions.countries.length > 10}
                  placeholder={t(
                    'bulkInvites.searchPage.countries.placeholder'
                  )}
                  searchPlaceholder={t(
                    'bulkInvites.searchPage.searchPlaceholder'
                  )}
                  noSearchResultsLabel={t(
                    'bulkInvites.searchPage.noSearchResultsLabel'
                  )}
                  options={searchOptions.countries}
                  onChange={this.onCountriesChange}
                  value={countries}
                />
              </Field>
              {settings.segmentsEnabled && (
                <Field>
                  <FieldLabel htmlFor="segment-ids">
                    {t('bulkInvites.searchPage.segmentIds.label')}
                  </FieldLabel>
                  <Select
                    id="segment-ids"
                    name="segmentIds"
                    enableSearch={searchOptions.segmentIds.length > 10}
                    placeholder={t(
                      'bulkInvites.searchPage.segmentIds.placeholder'
                    )}
                    searchPlaceholder={t(
                      'bulkInvites.searchPage.searchPlaceholder'
                    )}
                    noSearchResultsLabel={t(
                      'bulkInvites.searchPage.noSearchResultsLabel'
                    )}
                    options={searchOptions.segmentIds}
                    onChange={this.onSegmentIdsChange}
                    value={segmentIds}
                    disabled={searchOptions.segmentIds.length === 0}
                  />
                </Field>
              )}
            </div>
            {summary && showSummary && <SupplierSummary />}
          </div>
          <div className="invites-button-container">
            <Link to={CONSTANTS.SUPPLIERS.BULK_INVITES.START}>
              &lt;&nbsp;
              {t('bulkInvites.searchPage.back')}
            </Link>
            <Button
              type="submit"
              theme="primary"
              disabled={
                businessUnitIds.length === 0 &&
                countries.length === 0 &&
                segmentIds.length === 0
              }
            >
              {t('bulkInvites.searchPage.submit')}
            </Button>
          </div>
        </form>
      </>
    );
  }
}

export default withTranslation()(BulkInvitesSearchPage);
