import React from 'react';
import { Button } from 'taulia-ui';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import question from '../../images/taulia-question.png';
import CONSTANTS from '../../globals';

const NotFound = () => {
  const history = useHistory();
  const { t } = useTranslation();
  const navigateHome = () => history.push(CONSTANTS.DASHBOARD.HOME);

  return (
    <div className="not-found-404">
      <img src={question} alt={t('notFound.error')} />
      <h2>{t('notFound.error')}</h2>
      <p>{t('notFound.cantFind')}</p>
      <Button onClick={navigateHome} theme="primary">
        {t('notFound.backHome')}
      </Button>
    </div>
  );
};

export default NotFound;
