import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { cx, Button } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { CheckCircle } from '../../icons';

function AccordionItem({
  buttonLabel,
  canEdit,
  children,
  completeLabel,
  isComplete,
  isOpen,
  isSaveDisabled,
  label,
  onComplete,
  onSave,
  toggleOpen,
}) {
  const onSubmit = () => {
    onComplete(label);
    onSave();
    toggleOpen();
  };

  const { t } = useTranslation();

  return (
    <div className="accordion-item">
      <div className="top-section">
        <div>
          <span className="icon-span">
            <CheckCircle fill={isComplete ? '#35af28' : '#dcdedf'} />
            &nbsp;
            {label}
          </span>
          {isComplete && <span>{completeLabel}</span>}
        </div>
        {!isOpen && (
          <Button
            data-testid={`open-button-${label}`}
            onClick={() => toggleOpen(label)}
          >
            {buttonLabel}
          </Button>
        )}
      </div>
      <div
        aria-expanded={isOpen}
        className={cx('accordion-item-content', { open: isOpen })}
        data-testid={`accordion-item-content-${label}`}
      >
        {children}
        {canEdit && (
          <div style={{ marginTop: '20px' }}>
            <Button
              data-testid={`save-button-${label}`}
              disabled={isSaveDisabled}
              onClick={onSubmit}
              theme="primary"
            >
              {t('standard.save')}
            </Button>
            <Button
              data-testid={`cancel-button-${label}`}
              onClick={() => toggleOpen(label, true)}
              theme="text"
            >
              {t('standard.cancel')}
            </Button>
          </div>
        )}
      </div>
    </div>
  );
}

AccordionItem.propTypes = {
  buttonLabel: PropTypes.string,
  canEdit: PropTypes.bool,
  children: PropTypes.instanceOf(Object).isRequired,
  completeLabel: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  isComplete: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  isSaveDisabled: PropTypes.bool,
  label: PropTypes.string,
  onComplete: PropTypes.func.isRequired,
  onSave: PropTypes.func,
  toggleOpen: PropTypes.func.isRequired,
};

AccordionItem.defaultProps = {
  buttonLabel: 'Open!',
  canEdit: true,
  completeLabel: 'Complete!',
  label: 'A label!',
  isSaveDisabled: false,
  onSave: () => null,
};

function Accordion({ canEdit, children, onSubmit, onCancel }) {
  const [openSections, setOpenSections] = useState({});
  const [completeSections, setCompleteSections] = useState({});
  const { t } = useTranslation();

  const toggleOpen = (label, cancel = false) => {
    const isOpen = !!openSections[label];
    setOpenSections({ [label]: !isOpen });
    if (cancel) {
      onCancel(label);
    }
  };

  const onComplete = label => {
    setCompleteSections({ ...completeSections, [label]: true });
  };

  useEffect(() => {
    const completed = {};
    children.forEach(child => {
      if (child.props.isComplete) completed[child.props.label] = true;
    });
    setCompleteSections(completed);
  }, [children]);

  const allComplete = Object.keys(completeSections).length === children.length;

  return (
    <div className="accordion">
      {children.map(child => (
        <AccordionItem
          {...child.props}
          canEdit={canEdit}
          isOpen={
            !!openSections[child.props.label] ||
            !canEdit ||
            !!child.props.isOpen
          }
          isComplete={!!completeSections[child.props.label]}
          key={child.props.label}
          onComplete={onComplete}
          toggleOpen={toggleOpen}
        >
          {child.props.children}
        </AccordionItem>
      ))}
      {canEdit && (
        <div className="accordion-button-container">
          <Button onClick={() => onSubmit(true)}>
            {t('announcements.saveDraft')}
          </Button>
          <Button
            data-testid="publish-button"
            disabled={!allComplete}
            onClick={() => onSubmit(false)}
            theme="primary"
          >
            {t('announcements.publish')}
          </Button>
        </div>
      )}
    </div>
  );
}

Accordion.propTypes = {
  canEdit: PropTypes.bool,
  children: PropTypes.instanceOf(Object).isRequired,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
};

Accordion.defaultProps = {
  canEdit: true,
  onCancel: () => null,
  onSubmit: () => null,
};

export default Accordion;
