import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Notification } from 'taulia-ui';
import { useFetchContext } from '../../hooks';

const BuyerNotifications = () => {
  const { data: notificationData } = useFetchContext(
    '/api/buyer-announcements'
  );
  const { t } = useTranslation();
  const [currentNotifications, setCurrentNotifications] = useState([]);
  const location = useLocation();

  useEffect(() => {
    if (notificationData?.buyerAnnouncements?.length) {
      const { buyerAnnouncements } = notificationData;
      const currentNotificationData = [];
      const pathname = location.pathname || '';
      buyerAnnouncements.forEach(item => {
        const formattedObj = {
          id: item.id,
          message: item.message || t(item.localizationKey),
        };
        if (location && location.pathname) {
          if (item.path === pathname) {
            currentNotificationData.push(formattedObj);
          } else if (item.path === '*') {
            currentNotificationData.push(formattedObj);
          } else if (!item.path && location.pathname === '/home') {
            currentNotificationData.push(formattedObj);
          }
        }
      });
      setCurrentNotifications(currentNotificationData);
    }
  }, [notificationData, location]);

  return <Notification notificationData={currentNotifications} />;
};

export default BuyerNotifications;
