import React from 'react';
import { find, isEmpty } from 'lodash';
import {
  Alert,
  Button,
  Checkbox,
  Field,
  FieldErrorMap,
  Link,
  Progress,
} from 'taulia-ui';
import PropTypes from 'prop-types';
import { withTranslation, Trans } from 'react-i18next';
import SupplierSummary from './SupplierSummary';
import {
  createSupplierCsv,
  downloadCsvFile,
  createCsvFileName,
} from '../../Utils/SupplierUtils';
import CONSTANTS from '../../globals';
import { BulkInvitesContext } from '../../contexts';

const MAX_SUPPLIER_DOWNLOAD = 10000;

class BulkInvitesReviewPage extends React.Component {
  static propTypes = {
    onRequestClose: PropTypes.func,
    request: PropTypes.func.isRequired,
    t: PropTypes.func,
    history: PropTypes.shape().isRequired,
  };

  static defaultProps = {
    onRequestClose: null,
    t: null,
  };

  state = {
    loading: true,
    criteria: null,
    summary: null,
    showError: null,
    options: {
      downloadCsv: false,
      resend: true,
    },
  };

  componentDidMount() {
    const { request } = this.props;
    request('GET', '/api/bulkInvites/summary')
      .then(res => {
        const { criteria, summary } = res.body;
        if (criteria && summary) {
          this.setState({ criteria, summary, loading: false, showError: null });
        } else {
          this.setState({
            loading: false,
            showError: 'bulkInvites.reviewPage.error.fetchText',
          });
        }
      })
      .catch(() => {
        const code = 'bulkInvites.reviewPage.error.fetchText';
        this.setState({ loading: false, showError: code });
      });
  }

  onUpdateResendOption = event => {
    const { options } = this.state;
    this.setState({
      options: {
        ...options,
        resend: event.target.checked,
      },
    });
  };

  onSubmit = event => {
    event.preventDefault();
    const { onRequestClose, request, history } = this.props;
    const { criteria, options } = this.state;
    this.setState({ loading: true }, () => {
      request('POST', '/api/bulkInvites/send')
        .send({
          ...criteria,
          resendInvite: options.resend,
        })
        .then(() => {
          if (onRequestClose) onRequestClose();
          history.push(CONSTANTS.SUPPLIERS.BULK_INVITES.SUCCESS);
        })
        .catch(() => {
          if (onRequestClose) onRequestClose();
          this.setState({
            loading: false,
            showError: 'bulkInvites.reviewPage.error.submitText',
          });
        });
    });
  };

  downloadSupplierInviteList = () => {
    const { request } = this.props;
    const { criteria } = this.state;
    request('POST', '/api/bulkInvites/postBulkInvitationDataDownload')
      .send(criteria)
      .then(res => {
        this.setState({ options: { downloadCsv: true } });
        downloadCsvFile(
          createCsvFileName('TauliaSupplierInviteList'),
          createSupplierCsv(res.body.criteria)
        );
      })
      .catch(() => {
        this.setState({
          loading: false,
          showError: 'bulkInvites.reviewPage.error.failedDownload',
        });
      });
  };

  checkSupplierInviteCount = () => {
    const { t } = this.props;
    const { summary } = this.state;
    if (summary.totalSuppliers > MAX_SUPPLIER_DOWNLOAD) {
      return (
        <div className="downloadSupplierResults">
          <Alert inline theme="warn">
            <strong>
              {t('bulkInvites.reviewPage.error.limitedSupplierDownload')}
            </strong>
          </Alert>
        </div>
      );
    }
    return null;
  };

  static contextType = BulkInvitesContext;

  renderErrorMessage = () => {
    const { showError } = this.state;
    const { t } = this.props;
    return (
      <Alert inline theme="danger">
        <strong>{t('bulkInvites.reviewPage.error.title')}</strong>
        <FieldErrorMap code={showError}>
          <Trans
            i18nKey="bulkInvites.reviewPage.error.failedDownload"
            components={[
              <Link to={CONSTANTS.SUPPLIERS.BULK_INVITES.START} />,
              <Link
                href="http://support.taulia.com"
                target="_blank"
                rel="noopener noreferrer"
              />,
            ]}
          />
          <Trans
            key="bulkInvites.reviewPage.error.fetchText"
            i18nKey="bulkInvites.reviewPage.error.fetchText"
            components={[<Link to={CONSTANTS.SUPPLIERS.BULK_INVITES.START} />]}
          />
          <Trans
            key="bulkInvites.reviewPage.error.submitText"
            i18nKey="bulkInvites.reviewPage.error.submitText"
            components={[
              <Link to={CONSTANTS.SUPPLIERS.BULK_INVITES.START} />,
              <Link
                href="http://support.taulia.com"
                target="_blank"
                rel="noopener noreferrer"
              />,
            ]}
          />
        </FieldErrorMap>
      </Alert>
    );
  };

  renderFromSearchOptions = key => {
    const { searchOptions } = this.context;
    const { criteria } = this.state;
    if (
      !criteria ||
      isEmpty(criteria[key]) ||
      !searchOptions ||
      isEmpty(searchOptions[key])
    ) {
      return null;
    }
    const list = criteria[key].map(value => {
      const match = find(searchOptions[key], { value });
      if (match) {
        return match.label;
      }
      return '';
    });
    return list.join(', ');
  };

  renderCriteria = () => {
    const { searchOptions } = this.context;
    const { t } = this.props;
    const { criteria } = this.state;
    if (criteria.supplierNumbers?.length) {
      return (
        <div className="review-criteria">
          <h4>{t('bulkInvites.reviewPage.criteria.title')}</h4>
          <p>{t('bulkInvites.reviewPage.criteria.individual.title')}</p>
          <div className="criteria-data">
            {criteria.uploadFileName?.length && (
              <>
                <strong>
                  {t('bulkInvites.reviewPage.criteria.individual.fileUpload')}
                </strong>
                <p>{criteria.uploadFileName}</p>
              </>
            )}
            <strong>
              {t('bulkInvites.reviewPage.criteria.individual.supplierNumbers')}
            </strong>
            <p>{criteria.supplierNumbers.join(', ')}</p>
          </div>
        </div>
      );
    }
    if (
      !isEmpty(searchOptions) &&
      (criteria.businessUnitIds?.length ||
        criteria.countries?.length ||
        criteria.segmentIds?.length)
    ) {
      return (
        <div className="review-criteria">
          <h4>{t('bulkInvites.reviewPage.criteria.title')}</h4>
          <p>{t('bulkInvites.reviewPage.criteria.search.title')}</p>
          <div className="criteria-data">
            {criteria?.businessUnitIds.length ? (
              <>
                <strong>
                  {t('bulkInvites.reviewPage.criteria.search.businessUnitIds')}
                </strong>
                <p>{this.renderFromSearchOptions('businessUnitIds')}</p>
              </>
            ) : null}
            {criteria.countries?.length ? (
              <>
                <strong>
                  {t('bulkInvites.reviewPage.criteria.search.countries')}
                </strong>
                <p>{this.renderFromSearchOptions('countries')}</p>
              </>
            ) : null}
            {criteria.segmentIds?.length ? (
              <>
                <strong>
                  {t('bulkInvites.reviewPage.criteria.search.segmentIds')}
                </strong>
                <p>{this.renderFromSearchOptions('segmentIds')}</p>
              </>
            ) : null}
          </div>
        </div>
      );
    }
    return null;
  };

  renderSummary = () => {
    const { summary } = this.state;
    const { t } = this.props;
    return (
      <div className="review-summary">
        <h4>{t('bulkInvites.reviewPage.summary.title')}</h4>
        <SupplierSummary review />
        {summary.totalSuppliers > 0 && (
          <div className="downloadSupplierResults">
            <Button
              id="downloadSupplierList"
              onClick={this.downloadSupplierInviteList}
              theme="text"
            >
              {t('bulkInvites.reviewPage.options.downloadSupplierList')}
            </Button>
          </div>
        )}
        {this.checkSupplierInviteCount()}
      </div>
    );
  };

  renderForm = () => {
    const { options, summary } = this.state;
    const { onRequestClose, t, history } = this.props;
    return (
      <form className="page-form" onSubmit={this.onSubmit}>
        <div className="form-content">
          <h4>{t('bulkInvites.reviewPage.options.title')}</h4>
          <Field>
            <Checkbox
              checked={options.resend}
              data-testid="resend-email"
              inline
              name="resend"
              onChange={this.onUpdateResendOption}
            >
              <p>
                <strong>
                  {t('bulkInvites.reviewPage.options.resend.label')}
                </strong>
              </p>
              <p>
                {t('bulkInvites.reviewPage.options.resend.text', {
                  numContacts: summary.totalSuppliers,
                })}
              </p>
            </Checkbox>
          </Field>
        </div>
        <div className="invites-button-container">
          {!onRequestClose && (
            <Button
              data-testid="back-button"
              onClick={history.goBack}
              theme="text"
            >
              &lt;&nbsp;
              {t('bulkInvites.reviewPage.back')}
            </Button>
          )}
          <Button type="submit" theme="primary">
            {t('bulkInvites.reviewPage.submit')}
          </Button>
        </div>
      </form>
    );
  };

  render() {
    const { loading, showError } = this.state;
    const { t } = this.props;

    if (loading) {
      return <Progress />;
    }

    return (
      <>
        <h1>{t('bulkInvites.title')}</h1>
        {showError ? (
          <>{this.renderErrorMessage()}</>
        ) : (
          <div>
            <h3>{t('bulkInvites.reviewPage.title')}</h3>
            <p>{t('bulkInvites.reviewPage.text')}</p>
            {this.renderCriteria()}
            {this.renderSummary()}
            {this.renderForm()}
          </div>
        )}
      </>
    );
  }
}

export default withTranslation()(BulkInvitesReviewPage);
