/* eslint-disable react/jsx-wrap-multilines */
/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
import React from 'react';
import { Progress } from 'taulia-ui';
import CONSTANTS from '../../globals';
import { GenericErrorMessage } from '../../Utils/GenericErrorMessage';

export default class TermsAndConditions extends React.Component {
  constructor(props) {
    super(props);
    this.state = { termsAndConditions: null };
  }

  componentDidMount() {
    this.props
      .request('GET', '/api/terms-and-conditions')
      .send()
      .then(response => {
        this.setState({ showError: false, termsAndConditions: response.body });
      })
      .catch(error => {
        this.setState({ showError: true });
        if (CONSTANTS.DEBUG) {
          console.log(error); // eslint-disable-line no-console
        }
      });
  }

  render() {
    if (this.state.showError) {
      return (
        <div className="app-error">
          <GenericErrorMessage />
        </div>
      );
    }
    const { termsAndConditions } = this.state;
    return (
      <div className="terms-and-conditions">
        {termsAndConditions ? (
          <div
            // eslint-disable-next-line react/no-danger
            dangerouslySetInnerHTML={{ __html: termsAndConditions.content }}
          />
        ) : (
          <Progress />
        )}
      </div>
    );
  }
}
