import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import {
  ActionsCell,
  Button,
  ListView,
  StatusIndicator,
  toast,
} from 'taulia-ui';
import qs from 'qs';
import InviteUserModal from './InviteUserModal';
import PermissionsModal from './PermissionsModal';
import RoleModal from './RoleModal';
import ViewUserModal from './ViewUserModal';
import { genericErrorToast } from '../../../Utils/utils';
import { RequestContext } from '../../../contexts';

const UserManager = ({ location }) => {
  const { t } = useTranslation();
  const request = useContext(RequestContext);
  const [businessUnits, setBusinessUnits] = useState([]);
  const [roles, setRoles] = useState([]);
  const [showPermissionsModal, setShowPermissionsModal] = useState(false);
  const [showRoleModal, setShowRoleModal] = useState(false);
  const [showViewUserModal, setShowViewUserModal] = useState(false);
  const [showInviteUserModal, setShowInviteUserModal] = useState(false);
  const [user, setUser] = useState({});

  const getBusinessUnits = () => {
    request('GET', '/api/user-manager/business-units')
      .then(res => setBusinessUnits(res.body.availableBusinessUnitTree))
      .catch(genericErrorToast);
  };

  const getRoles = () => {
    request('GET', '/api/user-manager/buyer-roles')
      .then(res => setRoles(res.body.buyerRoles))
      .catch(genericErrorToast);
  };

  const params = qs.parse(location.search, {
    ignoreQueryPrefix: true,
  });

  // TODO: delete when connected to API
  const dummyData = [
    {
      id: params.stubbedUserId || '402881a875f759300175fb4af132000e', // Allows better testing on integration
      email: 'bigjared@theman.com',
      name: 'the one and only',
      status: 'pending',
      dateInvited: '2020-01-15T12:07:05.000Z',
      lastLogin: '2020-01-18T12:05:05.000Z',
      hasRestrictionsPerUser: true,
      userCompanyCodeAssignedCount: 3,
      numberOfAvailableCompanyCodes: 22,
      role: 'BUYER_ADMIN',
      locale: 'en-US',
    },
    {
      id: '402881a87620037f01762b6e66b80025',
      email: 'smalljared@nottheman.com',
      name: 'small jared',
      status: 'enabled',
      lastLogin: null,
      hasRestrictionsPerUser: false,
      role: 'BUYER_DOCUMENT_SEARCH_ONLY',
      locale: 'es',
      dateInvited: '2020-01-15T12:07:05.000Z',
    },
    {
      id: '402881a8761a48c201761afcbed9000e',
      email: 'mediumjared@nottheman.com',
      name: 'JARED',
      status: 'disabled',
      dateInvited: '2020-04-17T12:07:05.000Z',
      hasRestrictionsPerUser: true,
      userCompanyCodeAssignedCount: 4,
      numberOfAvailableCompanyCodes: 5,
      lastLogin: '2020-08-16T12:07:05.000Z',
      role: 'BUYER_SUPPLIER_LAUNCH_SEARCH',
      locale: 'de',
    },
  ];

  useEffect(() => {
    getBusinessUnits();
    getRoles();
  }, []);

  const statusIndicator = status => {
    const statusColorMap = {
      pending: 'yellow',
      enabled: 'green',
    };
    return (
      <StatusIndicator color={statusColorMap[status]}>
        {t(`userManager.${status}`)}
      </StatusIndicator>
    );
  };

  const resendInviteEmail = data => {
    request('POST', `/api/user-manager/resendInvitation/${data.email}`)
      .then(() => {
        toast.success(
          t('userManager.toast.resendInviteEmailSuccess', {
            email: data.email,
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
  };

  const deleteUser = data => {
    request('DELETE', `/api/user-manager/user/${data.id}`)
      .then(() => {
        toast.success(
          t('userManager.toast.deleteUserSuccess', {
            email: data.email,
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
  };

  const reactivateUser = data => {
    request('PUT', `/api/user-manager/user/${data.id}`)
      .send({ active: true })
      .then(() => {
        toast.success(
          t('userManager.toast.reactivateUserSuccess', {
            email: data.email,
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
  };

  const deactivateUser = data => {
    request('PUT', `/api/user-manager/user/${data.id}`)
      .send({ active: false })
      .then(() => {
        toast.success(
          t('userManager.toast.deactivateUserSuccess', {
            email: data.email,
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
  };

  const resetPassword = data => {
    request('POST', `/api/user-manager/user/${data.id}/resetPassword`)
      .then(() => {
        toast.success(
          t('userManager.toast.resetPasswordSuccess', {
            email: data.email,
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
  };

  const statusToActionMap = {
    pending: [
      { func: resendInviteEmail, trans: 'userManager.resendInviteEmail' },
      { func: deleteUser, trans: 'standard.delete' },
    ],
    enabled: [
      { func: deactivateUser, trans: 'userManager.deactivateUser' },
      { func: resetPassword, trans: 'userManager.resetPassword' },
    ],
    disabled: [
      { func: reactivateUser, trans: 'userManager.reactivateUser' },
      { func: resetPassword, trans: 'userManager.resetPassword' },
    ],
  };

  const getActionsForCell = data => {
    const { status } = data;
    const cellActions = [
      {
        label: t('userManager.editRole'),
        action: () => {
          setShowRoleModal(true);
          setUser(data);
        },
      },
      {
        label: t('userManager.editPermissions'),
        action: () => {
          setShowPermissionsModal(true);
          setUser(data);
        },
      },
    ];
    statusToActionMap[status].forEach(action => {
      cellActions.push({
        label: t(action.trans),
        action: () => action.func(data),
      });
    });
    return cellActions;
  };

  const columns = [
    {
      key: 'email',
      label: t('profile.email'),
      cell: data => (
        <Button
          onClick={() => {
            setShowViewUserModal(true);
            setUser(data.object);
          }}
          theme="text"
          title={data.object.email}
        >
          {data.object.email}
        </Button>
      ),
      width: 225,
      fixedOrder: 1,
    },
    {
      key: 'name',
      label: t('fields.name'),
      cell: data => (
        <Button
          onClick={() => {
            setShowViewUserModal(true);
            setUser(data.object);
          }}
          theme="text"
          title={data.object.name}
        >
          {data.object.name}
        </Button>
      ),
      width: 180,
      fixedOrder: 2,
    },
    {
      key: 'status',
      label: t('fields.status'),
      cell: data => statusIndicator(data.object.status),
      width: 300,
    },
    {
      key: 'role',
      label: t('supplierView.role'),
      cell: data => t(`userManager.roles.${data.object.role}`),
      width: 275,
    },
    {
      key: 'businessUnits',
      label: t('userManager.businessUnitAccess'),
      type: 'TEXT',
      width: 200,
      cell: data => {
        const {
          hasRestrictionsPerUser,
          userCompanyCodeAssignedCount,
          numberOfAvailableCompanyCodes,
        } = data.object;
        if (hasRestrictionsPerUser) {
          return (
            <span>
              {userCompanyCodeAssignedCount}
              &nbsp;&#47;&nbsp;
              {numberOfAvailableCompanyCodes}
            </span>
          );
        }
        return t('userManager.noRestriction');
      },
    },
    { key: 'lastLogin', label: t('fields.lastLogin'), type: 'DATE_TIME' },
    {
      key: 'actions',
      label: '',
      width: 10,
      cell: data => <ActionsCell items={getActionsForCell(data.object)} />,
    },
  ];

  const actions = [
    {
      label: t('userManager.inviteUser'),
      onClick: () => setShowInviteUserModal(true),
      allowEmptySelection: true,
    },
  ];

  const onModalClose = () => {
    setUser({});
    setShowInviteUserModal(false);
    setShowPermissionsModal(false);
    setShowRoleModal(false);
    setShowViewUserModal(false);
  };

  return (
    <>
      <h3>{t('nav.buyer_area.settings.user_manager')}</h3>
      {/* TODO: swap listview for EnhancedSearch when API is connected */}
      <ListView
        actions={actions}
        actionsEnabled
        columns={columns}
        data={dummyData} // TODO: remove
        noResultsText={<p>{t('userManager.noUsers')}</p>}
      />
      <PermissionsModal
        businessUnits={businessUnits}
        onRequestClose={onModalClose}
        open={showPermissionsModal}
        request={request}
        user={user}
      />
      <RoleModal
        onRequestClose={onModalClose}
        open={showRoleModal}
        request={request}
        roles={roles}
        user={user}
      />
      <ViewUserModal
        onRequestClose={onModalClose}
        open={showViewUserModal}
        setShowPermissionsModal={setShowPermissionsModal}
        setShowRoleModal={setShowRoleModal}
        setShowViewUserModal={setShowViewUserModal}
        statusToActionMap={statusToActionMap}
        statusIndicator={statusIndicator}
        user={user}
      />
      <InviteUserModal
        businessUnits={businessUnits}
        onRequestClose={onModalClose}
        open={showInviteUserModal}
        request={request}
        roles={roles}
      />
    </>
  );
};

export default UserManager;

UserManager.propTypes = {
  location: PropTypes.shape(),
};

UserManager.defaultProps = {
  location: {},
};
