import React, { useContext, useEffect, useState } from 'react';
import {
  ActionsCell,
  Button,
  FieldList,
  Link,
  ListView,
  MiniPager,
  Progress,
  StatusIndicator,
  Tabs,
  toast,
} from 'taulia-ui';
import { useLocation, useHistory, useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import BusinessUnitModal from './BusinessUnitModal';
import EditContactModal from './EditContactModal';
import EventLog from './EventLog';
import ViewUserModal from './ViewUserModal';
import {
  localizeCurrency,
  localizeDate,
  localizeNumber,
} from '../../Utils/localizeUtils';
import {
  statusColorMap,
  userCanEditSuppliers,
} from '../../Utils/SupplierUtils';
import {
  constructSearchQueryUrl,
  genericErrorToast,
  redirectTo404,
} from '../../Utils/utils';
import CONSTANTS from '../../globals';
import { useEnabledSettingsFetch } from '../../hooks';
import { UserSettingsContext, RequestContext } from '../../contexts';

function SupplierView() {
  const location = useLocation();
  const history = useHistory();
  const params = useParams();
  const { id } = params;
  const { userRoles } = useContext(UserSettingsContext);
  const [paginationData, setPaginationData] = useState({
    prev: '',
    next: '',
    total: 1,
    currIdx: 1,
  });
  const [option, setOption] = useState(
    location.search === '?events' ? 'eventLog' : 'contacts'
  );
  const [sendingCompanyInvite, setSendingCompanyInvite] = useState(false);
  const [showBusinessUnitModal, setShowBusinessUnitModal] = useState(false);
  const [showEditContactModal, setShowEditContactModal] = useState(false);
  const [showViewUserModal, setShowViewUserModal] = useState(false);
  const [selectedContact, setSelectedContact] = useState();
  const [supplierData, setSupplierData] = useState();

  const { t } = useTranslation();
  const request = useContext(RequestContext);

  const retrieveStoredSupplierData = () => {
    /* Retrieve stored supplier order from local storage.
    Set as "1 of 1" if supplier is not found in data */
    const storedSupplierData = localStorage.getItem('supplierData')?.split(',');
    if (storedSupplierData) {
      const currIdx = storedSupplierData.indexOf(id);
      setPaginationData({
        prev: storedSupplierData[currIdx - 1],
        next: storedSupplierData[currIdx + 1],
        total: currIdx > -1 ? storedSupplierData.length : 1,
        currIdx: currIdx > -1 ? currIdx + 1 : 1,
      });
    }
  };

  const enabledSettings = useEnabledSettingsFetch(
    ['SUPPLIER_CENTER_EVENT_LOG_UI'],
    null
  );

  const eventLogSettingEnabled = enabledSettings.isEnabled;

  const getSupplierData = () => {
    request('GET', `/api/supplierBranchView/${id}`)
      .then(res => {
        setSupplierData(res.body);
      })
      .catch(() => redirectTo404(history));
  };

  useEffect(() => {
    getSupplierData();
    retrieveStoredSupplierData();
  }, [id]);

  const statusIndicator = status => {
    return (
      <StatusIndicator color={statusColorMap[status]}>
        {t(`fields.combinedEnrollmentStatus.enumValues.${status}`)}
      </StatusIndicator>
    );
  };

  const returnSegmentLinks = () => {
    if (!supplierData?.segments.length) {
      return '';
    }
    return supplierData?.segments.map((segment, idx) => (
      <span key={segment.name}>
        <Link to={constructSearchQueryUrl('segments', [segment.id])}>
          {segment.name}
        </Link>
        {idx < supplierData?.segments.length - 1 && <>,&nbsp;</>}
      </span>
    ));
  };

  const returnBusinessUnits = () => {
    const maxUnits = 9;
    if (!supplierData?.businessUnits.length) {
      return '';
    }
    const supplierDataNumbers = supplierData?.businessUnits.map(
      unit => unit.number
    );
    let businessUnitData = supplierDataNumbers
      .slice(0, maxUnits)
      .reduce((prev, curr) => [prev, ', ', curr]);
    if (supplierData?.businessUnits.length > maxUnits) {
      businessUnitData = businessUnitData.concat(
        <span key="and-more">
          &nbsp;
          {t('supplierView.andMore', {
            count: supplierData?.businessUnits.length - maxUnits,
          })}
          &nbsp;
        </span>
      );
    }
    return (
      <>
        {businessUnitData}
        &nbsp;
        <Button
          onClick={() => setShowBusinessUnitModal(true)}
          style={{ display: 'inline' }}
          theme="text"
        >
          &#40;
          {t('supplierView.viewAll')}
          &#41;
        </Button>
      </>
    );
  };

  const returnMergedBranches = () => {
    if (!supplierData?.mergedBranches.length) {
      return '';
    }
    let mergedBranches = supplierData?.mergedBranches
      .slice(0, 5)
      .map((supplier, idx) => {
        return (
          <span key={supplier.number}>
            <Link to={`/suppliers/search/${supplier.id}`}>
              {supplier.number}
            </Link>
            {idx < supplierData?.mergedBranches.length - 1 && <>,&nbsp;</>}
          </span>
        );
      });

    if (supplierData?.mergedBranches.length > 5) {
      mergedBranches = mergedBranches.concat(
        <span key="and-more">
          &nbsp;
          {t('supplierView.andMore', {
            count: supplierData?.mergedBranches.length - 5,
          })}
          &nbsp;&#40;
          <Link
            data-testid="merged-branches-view-more"
            to={constructSearchQueryUrl(
              'number',
              supplierData?.mergedBranches.map(branch => branch.number)
            )}
          >
            {t('supplierView.viewAll')}
          </Link>
          &#41;
        </span>
      );
    }
    return mergedBranches;
  };

  const inviteCompany = () => {
    setSendingCompanyInvite(true);
    request(
      'POST',
      `/api/supplierCompany/${supplierData.supplierCompanyId}/inviteCompany`
    )
      .then(() => {
        toast.success(
          t('supplierView.sendCompanyInviteSuccess', {
            name: supplierData.name,
          })
        );
        setSendingCompanyInvite(false);
        getSupplierData();
      })
      .catch(() => {
        genericErrorToast();
        setSendingCompanyInvite(false);
      });
  };

  const inviteUser = user => {
    request(
      'POST',
      `/api/supplierCompany/${supplierData.supplierCompanyId}/sendIndividualInvitation`
    )
      .send({ email: user.emailAddress, invitationType: user.originalType })
      .then(() => {
        toast.success(
          t('supplierView.sendUserInviteSuccess', {
            emailAddress: user.emailAddress,
          })
        );
        setShowEditContactModal(false);
        getSupplierData();
      })
      .catch(() => genericErrorToast());
  };

  const saveContact = (contact, invite = false) => {
    const method = contact?.id ? 'PUT' : 'POST';

    let endpoint = `/api/supplierBranch/${id}/contacts`;
    let payload = contact;

    if (contact?.id && contact?.originalType === 'user') {
      endpoint = `/api/supplier/users/${contact?.id}/profile`;
      payload = {
        supplierBranchId: id,
        supplierCompanyId: supplierData?.supplierCompanyId,
        userId: contact?.id,
        contactId: contact?.matchingContactId,
        firstName: contact?.firstName,
        lastName: contact?.lastName,
        email: contact?.emailAddress,
      };
    }

    request(method, endpoint)
      .send(payload)
      .then(res => {
        toast.success(
          t(
            contact?.id
              ? 'supplierView.saveContactSuccess'
              : 'supplierView.addContactSuccess',
            { name: supplierData.name }
          )
        );
        if (invite) {
          if (res.body?.contact) {
            inviteUser({
              ...res.body.contact,
              originalType: contact?.originalType ?? 'contact',
            });
          }
          if (res.body?.updatedContact) {
            inviteUser({
              ...res.body.updatedContact[0],
              originalType: contact?.originalType ?? 'contact',
            });
          }
        } else {
          setShowEditContactModal(false);
        }
        getSupplierData();
      })
      .catch(() => genericErrorToast());
  };

  const showUserOrContactModal = userOrContact => {
    setSelectedContact(userOrContact);
    if (userOrContact.enrollmentStatus === 'active') {
      setShowViewUserModal(true);
    } else {
      setShowEditContactModal(true);
    }
  };

  const deleteContact = contact => {
    request('DELETE', `/api/supplierBranch/${id}/contacts/${contact.id}`)
      .then(() => {
        toast.success(t('supplierView.deleteContactSuccess'));
        getSupplierData();
      })
      .catch(() => genericErrorToast());
  };

  const tabData = ['contacts', 'eventLog'].map(tab => ({
    active: tab === option,
    callback: () => setOption(tab),
    label: t(`supplierView.options.${tab}.link`),
  }));

  // Listview stuff

  const actions = userCanEditSuppliers(userRoles)
    ? [
        {
          label: t('supplierView.addContact'),
          allowEmptySelection: true,
          onClick: () => {
            setSelectedContact();
            setShowEditContactModal(true);
          },
        },
      ]
    : [];

  const getActionsForCell = data => {
    if (!userCanEditSuppliers(userRoles)) {
      return [];
    }
    const { enrollmentStatus, originalType } = data;
    if (enrollmentStatus === 'active') return [];
    const cellActions = [
      {
        label: t(
          enrollmentStatus === 'invited'
            ? 'supplierView.resendInvitation'
            : 'supplierView.sendInvitation'
        ),
        action: () => inviteUser(data),
        disabled: enrollmentStatus === 'needs_attention',
      },
      {
        label: t('standard.edit'),
        action: () => {
          setSelectedContact(data);
          setShowEditContactModal(true);
        },
      },
    ];
    if (originalType === 'contact') {
      cellActions.push({
        label: t('standard.delete'),
        action: () => deleteContact(data),
      });
    }
    return cellActions;
  };

  const columns = [
    {
      key: 'name',
      label: t('fields.name'),
      type: 'TEXT',
      fixedOrder: 1,
      width: 250,
      cell: data => (
        <span>
          {userCanEditSuppliers(userRoles) ? (
            <Button
              theme="text"
              onClick={() => showUserOrContactModal(data.object)}
            >
              {`${data.object.firstName} ${data.object.lastName}`}
            </Button>
          ) : (
            `${data.object.firstName} ${data.object.lastName}`
          )}
        </span>
      ),
    },
    {
      key: 'emailAddress',
      label: t('profile.email'),
      type: 'TEXT',
      width: 250,
      fixedOrder: 2,
      cell: data => (
        <span>
          {userCanEditSuppliers(userRoles) ? (
            <Button
              theme="text"
              onClick={() => showUserOrContactModal(data.object)}
            >
              {data.object.emailAddress}
            </Button>
          ) : (
            data.object.emailAddress
          )}
        </span>
      ),
    },
    {
      key: 'enrollmentStatus',
      label: t('fields.status'),
      type: 'TEXT',
      width: 300,
      cell: data => (
        <>
          {statusIndicator(data.object.enrollmentStatus)}
          {data.object.enrollmentSubstatus && (
            <span className="subtext">
              {t(`supplierView.${data.object.enrollmentSubstatus}`)}
            </span>
          )}
        </>
      ),
    },
    {
      key: 'roles',
      label: t('supplierView.role'),
      cell: data => {
        const supplierRole = data.object.roles?.length
          ? t(`supplierView.${data.object.roles.find(role => role !== 'USER')}`)
          : '';
        return <span>{supplierRole}</span>;
      },
      width: 175,
    },
    { key: 'dateInvited', label: t('fields.dateInvited'), type: 'DATE_TIME' },
    { key: 'lastLogin', label: t('fields.lastLogin'), type: 'DATE_TIME' },
    {
      key: 'actions',
      label: '',
      width: 25,
      cell: data => <ActionsCell items={getActionsForCell(data.object)} />,
    },
  ];

  const noResultsText = (
    <>
      <h2>{t('supplierView.noContacts')}</h2>
      <p>{t('supplierView.getStarted')}</p>
    </>
  );

  const returnSortedUsers = () => {
    const contacts = [...supplierData.contacts];
    const ordering = {
      needs_attention: 0,
      ready_to_invite: 1,
      invited: 2,
      active: 3,
      disabled: 4,
    };
    return contacts.sort(
      (a, b) => ordering[a.enrollmentStatus] - ordering[b.enrollmentStatus]
    );
  };

  const addressLink = supplierData?.address ? (
    <Link
      target="_blank"
      rel="noopener noreferrer"
      to={`https://www.google.com/maps/search/?api=1&query=${encodeURI(
        supplierData?.address
      )}`}
    >
      {supplierData?.address}
    </Link>
  ) : (
    ''
  );

  const fieldListData = {
    colOneData: [
      {
        dataTestId: 'supplier-number',
        label: t('fields.supplier.number'),
        value: supplierData?.supplierNumber,
      },
      {
        dataTestId: 'supplier-status',
        label: t('fields.status'),
        value: statusIndicator(supplierData?.status),
      },
      {
        dataTestId: 'supplier-activated',
        label: t('fields.combinedEnrollmentStatus.enumValues.invited'),
        value: localizeDate(supplierData?.invited),
      },
      {
        dataTestId: 'supplier-enrolled',
        label: t('fields.combinedEnrollmentStatus.enumValues.enrolled'),
        value: localizeDate(supplierData?.activated),
      },
      {
        dataTestId: 'business-units',
        label: t('fields.businessUnits'),
        value: returnBusinessUnits(),
      },
    ],
    colTwoData: [
      {
        dataTestId: 'address',
        label: t('supplierView.address'),
        value: addressLink,
      },
      {
        dataTestId: 'phone',
        label: t('supplierView.phone'),
        value: supplierData?.phone,
      },
      {
        dataTestId: 'segments',
        label: t('fields.segments'),
        value: returnSegmentLinks(),
      },
      {
        dataTestId: 'merged-branches',
        label: t('supplierView.mergedBranches'),
        value: returnMergedBranches(),
      },
      {
        dataTestId: 'is2faRequired',
        label: t('fields.is2faRequired'),
        value: supplierData?.is2faRequired
          ? t('filters.trueLabel')
          : t('filters.falseLabel'),
      },
    ],
    colOneWidth: 180,
    colTwoWidth: 180,
  };

  if (!supplierData) {
    return <Progress />;
  }

  const backLink =
    localStorage.getItem('supplier-search-location') ||
    CONSTANTS.SUPPLIERS.SEARCH_BETA;

  return (
    <div className="supplier-view">
      <div className="flex-spread-row">
        <Link to={backLink}>
          &lt;&nbsp;
          {t('supplierView.returnToSuppliers')}
        </Link>
        <MiniPager
          current={paginationData.currIdx}
          nextAction={() => {
            history.push(`/suppliers/search/${paginationData.next}`);
          }}
          prevAction={() =>
            history.push(`/suppliers/search/${paginationData.prev}`)
          }
          total={paginationData.total}
        />
      </div>
      <div className="flex-spread-row">
        <h2>{supplierData.name}</h2>
        {userCanEditSuppliers(userRoles) && supplierData.status !== 'enrolled' && (
          <Button
            data-testid="supplier-view-invite-company"
            disabled={supplierData.status === 'needs_attention'}
            spinning={sendingCompanyInvite}
            theme="primary"
            onClick={inviteCompany}
          >
            {t(
              supplierData.status === 'invited'
                ? 'supplierView.resendInvitation'
                : 'supplierView.sendInvitation'
            )}
          </Button>
        )}
      </div>
      <FieldList {...fieldListData} />
      <dl className="money-grouping-container">
        <p>
          <dt>{t('supplierView.invoiceSpend')}</dt>
          <dd data-testid="invoice-spend">
            {localizeCurrency(
              supplierData.invSpendOneYear,
              supplierData.currency
            )}
          </dd>
        </p>
        {!supplierData.isSCFBuyer && (
          <>
            <p>
              <dt>{t('fields.epOfferedEnhSpendOneYear')}</dt>
              <dd data-testid="ep-offered-spendOneYear">
                {localizeCurrency(
                  supplierData.epOfferedEnhSpendOneYear,
                  supplierData.currency
                )}
              </dd>
            </p>
            <p>
              <dt>{t('fields.epTakenEnhSpendOneYear')}</dt>
              <dd data-testid="ep-taken-spendOneYear">
                {localizeCurrency(
                  supplierData.epTakenEnhSpendOneYear,
                  supplierData.currency
                )}
              </dd>
            </p>
          </>
        )}
        <p>
          <dt>{t('supplierView.tauliaInvoices')}</dt>
          <dd data-testid="taulia-invoices">
            {localizeNumber(supplierData.tauliaInvoices)}
          </dd>
        </p>
        <p>
          <dt>{t('supplierView.invoiceCount')}</dt>
          <dd>{localizeNumber(supplierData.invCountOneYear)}</dd>
        </p>
      </dl>
      {eventLogSettingEnabled && <Tabs data={tabData} />}
      {((eventLogSettingEnabled && option === 'contacts') ||
        !eventLogSettingEnabled) && (
        <ListView
          actions={actions}
          actionsEnabled
          columns={columns}
          data={returnSortedUsers()}
          isResizable
          noResultsText={noResultsText}
          showColumnConfig={false}
        />
      )}
      {eventLogSettingEnabled && option === 'eventLog' && (
        <EventLog supplierBranchId={id} />
      )}
      <BusinessUnitModal
        data={supplierData.businessUnits}
        open={showBusinessUnitModal}
        onRequestClose={() => setShowBusinessUnitModal(false)}
      />
      <EditContactModal
        open={showEditContactModal}
        data={selectedContact}
        onRequestClose={() => {
          setShowEditContactModal(false);
        }}
        onSave={contact => {
          saveContact(contact);
        }}
        onSaveAndInvite={contact => {
          saveContact(contact, true);
        }}
      />
      <ViewUserModal
        open={showViewUserModal}
        data={selectedContact}
        onRequestClose={() => {
          setShowViewUserModal(false);
        }}
      />
    </div>
  );
}

export default SupplierView;

SupplierView.defaultProps = {};

SupplierView.propTypes = {};
