import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Button, ListView } from 'taulia-ui';
import ActionCell from './DeletedSegmentActionCell';
import { RequestContext } from '../../contexts';

export default function DeletedSegmentList({ deletedSegments, viewSegments }) {
  const { t } = useTranslation();
  const request = useContext(RequestContext);
  const getFieldsConfig = () => {
    return [
      {
        key: 'name',
        label: t('supplierSegment.supplierSegmentList.name'),
        type: 'TEXT',
        width: 350,
      },
      {
        key: 'description',
        label: t('supplierSegment.supplierSegmentList.description'),
        type: 'TEXT',
        width: 350,
      },
      {
        key: 'dateArchived',
        label: t('supplierSegment.deletedSegmentList.dateDeleted'),
        type: 'DATE_TIME',
      },
      {
        key: 'numSuppliers',
        label: t('bulkInvites.supplierSummary.suppliers.title'),
        type: 'NUMBER',
        align: 'center',
      },
      {
        key: 'actions',
        label: '',
        type: 'TEXT',
        cell: segment => (
          <ActionCell request={request} segment={segment.object} />
        ),
      },
    ];
  };

  const columns = getFieldsConfig();
  const data = deletedSegments;

  return (
    <div className="supplier-segment">
      <h3>{t('supplierSegment.deletedSegmentList.title')}</h3>
      <p>{t('supplierSegment.deletedSegmentList.info')}</p>
      <Button
        style={{ margin: '20px 0' }}
        theme="text"
        id="viewSegmentsLink"
        onClick={viewSegments}
      >
        {t('supplierSegment.deletedSegmentList.viewSupplierSegments')}
      </Button>
      <ListView
        columns={columns}
        data={data}
        noResultsText={
          <p>{t('supplierSegment.deletedSegmentList.emptyResults')}</p>
        }
        isResizable={false}
        showColumnConfig={false}
        showCount={false}
        showDataLength={false}
      />
    </div>
  );
}

DeletedSegmentList.propTypes = {
  deletedSegments: PropTypes.arrayOf(PropTypes.shape()),
  viewSegments: PropTypes.func.isRequired,
};

DeletedSegmentList.defaultProps = {
  deletedSegments: [],
};
