/* eslint-disable no-param-reassign */
import { toString } from 'lodash';

// Utils helper functions, not exported from file
const addChecked = (obj, unit) => {
  Object.keys(obj).forEach(k => {
    const node = obj[k];
    if (typeof node === 'object') {
      if (unit === node.value) {
        node.checked = true;
      }
      addChecked(node, unit);
    }
  });
};

const checkCurrentRestrictions = (
  obj,
  hasRestrictions,
  hasRestrictionsList
) => {
  if (hasRestrictions) {
    hasRestrictionsList.forEach(unit => addChecked(obj, unit));
  }
};

const filterAllBusinessUnitsIds = ({ id, availableBusinessUnits = [] }) =>
  [id].concat(...availableBusinessUnits.map(filterAllBusinessUnitsIds));

const flattenedBusinessUnitIds = data =>
  [].concat(...data.map(unit => filterAllBusinessUnitsIds(unit)));

const formatLabel = (label, backupLabel) =>
  toString(label) === '' ? toString(backupLabel) : toString(label);

const newTreeDataFormat = (obj, newObj) => {
  Object.keys(obj).forEach(k => {
    const node = obj[k];
    if (typeof node === 'object') {
      if ('companyId' in obj) {
        newObj.value = obj.companyId;
        newObj.label = formatLabel(obj.companyName, 'Company');
      }
      if ('buyerId' in obj) {
        newObj.value = obj.id;
        newObj.label = formatLabel(obj.name, obj.number);
      }
      if ('availableBusinessUnits' in obj) {
        newObj.children = [];
        obj.availableBusinessUnits.forEach(item => {
          const itemObj = {};
          newTreeDataFormat(item, itemObj);
          newObj.children.push(itemObj);
        });
      }
    }
  });
};

// Functions exported from file
export const fullAccessBusinessUnits = units =>
  units.length
    ? flattenedBusinessUnitIds(units).filter(x => x !== undefined)
    : [];

export const generateTreeSelectFormat = (
  businessUnits,
  hasRestrictions,
  hasRestrictionsList
) => {
  const newTreeBusinessUnits = [];
  businessUnits.forEach(item => {
    const newObj = {};
    newTreeDataFormat(item, newObj);
    newTreeBusinessUnits.push(newObj);
  });
  checkCurrentRestrictions(
    newTreeBusinessUnits,
    hasRestrictions,
    hasRestrictionsList
  );
  return newTreeBusinessUnits;
};

export const getSelectedBusinessUnits = (selectedList, fullList) => {
  const idArr = [];
  selectedList.forEach(selectedObj => {
    if ('children' in selectedObj) {
      fullList.forEach(fullObj => {
        if (fullObj.value === selectedObj.value) {
          idArr.push(fullObj.children.map(item => item.value));
        }
      });
    } else {
      idArr.push(selectedObj.value);
    }
  });
  return [].concat(...idArr);
};
