import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Field,
  FieldLabel,
  FieldError,
  Text,
  Textarea,
  toast,
} from 'taulia-ui';
import { RequestContext } from '../../contexts';

export default class SegmentForm extends Component {
  static propTypes = {
    closeCreateSegmentModal: PropTypes.func,
    loadSegments: PropTypes.func,
    params: PropTypes.shape(),
    segments: PropTypes.arrayOf(PropTypes.shape()),
    segmentToEdit: PropTypes.shape(),
    t: PropTypes.func,
  };

  static defaultProps = {
    closeCreateSegmentModal: null,
    loadSegments: null,
    params: {},
    segments: [],
    segmentToEdit: { name: '', description: '' },
    t: null,
  };

  state = {
    name: { value: '', notUniqueError: false },
    description: { value: '' },
  };

  componentDidMount() {
    const { segmentToEdit } = this.props;
    this.setState({
      name: {
        value: segmentToEdit.name,
        notUniqueError: false,
        originalValue: segmentToEdit.name,
      },
      description: {
        value: segmentToEdit.description,
        originalValue: segmentToEdit.description,
      },
    });
  }

  createOrUpdateSegment = e => {
    e.preventDefault();

    const { name, description } = this.state;
    const {
      closeCreateSegmentModal,
      loadSegments,
      segmentToEdit,
      t,
    } = this.props;
    const request = this.context;
    const newSegment = {
      id: segmentToEdit.id,
      name: name.value,
      description: description.value,
    };

    if (!this.isNameUnique(newSegment)) {
      this.setState({
        name: Object.assign({}, name, { notUniqueError: true }),
      });
      return;
    }

    const alertType = segmentToEdit.id
      ? 'segmentWasUpdated'
      : 'segmentWasAdded';

    request('POST', '/api/segment')
      .send(newSegment)
      .then(() => {
        closeCreateSegmentModal();
        loadSegments();
        toast.success(
          t(`supplierSegment.supplierSegmentForm.${alertType}`, {
            segmentName: newSegment.name,
          })
        );
      });
  };

  onDescriptionChange = event => {
    const { description } = this.state;
    this.setState({
      description: { ...description, value: event.target.value },
    });
  };

  onNameChange = event => {
    const { name } = this.state;
    this.setState({
      name: { ...name, value: event.target.value, notUniqueError: false },
    });
  };

  isNameUnique(newSegment) {
    const { segments } = this.props;
    const matchingSegments = segments.filter(
      other =>
        other.id !== newSegment.id &&
        other.name.toLowerCase() === newSegment.name.toLowerCase()
    );
    return matchingSegments.length === 0;
  }

  isSubmitButtonDisabled() {
    const { name, description } = this.state;
    return (
      !name.value ||
      name.notUniqueError ||
      (name.value === name.originalValue &&
        description.value === description.originalValue)
    );
  }

  static contextType = RequestContext;

  renderButtons = () => {
    const { closeCreateSegmentModal, t } = this.props;
    return (
      <div
        className="btn-container"
        style={{ paddingTop: '15px', float: 'right' }}
      >
        <Button
          id="segment-form-cancel-link"
          onClick={closeCreateSegmentModal}
          onKeyDown={closeCreateSegmentModal}
          theme="text"
        >
          {t('standard.cancel')}
        </Button>
        <Button
          id="segment-form-submit-button"
          type="submit"
          theme="primary"
          disabled={this.isSubmitButtonDisabled()}
        >
          {t('supplierSegment.supplierSegmentForm.saveSegment')}
        </Button>
      </div>
    );
  };

  renderInfo() {
    const { segmentToEdit, t } = this.props;
    if (!segmentToEdit.id) {
      return '';
    }
    return (
      <small>
        <em>
          {t('supplierSegment.supplierSegmentForm.field.info', {
            createdOn: ` ${new Date(segmentToEdit.dateCreated).toString()} `,
            createdBy: ` ${segmentToEdit.createdBy}`,
          })}
        </em>
      </small>
    );
  }

  render() {
    const { name, description } = this.state;
    const { segmentToEdit, t } = this.props;
    const nameValidationProp = {};
    if (name.notUniqueError) {
      nameValidationProp.validationState = 'error';
    }

    return (
      <>
        <h3>
          {segmentToEdit.id
            ? t('supplierSegment.supplierSegmentForm.edit.title')
            : t('supplierSegment.supplierSegmentForm.create.title')}
        </h3>
        <form
          data-testid="form"
          id="segmentForm"
          onSubmit={this.createOrUpdateSegment}
        >
          <Field {...nameValidationProp}>
            <FieldLabel htmlFor="segment-name">
              {t('supplierSegment.supplierSegmentForm.field.name.label')}
            </FieldLabel>
            <Text
              id="segment-name"
              autoFocus="autoFocus"
              type="text"
              maxLength="255"
              size="44"
              name="name"
              placeholder={t(
                'supplierSegment.supplierSegmentForm.field.name.placeholder'
              )}
              value={name.value}
              onChange={this.onNameChange}
            />
            <FieldError>
              {t('supplierSegment.supplierSegmentForm.field.name.error')}
            </FieldError>
          </Field>
          <Field>
            <FieldLabel htmlFor="segment-desc">
              {t('supplierSegment.supplierSegmentForm.field.description')}
            </FieldLabel>
            <Textarea
              id="segment-desc"
              style={{ resize: 'vertical' }}
              rows="8"
              cols="45"
              maxLength="1024"
              name="description"
              value={description.value}
              onChange={this.onDescriptionChange}
            />
          </Field>
          {this.renderInfo()}
          {this.renderButtons()}
        </form>
      </>
    );
  }
}
