import React from 'react';
import PropTypes from 'prop-types';
import { Alert, Field, Progress, Select } from 'taulia-ui';
import { withTranslation } from 'react-i18next';
import CONSTANTS from '../../globals';
import { RequestContext } from '../../contexts';
import { GenericErrorMessage } from '../../Utils/GenericErrorMessage';

class CompanySettings extends React.Component {
  static propTypes = {
    t: PropTypes.func.isRequired,
  };

  state = {
    buyerGroupLoaded: false,
    buyerGroupCompanies: [],
    selectedBuyer: '',
    showBuyerGroupError: false,
    showBuyerSelectSuccess: false,
  };

  componentDidMount() {
    const request = this.context;
    request('GET', '/api/buyer-group')
      .then(response => {
        this.setState({
          buyerGroupLoaded: true,
          buyerGroupCompanies: response.body.companies || [],
          selectedBuyer: response.body.selectedBuyer,
          showBuyerGroupError: false,
        });
      })
      .catch(error => {
        this.setBuyerGroupError({ buyerGroupLoaded: true });
        if (CONSTANTS.DEBUG) {
          console.log(error); // eslint-disable-line no-console
        }
      });
  }

  onCompanyChange = selectedBuyer => {
    const request = this.context;
    request('POST', '/api/selected-buyer')
      .send({ buyerId: selectedBuyer })
      .then(() => {
        this.setBuyerSelectSuccess({ selectedBuyer });
        this.startReloadPageTimer();
      })
      .catch(error => {
        this.setBuyerGroupError();
        if (CONSTANTS.DEBUG) {
          console.log(error); // eslint-disable-line no-console
        }
      });
  };

  getSelectedBuyerName = () => {
    const { buyerGroupCompanies, selectedBuyer } = this.state;
    const selectedCompany = buyerGroupCompanies.find(
      company => company.id === selectedBuyer
    );
    return selectedCompany ? selectedCompany.name : '';
  };

  setBuyerGroupError = props => {
    this.setState({
      showBuyerGroupError: true,
      showBuyerSelectSuccess: false,
      ...props,
    });
  };

  setBuyerSelectSuccess = props => {
    this.setState({
      showBuyerGroupError: false,
      showBuyerSelectSuccess: true,
      ...props,
    });
  };

  startReloadPageTimer = () => {
    setTimeout(() => {
      window.location.reload();
    }, 3000);
  };

  static contextType = RequestContext;

  render() {
    const {
      buyerGroupCompanies,
      buyerGroupLoaded,
      selectedBuyer,
      showBuyerGroupError,
      showBuyerSelectSuccess,
    } = this.state;
    const { t } = this.props;

    const options = buyerGroupCompanies.map(group => {
      return { label: group.name, value: group.id };
    });

    return (
      <div className="company-settings-wrapper">
        <h3>{t('nav.buyer_area.settings.company_settings')}</h3>
        {!buyerGroupLoaded && <Progress />}
        {showBuyerGroupError && (
          <div className="app-error">
            <GenericErrorMessage />
          </div>
        )}
        {showBuyerSelectSuccess && (
          <Alert inline theme="success">
            {t('company_settings.change_company_success', {
              buyerCompany: this.getSelectedBuyerName(),
            })}
          </Alert>
        )}
        {buyerGroupCompanies.length > 1 && (
          <div>
            <p style={{ maxWidth: '600px' }}>
              {t('company_settings.info', {
                buyerCompany: this.getSelectedBuyerName(),
              })}
            </p>
            <Field>
              <Select
                enableSearch={false}
                options={options}
                multiple={false}
                onChange={this.onCompanyChange}
                value={selectedBuyer}
              />
            </Field>
          </div>
        )}
        {buyerGroupCompanies.length < 2 && t('company_settings.no_settings')}
      </div>
    );
  }
}

export default withTranslation()(CompanySettings);
