import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Button, DateTimeCell, EnhancedSearch, ProgressBar } from 'taulia-ui';
import EventLogModal from './EventLogModal';
import { useFetchContext } from '../../hooks';
import {
  progressBarColorMap,
  progressBarLabelMap,
} from '../../Utils/SupplierUtils';
import { genericErrorToast } from '../../Utils/utils';

const EventLog = ({ supplierBranchId }) => {
  const [eventModalData, setEventModalData] = useState({});
  const [eventModalSelectedId, setEventModalSelectedId] = useState();
  const [showEventModal, setShowEventModal] = useState(false);
  const { data: eventLogIdData, error } = useFetchContext(
    `/api/eventLog/${eventModalSelectedId}`
  );
  const { t } = useTranslation();

  useEffect(() => {
    setEventModalData(eventLogIdData);
    if (error) {
      genericErrorToast();
    }
  }, [eventLogIdData, error]);

  const handleBankEventClick = props => {
    const { id } = props;
    setEventModalSelectedId(id);
    setShowEventModal(true);
  };

  const handleModalClose = () => {
    setEventModalSelectedId();
    setShowEventModal(false);
  };

  const enumLinkCell = props => {
    const { column, value } = props;
    const { enumOptions } = column;
    const option = enumOptions.find(it => it.value === value);
    const enumValue = option?.label || value;

    const onClick = handleBankEventClick
      ? event => {
          handleBankEventClick(props);
          event.preventDefault();
        }
      : null;
    return (
      <Button theme="text" title={enumValue} onClick={onClick}>
        {enumValue ?? <span></span>}
      </Button>
    );
  };

  const progressBarCell = props => {
    const { value, object } = props;
    const roundedRiskScoreTotal = object?.riskScoreTotal
      ? Math.round(object.riskScoreTotal)
      : 0;

    return (
      <span title={value} className="event-log-progress-bar">
        <ProgressBar
          color={progressBarColorMap[value] || 'gray'}
          value={roundedRiskScoreTotal || 0}
          label={value ? t(`supplierView.${progressBarLabelMap[value]}`) : ''}
        />
      </span>
    );
  };

  const onQueryChange = query => {
    query.filters.push({
      field: 'supplierBranchId',
      operator: 'EQUAL',
      values: [supplierBranchId],
    });
    return query;
  };

  const defaultQuery = {
    select: [
      'eventType',
      'riskLevel',
      'dateCreated',
      'userEmail',
      'ipAddress',
      'countryCode',
      'regionName',
      'riskFactors',
      'riskScoreTotal',
      'userId',
      'userFirstName',
      'userLastName',
      'userLastLogin',
      'userLastPasswordResetRequest',
      'userDateCreated',
      'is2FaEnabled',
      'id',
    ],
    filters: [
      { field: 'dateCreated', operator: 'EQUAL', values: [] },
      { field: 'riskLevel', operator: 'EQUAL', values: [] },
      { field: 'userEmail', operator: 'CONTAINS', values: [] },
      {
        field: 'supplierBranchId',
        operator: 'EQUAL',
        values: [supplierBranchId],
      },
    ],
    sorts: [
      {
        field: 'dateCreated',
        type: 'DESC',
      },
    ],
  };

  const props = {
    csvExportLimit: 100000,
    defaultQuery,
    excludedFields: [
      'is2FaEnabled',
      'riskScoreTotal',
      'userDateCreated',
      'userFirstName',
      'userId',
      'userLastLogin',
      'userLastName',
      'userLastPasswordResetRequest',
      'id',
      'riskFactors',
      'supplierBranchId',
    ],
    fieldOverrides: {
      eventType: {
        column: {
          cell: enumLinkCell,
          fixedOrder: 1,
          width: 175,
          enumOptions: [
            {
              value: 'BANK_ADD',
              label: t('supplierView.BANK_ADD'),
            },
            {
              value: 'BANK_CHANGE',
              label: t('supplierView.BANK_CHANGE'),
            },
            {
              value: 'BANK_DELETE',
              label: t('supplierView.BANK_DELETE'),
            },
          ],
        },
        type: 'TEXT',
      },
      riskLevel: {
        column: {
          fixedOrder: 2,
          width: 200,
          cell: progressBarCell,
        },
      },
      dateCreated: {
        column: {
          cell: DateTimeCell,
          width: 200,
        },
      },
      userEmail: { label: t('fields.email'), column: { width: 250 } },
    },
    onQueryChange,
    showColumnConfig: false,
    uri: '/eui/simEventLog',
  };

  return (
    <div
      className="event-log"
      data-testid="supplier-view-event-log"
      key={supplierBranchId}
    >
      <EnhancedSearch {...props} />
      <EventLogModal
        key="event-log"
        data={eventModalData}
        onRequestClose={handleModalClose}
        open={showEventModal}
      />
    </div>
  );
};

export default EventLog;

EventLog.propTypes = {
  column: PropTypes.shape({
    enumOptions: PropTypes.arrayOf(
      PropTypes.shape({ value: PropTypes.string })
    ),
  }),
  data: PropTypes.shape(),
  id: PropTypes.string,
  object: PropTypes.shape(),
  supplierBranchId: PropTypes.string.isRequired,
  value: PropTypes.string,
};

EventLog.defaultProps = {
  column: { enumOptions: [] },
  data: {},
  id: '',
  object: {},
  value: null,
};
