import React from 'react';
import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import CONSTANTS from '../../globals';
import { useFeatureFetch } from '../../hooks';
import { GenericErrorMessage } from '../../Utils/GenericErrorMessage';
import SupplierSearch from './SupplierSearch';

// TODO remove when /supplier/search-beta is no longer used

const SupplierSearchBeta = props => {
  const history = useHistory();
  const supplierCenterFeature = useFeatureFetch(
    CONSTANTS.FEATURES.SUPPLIER_CENTER_NEW_UI
  );

  if (supplierCenterFeature.loading) {
    return null;
  }

  if (supplierCenterFeature.error) {
    return <GenericErrorMessage />;
  }

  if (supplierCenterFeature.isEnabled) {
    const { location } = props;
    history.push({
      pathname: CONSTANTS.SUPPLIERS.SEARCH,
      search: location.search,
    });
  }

  return <SupplierSearch {...props} />;
};

SupplierSearchBeta.propTypes = {
  location: PropTypes.shape(),
};

SupplierSearchBeta.defaultProps = {
  location: {},
};

export default SupplierSearchBeta;
