import React, { useContext, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Button, ListView, StatusIndicator } from 'taulia-ui';
import {
  localizeDate,
  localizeNumber,
  localizeTime,
} from '../../Utils/localizeUtils';
import { downloadBlob, genericErrorToast } from '../../Utils/utils';
import { RequestContext, UserSettingsContext } from '../../contexts';

const Exports = () => {
  const { t } = useTranslation();
  const request = useContext(RequestContext);
  const { userId } = useContext(UserSettingsContext);
  const [documentData, setDocumentData] = useState([]);
  const [paginationData, setPaginationData] = useState({
    page: 0,
    maxPerPage: 10,
    totalResults: 0,
  });

  let pollDelay = 500;

  const getDocuments = (currentPage = 0, pageSize = 10) => {
    request('GET', `/api/document/export/user/${userId}`)
      .query({
        includeTotal: true,
        maxPerPage: pageSize,
        page: currentPage,
      })
      .then(res => {
        if (res.body?.success) {
          const { documentExports, maxPerPage, page, totalResults } = res.body;
          setDocumentData(documentExports);
          setPaginationData({ maxPerPage, page, totalResults });
          const isPending = documentExports.some(
            doc => doc.status === 'PENDING'
          );
          if (isPending) {
            setTimeout(() => getDocuments(currentPage, pageSize), pollDelay);
            pollDelay *= 2;
          }
        }
      })
      .catch(genericErrorToast);
  };

  useEffect(() => {
    getDocuments();
  }, []);

  const onPageChange = (page, pageSize) => {
    getDocuments(page, pageSize);
    setPaginationData({ ...paginationData, maxPerPage: pageSize, page });
  };

  const statusIndicator = status => {
    const statusColorMap = {
      PENDING: 'yellow',
      SUCCESS: 'green',
      FAILED: 'red',
    };
    return (
      <StatusIndicator color={statusColorMap[status]}>
        {t(`documentExports.${status}`)}
      </StatusIndicator>
    );
  };

  const downloadFile = (id, dateCreated, recordType) => {
    request('GET', `/api/document/export/attachmentId/${id}`)
      .then(res => {
        const resultsToDownload = new Blob([res.text], {
          type: 'text/csv;charset=utf-8',
        });
        const translatedType = t(
          `documentExports.${recordType.toLowerCase()}Export`
        );
        const localizedDate = localizeDate(dateCreated);
        downloadBlob(
          resultsToDownload,
          `${translatedType} 
          ${localizedDate}.csv`
        );
      })
      .catch(genericErrorToast);
  };

  const columns = [
    {
      key: 'file',
      label: t('documentExports.file'),
      type: 'TEXT',
      cell: data => {
        const { recordType, dateCreated, fileType } = data.object;
        const date = localizeDate(dateCreated);
        const time = localizeTime(dateCreated);
        return (
          <div className="table-group">
            <span>
              {t(`documentExports.${recordType.toLowerCase()}Export`)}
            </span>
            <span>
              {t('documentExports.exportedToOn', {
                date,
                fileType,
                time,
              })}
            </span>
          </div>
        );
      },
      width: 400,
    },
    {
      key: 'status',
      label: t('fields.status'),
      type: 'TEXT',
      cell: data => statusIndicator(data.object.status),
    },
    {
      key: 'recordsExported',
      label: t('documentExports.recordsExported'),
      type: 'TEXT',
      width: 400,
      cell: data => {
        const { recordCount, recordType } = data.object;
        return (
          <div className="table-group">
            <span>{localizeNumber(recordCount)}</span>
            <span>
              {t(`documentExports.${recordType.toLowerCase()}`, {
                count: recordCount,
              })}
            </span>
          </div>
        );
      },
    },
    {
      key: 'actions',
      label: '',
      cell: data => {
        const { attachmentId, dateCreated, recordType } = data.object;
        if (attachmentId) {
          return (
            <Button
              onClick={() =>
                downloadFile(attachmentId, dateCreated, recordType)
              }
            >
              {t('documentExports.downloadFile')}
            </Button>
          );
        }
        return null;
      },
    },
  ];

  return (
    <div>
      <h1>{t('nav.buyer_area.documents.exports')}</h1>
      <ListView
        columns={columns}
        data={documentData}
        isResizable
        noResultsText={<p>{t('documentExports.noDocuments')}</p>}
        onPageChange={onPageChange}
        page={paginationData?.page}
        pageSize={paginationData?.maxPerPage}
        paginationEnabled={!!paginationData}
        showColumnConfig={false}
        showCount
        totalCount={paginationData?.totalResults}
      />
    </div>
  );
};

export default Exports;
