import React, { Fragment } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'taulia-ui';
// eslint-disable-next-line no-unused-vars
import { useFetchContext } from '../../hooks';

const Footer = () => {
  const { t } = useTranslation();
  const { data, error } = useFetchContext('/api/menu/buyer_area_footer');

  if (error) {
    // eslint-disable-next-line no-console
    console.error('Error loading Footer Menu');
  }

  if (data) {
    const { children } = data;
    return (
      <div className="footer">
        {children.map((item, index, arr) => {
          const middle = index + 1 === Math.ceil(arr.length / 2);
          const props = item.isLocal
            ? { to: item.path }
            : { to: item.url, target: '_blank' };
          return (
            <Fragment key={item.label}>
              <span>
                <Link {...props}>{t(item.label)}</Link>
              </span>
              {middle && (
                <img
                  alt="Taulia logo"
                  src="https://storage.googleapis.com/brandings/taulia-styles/tau-logo-icon-only.svg"
                />
              )}
            </Fragment>
          );
        })}
      </div>
    );
  }

  return null;
};

export default Footer;
