import React from 'react';
import PropTypes from 'prop-types';
import {
  EnhancedSearch,
  LinkCell,
  Modal,
  StatusIndicator,
  toast,
} from 'taulia-ui';
import { withTranslation } from 'react-i18next';
import { isEqual } from 'lodash';
import qs from 'qs';
import ReviewPage from '../BulkInvitesPages/ReviewPage';
import AddSegmentsModal from './AddSegmentsModal';
import UploadContactsModal from './UploadContactsModal';
import {
  statusColorMap,
  userCanEditSuppliers,
} from '../../Utils/SupplierUtils';
import CONSTANTS from '../../globals';
import { UserSettingsContext } from '../../contexts';
import { genericErrorToast } from '../../Utils/utils';

class SupplierSearch extends React.Component {
  static propTypes = {
    csvExportLimit: PropTypes.number.isRequired,
    i18n: PropTypes.shape(),
    location: PropTypes.shape().isRequired,
    history: PropTypes.shape().isRequired,
    request: PropTypes.func.isRequired,
    settings: PropTypes.shape(),
    t: PropTypes.func.isRequired,
  };

  static defaultProps = {
    i18n: { language: 'en-US ' },
    settings: { segmentsEnabled: false, isScfBuyer: false, isSingleErp: true },
  };

  state = {
    countryOptions: [],
    regionOptions: [],
    segments: [],
    selectedSuppliers: {},
    showAddSegmentsModal: false,
    showInviteSuppliersModal: false,
    showUploadContactsModal: false,
    onActionComplete: () => {
      // do nothing
    },
  };

  componentDidMount() {
    const { i18n, settings } = this.props;
    this.loadOptions('countries', i18n.language).then(options => {
      this.setState({ countryOptions: options });
    });
    this.loadOptions('regions', i18n.language).then(options => {
      this.setState({ regionOptions: options });
    });
    if (settings.segmentsEnabled) {
      this.loadSegments();
    }
  }

  onDataChange = data => {
    const {
      location: { pathname, search },
    } = this.props;
    localStorage.setItem(
      'supplierData',
      data.map(supplierItem => supplierItem.id)
    );
    localStorage.setItem('supplier-search-location', pathname + search);
  };

  onQueryChange = query => {
    const { location, history } = this.props;
    this.setState({ query });
    const urlParams = qs.stringify(query, {
      encode: true,
    });
    if (!isEqual(query, CONSTANTS.DEFAULT_QUERY)) {
      history.push({
        pathname: location.pathname,
        search: `?${urlParams}`,
      });
    }
  };

  loadOptions = (field, locale) => {
    const { request } = this.props;
    return request('GET', `/eui/supplier/${field}?locale=${locale}`).then(
      res => res.body
    );
  };

  loadSegments = () => {
    const { request } = this.props;
    request('GET', '/api/segments')
      .then(res => {
        const { segments } = res.body;
        this.setState({ segments });
      })
      .catch(() => console.error('Error loading segments')); // eslint-disable-line no-console
  };

  addMoneyFields = overrides => {
    const moneyFields = [
      'invSpendThirtyDay',
      'invSpendOneYear',
      'eInvEfileSpendThirtyDay',
      'eInvEfileSpendOneYear',
      'eInvEsendSpendThirtyDay',
      'eInvEsendSpendOneYear',
      'eInvPortalSpendThirtyDay',
      'eInvPortalSpendOneYear',
      'revenueUsd',
      'epOfferedEnhSpendThirtyDay',
      'epTakenEnhSpendOneYear',
      'epTakenEnhSpendThirtyDay',
      'epOfferedEnhSpendOneYear',
    ];

    const overridesCopy = { ...overrides };

    moneyFields.forEach(field => {
      overridesCopy[field] = { type: 'MONEY', currency: 'USD' };
    });
    return overridesCopy;
  };

  addSuppliersToSegment = async segment => {
    const { request, t } = this.props;
    const { selectedSuppliers, query, onActionComplete } = this.state;
    let supplierNumbers;
    if (selectedSuppliers.all) {
      await request('POST', '/eui/supplier/search')
        .send({ ...this.prepareQuery(query, 'number') })
        .then(response => {
          supplierNumbers = response.body.results.map(item => item.number);
        });
    } else {
      const idQuery = {
        filters: [
          { field: 'id', operator: 'EQUAL', values: selectedSuppliers.ids },
        ],
        select: ['number'],
        pagination: {},
        sorts: [],
      };

      await request('POST', '/eui/supplier/search')
        .send(idQuery)
        .then(response => {
          supplierNumbers = response.body.results.map(
            supplier => supplier.number
          );
        });
    }

    const data = {
      segmentId: segment.id,
      supplierNumbers,
      previewOnly: false,
      replaceExistingAssociations: false,
    };

    if (selectedSuppliers.selectedCount > 50) {
      request('POST', '/api/segment/associateSuppliersAsync')
        .send(data)
        .then(() => {
          toast.info(
            t('supplierSearch.addSuppliersProgress', {
              suppliers: selectedSuppliers.selectedCount,
              segmentName: segment.name,
            })
          );
          this.setState({ showAddSegmentsModal: false });
        })
        .catch(() => toast.error(t('supplierSearch.addSuppliersFailure')));
    } else {
      const supplierCount = t('supplierSearch.supplierCount', {
        count: selectedSuppliers.selectedCount,
      });
      request('POST', '/api/segment/associateSuppliers')
        .send(data)
        .then(() => {
          toast.success(
            t('supplierSearch.addSuppliersSuccess', {
              supplierCount,
              segmentName: segment.name,
            })
          );
          onActionComplete();
          this.setState({ showAddSegmentsModal: false });
        })
        .catch(() => toast.error(t('supplierSearch.addSuppliersFailure')));
    }
  };

  openInviteModal = supplierDetailsIds => {
    const { request, history } = this.props;
    request('POST', '/api/bulkInvites/criteria')
      .send({ supplierDetailsIds })
      .then(() => history.push(CONSTANTS.SUPPLIERS.BULK_INVITES.REVIEW))
      .then(() => this.setState({ showInviteSuppliersModal: true }))
      .catch(genericErrorToast);
  };

  inviteSuppliers = selection => {
    const { request } = this.props;
    if (selection.all) {
      const { query } = this.state;
      request('POST', '/eui/supplier/search')
        .send({ ...this.prepareQuery(query) })
        .then(response => {
          const ids = [];
          response.body.results.forEach(supplier => {
            ids.push(supplier.id);
          });
          this.openInviteModal(ids);
        });
    } else {
      this.openInviteModal(selection.ids);
    }
  };

  prepareQuery = (query, select = 'id') => {
    // Remove any filters from the query that don't have valid arguments.
    const unaryOperators = ['IS_NULL', 'NOT_NULL'];
    const ternaryOperators = ['BETWEEN', 'NOT_BETWEEN'];

    const filters = query.filters.filter(filter => {
      const values = filter.values ? filter.values.filter(it => it) : [];
      const operator = filter.operator || 'EQUAL';

      if (unaryOperators.includes(operator)) {
        return true;
      }
      if (ternaryOperators.includes(operator)) {
        return values.length === 2;
      }
      return values.length > 0;
    });

    return {
      filters,
      pagination: {},
      select: [select],
      sorts: [],
    };
  };

  statusColorCell = cell => {
    const { t } = this.props;
    const { value } = cell;
    return (
      <span title={value}>
        <StatusIndicator color={statusColorMap[value]}>
          {t(`fields.combinedEnrollmentStatus.enumValues.${value}`)}
        </StatusIndicator>
      </span>
    );
  };

  static contextType = UserSettingsContext;

  render() {
    const {
      countryOptions,
      regionOptions,
      selectedSuppliers,
      segments,
      showAddSegmentsModal,
      showInviteSuppliersModal,
      showUploadContactsModal,
    } = this.state;
    const { csvExportLimit, location, request, settings, t } = this.props;
    const { companyId, userId, userRoles } = this.context;
    if (!countryOptions.length || !regionOptions.length) {
      return null;
    }
    const regionOptionMatcher = (option, search, defaultOptionMatcher) => {
      if (defaultOptionMatcher(option, search)) {
        const filters =
          qs.parse(location.search.slice(1), {
            decode: true,
            arrayLimit: 50,
          }).filters || [];

        const countryFilters =
          filters.find(filter => filter.field === 'country') || {};

        if (countryFilters.values) {
          // If there is a country being filtered, make sure the region is in at least one of the filtered countries.
          return countryFilters.values.some(country =>
            option.value.includes(`${country}:`)
          );
        }

        return true;
      }

      return false;
    };

    const fieldOverrides = {
      name: {
        column: {
          cell: LinkCell,
          linkUrlGetter: ({ object }) => `/suppliers/search/${object.id}`,
          fixedOrder: 1,
          width: 250,
        },
      },
      number: {
        column: {
          cell: LinkCell,
          linkUrlGetter: ({ object }) => `/suppliers/search/${object.id}`,
          fixedOrder: 2,
          width: 200,
        },
      },
      combinedEnrollmentStatus: { column: { cell: this.statusColorCell } },

      // These two fields are overridden to be type `ENUM` so they will use a typeahead and translate country and
      // region ISO codes to a label with the localized name.
      country: { type: 'ENUM', enumOptions: countryOptions },
      region: {
        type: 'ENUM',
        enumOptions: regionOptions,
        optionMatcher: regionOptionMatcher,
      },
    };

    const actions = [
      {
        label: t('supplierSearch.inviteActionTitle'),
        allowEmptySelection: false,
        onClick: selection => {
          this.inviteSuppliers(selection);
        },
      },
      {
        label: t('supplierSearch.uploadContacts'),
        preventSelectedSelection: true,
        onClick: () => this.setState({ showUploadContactsModal: true }),
      },
    ];

    if (settings.segmentsEnabled) {
      actions.push({
        label: t('supplierSearch.addSuppliersTitle'),
        allowEmptySelection: false,
        onClick: (selection, onActionComplete) => {
          this.setState({
            selectedSuppliers: selection,
            showAddSegmentsModal: true,
            onActionComplete,
          });
        },
      });
    }

    const excludedFields = [
      'id',
      'isEnrolled',
      'invitationEligibilityStatus',
      'enrollmentStatus',
      'businessUnitNumbers',
      'invPaidSpendThirtyDayEur',
      'invPaidSpendThirtyDayGbp',
      'invPaidSpendThirtyDayCan',
      'invPaidSpendThirtyDayAus',
      'invPaidSpendThirtyDayLocal',
      'invPaidSpendOneYearEur',
      'invPaidSpendOneYearGbp',
      'invPaidSpendOneYearCan',
      'invPaidSpendOneYearAus',
      'invPaidSpendOneYearLocal',
      'eInvEfileSpendThirtyDayEur',
      'eInvEfileSpendThirtyDayGbp',
      'eInvEfileSpendThirtyDayCan',
      'eInvEfileSpendThirtyDayAus',
      'eInvEfileSpendThirtyDayLocal',
      'eInvEfileSpendOneYearEur',
      'eInvEfileSpendOneYearGbp',
      'eInvEfileSpendOneYearCan',
      'eInvEfileSpendOneYearAus',
      'eInvEfileSpendOneYearLocal',
      'eInvEsendSpendThirtyDayEur',
      'eInvEsendSpendThirtyDayGbp',
      'eInvEsendSpendThirtyDayCan',
      'eInvEsendSpendThirtyDayAus',
      'eInvEsendSpendThirtyDayLocal',
      'eInvEsendSpendOneYearEur',
      'eInvEsendSpendOneYearGbp',
      'eInvEsendSpendOneYearCan',
      'eInvEsendSpendOneYearAus',
      'eInvEsendSpendOneYearLocal',
      'eInvPortalSpendThirtyDayEur',
      'eInvPortalSpendThirtyDayGbp',
      'eInvPortalSpendThirtyDayCan',
      'eInvPortalSpendThirtyDayAus',
      'eInvPortalSpendThirtyDayLocal',
      'eInvPortalSpendOneYearEur',
      'eInvPortalSpendOneYearGbp',
      'eInvPortalSpendOneYearCan',
      'eInvPortalSpendOneYearAus',
      'eInvPortalSpendOneYearLocal',
    ];
    if (settings.isScfBuyer) {
      excludedFields.push(
        'epOfferedEnhSpendThirtyDay',
        'epTakenEnhSpendOneYear',
        'epTakenEnhSpendThirtyDay',
        'epOfferedEnhSpendOneYear'
      );
    }
    if (settings.isSingleErp) {
      excludedFields.push('clientId');
    }
    const successCallback = res => {
      const { history } = this.props;
      const suppliers = t('supplierSegment.alert.supplierCount', {
        count: res.body.documentExport.recordCount,
      });
      toast.success(t('documentExports.successToast', { suppliers }));
      history.push(CONSTANTS.DOCUMENTS.EXPORTS);
    };

    const enhancedSearchProps = {
      actions: userCanEditSuppliers(userRoles) ? actions : [],
      csvExportLimit,
      defaultQuery: CONSTANTS.DEFAULT_QUERY,
      documentExportData: {
        buyerId: companyId,
        errorCallback: () => toast.error(t('documentExports.errorToast')),
        successCallback,
        userId,
      },
      excludedFields,
      fieldOverrides: this.addMoneyFields(fieldOverrides),
      location,
      onDataChange: this.onDataChange,
      onQueryChange: this.onQueryChange,
      request,
      uri: '/eui/supplier',
    };

    return (
      <div>
        <EnhancedSearch {...enhancedSearchProps} />
        <AddSegmentsModal
          onRequestClose={() => this.setState({ showAddSegmentsModal: false })}
          open={showAddSegmentsModal}
          request={request}
          selectedSuppliers={selectedSuppliers}
          segments={segments}
          onAddSuppliersToSegment={this.addSuppliersToSegment}
        />
        <UploadContactsModal
          onRequestClose={() =>
            this.setState({ showUploadContactsModal: false })
          }
          open={showUploadContactsModal}
          request={request}
        />
        <Modal
          onRequestClose={() =>
            this.setState({ showInviteSuppliersModal: false })
          }
          open={showInviteSuppliersModal}
          request={request}
        >
          <ReviewPage
            onRequestClose={() =>
              this.setState({ showInviteSuppliersModal: false })
            }
            request={request}
          />
        </Modal>
      </div>
    );
  }
}

export default withTranslation()(SupplierSearch);
