import React, { useEffect, useReducer, useState } from 'react';
import {
  Button,
  Col,
  Field,
  FieldErrorMap,
  FieldLabel,
  Grid,
  Modal,
  PasswordValidator,
  StatusIndicator,
  Text,
  toast,
} from 'taulia-ui';
import { every } from 'lodash';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';
import { useFetchContext, useSendContext } from '../../hooks';

const INITIAL_STATE = {
  newPassword: '',
  oldPassword: '',
  errors: {},
  validations: {
    length: false,
    upper: false,
    lower: false,
    number: false,
    match: false,
  },
  authType: '',
  phoneNumber: '',
  userId: '',
};

const SecuritySettings = () => {
  const { t } = useTranslation();
  const location = useLocation();

  const reducer = (prevState, updatedProperty) => ({
    ...prevState,
    ...updatedProperty,
  });

  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [showDisable2faModal, setShowDisable2faModal] = useState(false);
  const [status2fa, setStatus2fa] = useState(false);
  const [state, setState] = useReducer(reducer, INITIAL_STATE);

  const { data: user2faData, loading: user2faDataLoading } = useFetchContext(
    '/api/security/user-2fa-config'
  );

  const {
    data: updatedPassword,
    doSend: sendPassword,
    error: passwordError,
    loading: passwordLoading,
  } = useSendContext('PUT', '/api/password');

  const {
    doSend: doDelete,
    error: deleteError,
    success: deleteSuccess,
  } = useSendContext('DELETE', '/api/security/user-2fa-config');

  const translateError = errorCode => {
    const errorMap = {
      INCORRECT_PASSWORD: 'profile.errors.toast.incorrectPassword',
      OLD_PASSWORD_MISMATCH: 'profile.errors.toast.passwordMismatch',
      PASSWORD_VALIDATION_FAILS: 'profile.errors.toast.invalidPassword',
    };
    const error = errorMap[errorCode] || 'standard.error';
    return t(error);
  };

  useEffect(() => {
    // Clears modal on exit
    setState(INITIAL_STATE);
  }, [showPasswordModal]);

  useEffect(() => {
    if (user2faData) {
      setStatus2fa(user2faData.enrolled);
      setState({
        authType: user2faData.type,
        phoneNumber: user2faData.phoneNumber,
        userId: user2faData.userId,
      });
    }
  }, [user2faData]);
  useEffect(() => {
    if (updatedPassword) {
      toast.success(t('security_settings.successToast'));
    }
  }, [updatedPassword]);

  useEffect(() => {
    if (deleteSuccess) {
      toast.success(t('authentication.disable2faSuccessToast'));
    }
  }, [deleteSuccess]);

  useEffect(() => {
    if (deleteError) {
      toast.error(t('standard.error'));
    }
  }, [deleteError]);

  useEffect(() => {
    if (passwordError) {
      toast.error(translateError(passwordError.failureReason));
    }
  }, [passwordError]);

  useEffect(() => {
    if (location.search.includes('user2FASetupSuccess')) {
      toast.success(t('authentication.user2faSetupSuccess'));
    }
  }, []);

  const handleEnableOnClick = () => {
    window.location = `${user2faData?.loginUrl}/authentication/choose-method`;
  };

  const handleDisableOnClick = () => {
    setShowDisable2faModal(true);
  };

  const handleInputChange = e => {
    const { name, value } = e.target;
    setState({
      [name]: value,
      errors: { ...state.errors, [name]: '' },
    });
  };

  const changePassword = e => {
    e.preventDefault();
    const { oldPassword, newPassword } = state;
    sendPassword({ oldPassword, newPassword });
    setShowPasswordModal(false);
  };

  const handleSaveDisable2fa = e => {
    e.preventDefault();
    doDelete();
    setStatus2fa(false);
    setState({
      authType: '',
      phoneNumber: '',
      userId: '',
    });
    setShowDisable2faModal(false);
  };

  const validateOnBlur = event => {
    const { value, name } = event.target;
    let error;
    if (!value.length) {
      error = 'required';
    }
    setState({ errors: { ...state.errors, [name]: error } });
  };

  const renderPasswordModal = () => {
    return (
      <Modal
        width={600}
        open={showPasswordModal}
        title={t('profile.changePassword')}
        onRequestClose={() => setShowPasswordModal(false)}
      >
        <h3>{t('profile.changePassword')}</h3>
        <form onSubmit={changePassword}>
          <Field validationState={state.errors.oldPassword ? 'error' : null}>
            <FieldLabel htmlFor="current-password">
              {t('profile.oldPassword')}
            </FieldLabel>
            <Text
              id="current-password"
              validationState={state.errors.oldPassword ? 'error' : null}
              value={state.oldPassword}
              type="password"
              name="oldPassword"
              onChange={handleInputChange}
              onBlur={validateOnBlur}
            />
            <FieldErrorMap code={state.errors.oldPassword}>
              {t('error.requiredField')}
            </FieldErrorMap>
          </Field>
          <PasswordValidator
            changePassword
            email={{ value: state.oldEmail }}
            password={{
              value: state.newPassword,
              error: state.errors.newPassword,
              validations: state.validations,
            }}
            handleInputChange={({ value, validations }) =>
              setState({ newPassword: value, validations })
            }
          />
          <div key="actions" style={{ float: 'right' }}>
            <Button theme="text" onClick={() => setShowPasswordModal(false)}>
              {t('standard.cancel')}
            </Button>
            <Button
              data-testid="update-password-button"
              disabled={!(state.oldPassword.length && every(state.validations))}
              spinning={passwordLoading}
              theme="primary"
              type="submit"
            >
              {t('profile.update')}
            </Button>
          </div>
        </form>
      </Modal>
    );
  };

  const renderDisable2faModal = () => {
    return (
      <Modal
        width={600}
        open={showDisable2faModal}
        title="Disable 2fa modal"
        onRequestClose={() => setShowDisable2faModal(false)}
      >
        <p>{t('authentication.disable2FAText')}</p>
        <div style={{ marginTop: '20px' }} className="modal-button-container">
          <Button
            data-testid="disable-2fa-modal-cancel"
            onClick={() => setShowDisable2faModal(false)}
            theme="text"
          >
            {t('standard.cancel')}
          </Button>
          <Button
            data-testid="disable-2fa-modal-save"
            onClick={handleSaveDisable2fa}
            theme="primary"
          >
            {t('authentication.disable2faButton')}
          </Button>
        </div>
      </Modal>
    );
  };

  const sm = 100;
  const md = 60;
  const lg = 60;

  if (user2faDataLoading) {
    return <div />;
  }

  return (
    <div className="profile">
      <h3>{t('nav.buyer_area.settings.security_settings')}</h3>
      <Grid gutter={100}>
        <Col sm={sm} md={md} lg={lg}>
          <FieldLabel htmlFor="change-password-modal">
            {t('profile.changePassword')}
          </FieldLabel>
          <p>{t('profile.passwordSubtext')}</p>
          <Button
            data-testid="change-password-button"
            type="submit"
            theme="primary"
            onClick={() => setShowPasswordModal(true)}
          >
            {t('profile.changePassword')}
          </Button>
          {renderPasswordModal()}
          <FieldLabel htmlFor="authentication-header">
            {t('authentication.header')}
          </FieldLabel>
          <span
            data-testid={`status-indicator-${
              status2fa ? 'enabled' : 'disabled'
            }`}
            style={{ marginLeft: '10px' }}
          >
            <StatusIndicator color={status2fa ? 'green' : 'gray'}>
              {status2fa ? t('userManager.enabled') : t('userManager.disabled')}
            </StatusIndicator>
          </span>
          <p>{t('authentication.infoText')}</p>
          {status2fa && (
            <table className="enabled-2fa-info">
              <tbody>
                <tr>
                  <td>
                    <div className="flex-start-row">
                      <FieldLabel htmlFor="authentication-preferredMethod">
                        {`${t('authentication.preferredMethod')}:`}
                        <>&nbsp;</>
                      </FieldLabel>
                      <span>
                        {state.authType === 'SMS'
                          ? state.phoneNumber
                          : t('authentication.authenticatorApp')}
                      </span>
                    </div>
                    <p>{t('authentication.preferredMethodText')}</p>
                  </td>
                  <td>
                    <Button
                      type="submit"
                      theme="light"
                      size="md"
                      onClick={() => {
                        window.location = `${user2faData?.loginUrl}/authentication/choose-method`;
                      }}
                    >
                      {t('standard.edit')}
                    </Button>
                  </td>
                </tr>
                <tr>
                  <td>
                    <FieldLabel htmlFor="authentication-recoveryCodes">
                      {t('authentication.recoveryCodes')}
                    </FieldLabel>
                    <p>{t('authentication.recoveryCodesText')}</p>
                  </td>
                  <td>
                    <Button
                      type="submit"
                      theme="light"
                      size="md"
                      onClick={() => {
                        window.location = `${user2faData?.loginUrl}/authentication/recovery-codes?viewOnly=true`;
                      }}
                    >
                      {t('standard.view')}
                    </Button>
                  </td>
                </tr>
              </tbody>
            </table>
          )}
          {renderDisable2faModal()}
          <Button
            type="submit"
            theme={status2fa ? 'light' : 'primary'}
            onClick={status2fa ? handleDisableOnClick : handleEnableOnClick}
          >
            {status2fa
              ? t('authentication.disable')
              : t('authentication.enable')}
          </Button>
        </Col>
      </Grid>
    </div>
  );
};

export default SecuritySettings;
