import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Checkbox,
  DateRangePicker,
  Field,
  FieldErrorMap,
  FieldLabel,
  FileUpload,
  MultiFileUpload,
  Progress,
  Radio,
  Select,
  Text,
  toast,
} from 'taulia-ui';
import { Trans, useTranslation } from 'react-i18next';
import { useHistory, useParams, useLocation } from 'react-router-dom';
import CKEditor from 'ckeditor4-react';
import dompurify from 'dompurify';
import Accordion from './Accordion';
import Preview from './Preview';
import { localizeDate } from '../../Utils/localizeUtils';
import {
  downloadSampleSupplierInviteList,
  redirectTo404,
  genericErrorToast,
} from '../../Utils/utils';
import { RequestContext, UserSettingsContext } from '../../contexts';
import CONSTANTS from '../../globals';

function AnnouncementsView({ erpClients }) {
  const history = useHistory();
  const params = useParams();
  const location = useLocation();
  const request = useContext(RequestContext);
  const { t } = useTranslation();
  const { companyName } = useContext(UserSettingsContext);
  const [acceptButtonLabel, setAcceptButtonLabel] = useState({
    value: '',
    error: null,
  });
  const [alternateRecipientEnabled, setAlternateRecipientEnabled] = useState(
    false
  );
  const [audience, setAudience] = useState();
  const [canEdit, setCanEdit] = useState(true);
  const [
    supplierNumbersAttachmentId,
    setSupplierNumbersAttachmentId,
  ] = useState('');
  const [customButtonsEnabled, setCustomButtonsEnabled] = useState(false);
  const [declineButtonLabel, setDeclineButtonLabel] = useState({
    value: '',
    error: null,
  });
  const [files, setFiles] = useState([]);
  const [forceAccept, setForceAccept] = useState(false);
  const [isEditingTitle, setIsEditingTitle] = useState(false);
  const [message, setMessage] = useState('');
  const [requiresAck, setRequiresAck] = useState(false);
  const [savedDates, setSavedDates] = useState([]);
  const [erpClientId, setErpClientId] = useState('');
  const [subject, setSubject] = useState({ value: '', error: 'error' });
  const [tempAudience, setTempAudience] = useState('suppliers');
  const [tempDates, setTempDates] = useState([]);
  const [tempSubject, setTempSubject] = useState({ value: '', error: 'error' });
  const [tempMessage, setTempMessage] = useState('');
  const [tempTitle, setTempTitle] = useState('');
  const [title, setTitle] = useState('');
  const [upload] = useState({ value: null, error: null });
  const [
    supplierNumbersAttachmentFile,
    setSupplierNumbersAttachmentFile,
  ] = useState();

  const { id } = params;
  const isNew = location?.search === '?new';

  const loadAnnouncement = () => {
    request('GET', `/api/announcement/${id}`)
      .then(res => {
        const responseAudience = res.body.targetingCriteria.allSuppliers
          ? 'suppliers'
          : 'supplierNumbers';
        setAudience(responseAudience);
        setTempAudience(responseAudience);
        if (responseAudience === 'supplierNumbers') {
          setSupplierNumbersAttachmentId(
            res.body.targetingCriteria.supplierNumbersAttachmentId
          );
        }
        setAcceptButtonLabel({
          value: res.body.acceptButtonLabel,
          error: !res.body.acceptButtonLabel?.length,
        });
        setAlternateRecipientEnabled(res.body.alternateRecipientEnabled);
        setCanEdit(res.body.status === 'DRAFT');
        setCustomButtonsEnabled(res.body.customButtonsEnabled);
        setDeclineButtonLabel({
          value: res.body.declineButtonLabel,
          error: !res.body.declineButtonLabel?.length,
        });
        setForceAccept(res.body.forceAccept);
        setMessage(res.body.message);
        setTempMessage(res.body.message);
        setRequiresAck(res.body.requiresAck);
        if (res.body.startDate && res.body.endDate) {
          setSavedDates([res.body.startDate, res.body.endDate]);
          setTempDates([res.body.startDate, res.body.endDate]);
        }
        setSubject({
          value: res.body.subject,
          error: !res.body.subject?.length,
        });
        setTempSubject({
          value: res.body.subject,
          error: !res.body.subject?.length,
        });
        setTitle(res.body.title);
        setTempTitle(res.body.title);
      })
      .catch(() => redirectTo404(history));
  };

  const loadAttachments = () => {
    request('GET', `/api/announcement/${id}/attachments`).then(res => {
      setFiles(res.body);
    });
  };

  const uploadAttachments = attachments => {
    const formData = new FormData();
    attachments.forEach(attachment => formData.append('files', attachment));
    request('POST', `/api/announcement/${id}/attachments`)
      .send(formData)
      .then(loadAttachments);
  };

  const deleteAttachment = attachment => {
    request(
      'DELETE',
      `/api/announcement/attachment/${attachment.attachmentId}`
    ).then(loadAttachments);
  };

  const downloadAttachment = attachment => {
    window.location = `/api/announcement/attachment/${attachment.attachmentId}/download`;
  };

  const saveAnnouncement = (draft = false, accordion = false) => {
    request('PUT', `/api/announcement/${id}`)
      .send({
        acceptButtonLabel: acceptButtonLabel.value,
        alternateRecipientEnabled,
        customButtonsEnabled,
        declineButtonLabel: declineButtonLabel.value,
        endDate: savedDates[1],
        forceAccept,
        message,
        requiresAck,
        startDate: savedDates[0],
        status: draft ? 'DRAFT' : 'ACTIVE_NOT_SENT',
        subject: subject.value,
        targetingCriteria: {
          allSuppliers: audience === 'suppliers',
          erpClientId,
          supplierNumbersAttachmentId:
            audience === 'supplierNumbers' ? supplierNumbersAttachmentId : null,
        },
        title,
      })
      .then(() => {
        toast.success(
          t(
            draft
              ? 'announcements.saveDraftSuccess'
              : 'announcements.publishSuccess',
            { title }
          )
        );
        if (!accordion) {
          history.push(CONSTANTS.SUPPLIERS.ANNOUNCEMENTS);
        }
      })
      .catch(genericErrorToast);
  };

  useEffect(() => {
    loadAnnouncement();
    loadAttachments();
  }, []);

  useEffect(() => {
    if (supplierNumbersAttachmentId) {
      request(
        'GET',
        `/api/announcement/attachment/${supplierNumbersAttachmentId}`
      ).then(res => {
        setSupplierNumbersAttachmentFile(res.body.fileName);
      });
    }
  }, [supplierNumbersAttachmentId]);

  const erpOptions = erpClients.map(client => {
    return { value: client.clientId, label: client.longDescription };
  });

  const getAudienceCompleteLabel = () => {
    let i18nKey = 'announcements.sendToSupplierNumbers';
    if (audience === 'suppliers') {
      i18nKey = 'announcements.sendToSuppliers';
    }
    return <Trans i18nKey={i18nKey} components={[<strong />]} />;
  };

  const onAudienceSave = () => {
    if (audience === 'suppliers' && supplierNumbersAttachmentId) {
      request(
        'DELETE',
        `/api/announcement/attachment/${supplierNumbersAttachmentId}`
      ).then(() => {
        setSupplierNumbersAttachmentId(null);
        setSupplierNumbersAttachmentFile(null);
      });
    }
    saveAnnouncement(true, true);
    setTempAudience(audience);
  };

  const onContentSave = () => {
    saveAnnouncement(true, true);
    setTempSubject(subject);
    setTempMessage(message);
  };

  const onSchedulingSave = () => {
    saveAnnouncement(true, true);
    setTempDates(savedDates);
  };

  const onTitleSave = () => {
    saveAnnouncement(true, true);
    setTempTitle(title);
    setIsEditingTitle(false);
  };

  const onTitleCancel = () => {
    setTitle(tempTitle);
    setIsEditingTitle(false);
  };

  const onCancel = label => {
    if (label === 'Audience') {
      setAudience(tempAudience);
    }
    if (label === 'Content') {
      setMessage(tempMessage);
      setSubject(tempSubject);
    }
    if (label === 'Scheduling') {
      setSavedDates(tempDates);
    }
  };

  const onCSVUpload = file => {
    setSupplierNumbersAttachmentFile(file.name);
    const data = new FormData();
    data.append('file', file);
    request('POST', `/api/announcement/${id}/targetingCriteria`)
      .send(data)
      .then(res => setSupplierNumbersAttachmentId(res.body.id));
  };

  const onRequiresAckChange = () => {
    if (requiresAck) {
      setForceAccept(false);
      setCustomButtonsEnabled(false);
    }
    setRequiresAck(!requiresAck);
  };

  const audienceSaveDisabled =
    audience === 'supplierNumbers' &&
    (!supplierNumbersAttachmentId || (erpClients.length && !erpClientId));

  const contentSaveDisabled =
    !!subject.error ||
    !message?.length ||
    (customButtonsEnabled &&
      (!acceptButtonLabel?.value?.length ||
        !declineButtonLabel?.value?.length));

  if (!tempTitle) {
    return <Progress />;
  }

  return (
    <>
      <div>
        {isEditingTitle ? (
          <>
            <Text
              data-testid="edit-name-input"
              style={{ marginBottom: '10px' }}
              onChange={e => setTitle(e.target.value)}
              value={title}
            />
            <Button
              data-testid="save-name-button"
              disabled={!title.length}
              theme="primary"
              onClick={onTitleSave}
            >
              {t('standard.save')}
            </Button>
            <Button
              data-testid="cancel-name-button"
              theme="text"
              onClick={onTitleCancel}
            >
              {t('standard.cancel')}
            </Button>
          </>
        ) : (
          <>
            <h1 style={{ marginBottom: '28px' }}>{title}</h1>
            {canEdit && (
              <Button onClick={() => setIsEditingTitle(true)} theme="text">
                {t('announcements.editName')}
              </Button>
            )}
          </>
        )}
      </div>
      <Accordion
        canEdit={canEdit}
        onSubmit={saveAnnouncement}
        onCancel={onCancel}
      >
        <section
          buttonLabel={t('announcements.editAudience')}
          completeLabel={getAudienceCompleteLabel()}
          isComplete={!!audience}
          isOpen={isNew}
          isSaveDisabled={!!audienceSaveDisabled}
          label={t('announcements.audience')}
          onSave={onAudienceSave}
        >
          <section className="audience-card-container">
            <article className="audience-card">
              <Radio
                aria-label="suppliers"
                checked={audience === 'suppliers'}
                disabled={!canEdit}
                onChange={event => setAudience(event.target.value)}
                value="suppliers"
              >
                <p className="title">{t('announcements.allSuppliers')}</p>
                <p>{t('announcements.publishAnnouncement')}</p>
              </Radio>
            </article>
            <article className="audience-card">
              <Radio
                aria-label="supplierNumbers"
                checked={audience === 'supplierNumbers'}
                disabled={!canEdit}
                onChange={event => setAudience(event.target.value)}
                value="supplierNumbers"
              >
                <p className="title">
                  {t('announcements.listSupplierNumbers')}
                </p>
                <p>{t('announcements.uploadList')}</p>
              </Radio>
            </article>
          </section>
          {audience === 'supplierNumbers' && (
            <>
              {erpClients.length > 1 && (
                <div style={{ marginBottom: '10px' }}>
                  <FieldLabel htmlFor="erp-select">
                    {t('supplierSegment.segmentAddSupplierForm.erpSystem')}
                  </FieldLabel>
                  <Select
                    disabled={!canEdit}
                    id="erp-select"
                    multiple={false}
                    noSearchResultsLabel={t(
                      'bulkInvites.searchPage.noSearchResultsLabel'
                    )}
                    onChange={erp => setErpClientId(erp)}
                    options={erpOptions}
                    placeholder={t(
                      'supplierSegment.segmentAddSupplierForm.selectErpSystem'
                    )}
                    searchPlaceholder={t(
                      'bulkInvites.searchPage.searchPlaceholder'
                    )}
                    value={erpClientId}
                  />
                </div>
              )}
              <Field validationState={upload.error ? 'error' : null}>
                <FieldLabel htmlFor="file-upload">
                  {t('supplierSegment.segmentAddSupplierForm.selectCsvFile')}
                </FieldLabel>
                <FileUpload
                  acceptedFileTypes={['.csv']}
                  disabled={!canEdit}
                  file={{ name: supplierNumbersAttachmentFile }}
                  onChange={onCSVUpload}
                />
                <FieldErrorMap code={upload.error}>
                  <span key="invalidType">
                    {t(
                      'bulkInvites.individualPage.options.upload.error.invalidType'
                    )}
                  </span>
                  {t(
                    'bulkInvites.individualPage.options.upload.error.noContent'
                  )}
                </FieldErrorMap>
                <p className="padding">
                  <Trans
                    values={{
                      hereLink: t(
                        'bulkInvites.individualPage.options.upload.infoLink'
                      ),
                    }}
                    i18nKey="bulkInvites.individualPage.options.upload.info"
                    components={[
                      <Button
                        style={{ display: 'inline-block' }}
                        theme="text"
                        onClick={downloadSampleSupplierInviteList}
                      />,
                    ]}
                  />
                </p>
              </Field>
            </>
          )}
        </section>
        <section
          buttonLabel={t('announcements.editContent')}
          completeLabel={subject.value}
          label={t('announcements.content')}
          isComplete={!!subject.value}
          isSaveDisabled={contentSaveDisabled}
          onSave={onContentSave}
        >
          <Field validationState={subject.error ? 'error' : null}>
            <FieldLabel htmlFor="subject">
              {t('announcements.subject')}
            </FieldLabel>
            <Text
              disabled={!canEdit}
              id="subject"
              name="subject"
              onChange={e =>
                setSubject({
                  value: e.target.value,
                  error: !e.target.value.length,
                })
              }
              validationState={subject.error ? 'error' : null}
              value={subject.value}
            />
            <FieldErrorMap code="error">
              {t('error.requiredField')}
            </FieldErrorMap>
          </Field>
          {canEdit ? (
            <>
              <CKEditor
                data={message}
                onChange={e => setMessage(e.editor.getData())}
              />
              <MultiFileUpload
                buttonText={t('announcements.selectFile')}
                files={files}
                onAdd={uploadAttachments}
                onClick={downloadAttachment}
                onDelete={deleteAttachment}
                pText={t('announcements.dragDrop')}
              />
            </>
          ) : (
            <div
              // eslint-disable-next-line react/no-danger
              dangerouslySetInnerHTML={{
                __html: dompurify.sanitize(message),
              }}
              style={{ marginBottom: '10px' }}
            />
          )}
          <Checkbox
            checked={requiresAck}
            disabled={!canEdit}
            data-testid="requires-ack-checkbox"
            onChange={onRequiresAckChange}
          >
            {t('announcements.requiresAck')}
          </Checkbox>
          <Checkbox
            disabled={!requiresAck || !canEdit}
            checked={forceAccept}
            onChange={() => setForceAccept(!forceAccept)}
          >
            {t('announcements.forceAccept')}
          </Checkbox>
          <Checkbox
            disabled={!requiresAck || !canEdit}
            checked={customButtonsEnabled}
            data-testid="customize-button-checkbox"
            onChange={() => setCustomButtonsEnabled(!customButtonsEnabled)}
          >
            {t('announcements.customButtonText')}
          </Checkbox>
          {customButtonsEnabled && (
            <div className="field-container">
              <Field validationState={acceptButtonLabel.error ? 'error' : null}>
                <FieldLabel htmlFor="primary-action">
                  {t('announcements.acceptButtonLabel')}
                </FieldLabel>
                <Text
                  disabled={!canEdit}
                  id="primary-action"
                  name="primary-action"
                  onChange={e =>
                    setAcceptButtonLabel({
                      value: e.target.value,
                      error: !e.target.value.length,
                    })
                  }
                  placeholder={t('announcements.acceptButtonLabel')}
                  validationState={acceptButtonLabel.error ? 'error' : null}
                  value={acceptButtonLabel.value}
                />
                <FieldErrorMap code="error">
                  {t('error.requiredField')}
                </FieldErrorMap>
              </Field>
              <Field
                validationState={declineButtonLabel.error ? 'error' : null}
              >
                <FieldLabel htmlFor="secondary-action">
                  {t('announcements.declineButtonLabel')}
                </FieldLabel>
                <Text
                  disabled={!canEdit}
                  id="secondary-action"
                  name="secondary-action"
                  onChange={e =>
                    setDeclineButtonLabel({
                      value: e.target.value,
                      error: !e.target.value.length,
                    })
                  }
                  placeholder={t('announcements.declineButtonLabel')}
                  validationState={declineButtonLabel.error ? 'error' : null}
                  value={declineButtonLabel.value}
                />
                <FieldErrorMap code="error">
                  {t('error.requiredField')}
                </FieldErrorMap>
              </Field>
            </div>
          )}
          <Checkbox
            checked={alternateRecipientEnabled}
            disabled={!canEdit}
            onChange={() =>
              setAlternateRecipientEnabled(!alternateRecipientEnabled)
            }
          >
            {t('announcements.alternateRecipientEnabled')}
          </Checkbox>
        </section>
        <section
          buttonLabel={t('announcements.editScheduling')}
          completeLabel={[
            <span key="scheduling">
              <Trans
                values={{
                  date1: localizeDate(savedDates[0]),
                  date2: localizeDate(savedDates[1]),
                }}
                i18nKey="announcements.displayFromTo"
                components={[<strong />]}
              />
            </span>,
          ]}
          isComplete={!!savedDates.length}
          isSaveDisabled={!savedDates.length}
          label={t('announcements.scheduling')}
          onSave={onSchedulingSave}
        >
          <DateRangePicker
            key={savedDates}
            disabled={!canEdit}
            endDate={savedDates[1]}
            endLabel={t('announcements.displayUntil')}
            onChangeDate={dates => setSavedDates(dates)}
            startDate={savedDates[0]}
            startLabel={t('announcements.displayFrom')}
          />
        </section>
      </Accordion>
      {audience && subject.value && savedDates.length ? (
        <Preview
          announcement={{
            acceptButtonLabel: acceptButtonLabel.value,
            alternateRecipientEnabled,
            declineButtonLabel: declineButtonLabel.value,
            files,
            message,
            requiresAck,
            subject: subject.value,
          }}
          companyName={companyName}
        />
      ) : (
        ''
      )}
    </>
  );
}

AnnouncementsView.defaultProps = {
  erpClients: [],
};

AnnouncementsView.propTypes = {
  erpClients: PropTypes.arrayOf(PropTypes.shape()),
};

export default AnnouncementsView;
