import { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useFetchContext } from './useFetch';

const useFeatureFetch = featureName => {
  const { data, loading, error } = useFetchContext(
    `/api/setting/feature-enabled?featureName=${featureName}`
  );
  const [isEnabled, setIsEnabled] = useState(false);

  useEffect(() => {
    setIsEnabled(data?.isEnabled);
  }, [data]);

  return { loading, isEnabled, error };
};

useFeatureFetch.propTypes = {
  featureName: PropTypes.string.isRequired,
};

export default useFeatureFetch;
