import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Link, Modal, Radio, toast } from 'taulia-ui';
import { Trans, useTranslation } from 'react-i18next';
import { genericErrorToast } from '../../../Utils/utils';
import { RequestContext } from '../../../contexts';

function RoleModal({ open, onRequestClose, roles, user }) {
  const { t } = useTranslation();
  const request = useContext(RequestContext);
  const [option, setOption] = useState('fullAccess');
  const { email, id } = user;

  useEffect(() => {
    setOption(user.role);
  }, [user]);

  const changeRole = () => {
    request('PUT', `/api/user-manager/user/${id}`)
      .send({ role: option })
      .then(() => {
        toast.success(
          t('userManager.toast.changeRoleSuccess', {
            email: user.email,
            role: t(`userManager.roles.${option}`),
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
    onRequestClose();
  };

  return (
    <Modal width={800} open={open} onRequestClose={onRequestClose}>
      <strong>{t('userManager.editRoleFor', { email })}</strong>
      <hr className="modal-divider" />
      <section>
        <p style={{ marginBottom: '10px' }}>
          <Trans
            i18nKey="userManager.roleDesc"
            components={[
              <Link to="https://customersupport.taulia.com/articles/en_US/Agent_Knowledge_Base/Q-How-can-I-add-or-remove-users-in-the-Buyer-UI-000008765/" />,
            ]}
          />
        </p>
        {roles.map(role => (
          <Radio
            aria-label={role}
            checked={option === role}
            key={role}
            onChange={event => setOption(event.target.value)}
            value={role}
          >
            <strong>{t(`userManager.roles.${role}`)}</strong>
            <p style={{ marginBottom: '10px' }}>
              {t(`userManager.roles.${role}Desc`)}
            </p>
          </Radio>
        ))}
      </section>
      <div style={{ marginTop: '20px', float: 'right' }}>
        <Button onClick={changeRole} theme="primary">
          {t('userManager.changeRole')}
        </Button>
      </div>
    </Modal>
  );
}

RoleModal.propTypes = {
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  roles: PropTypes.arrayOf(PropTypes.string),
  user: PropTypes.shape({
    email: PropTypes.string,
    id: PropTypes.string,
    role: PropTypes.string,
  }),
};

RoleModal.defaultProps = {
  open: true,
  roles: ['BUYER_ADMIN', 'BUYER_SEARCH_ONLY'],
  user: { email: '', id: '', role: '' },
};

export default RoleModal;
