import React from 'react';
import PropTypes from 'prop-types';
import { Progress } from 'taulia-ui';
import { useFeatureFetch } from '../hooks';
import { GenericErrorMessage } from './GenericErrorMessage';

const FeatureToggledComponent = ({
  featureName,
  enabledComponent,
  disabledComponent,
}) => {
  const featureFetch = useFeatureFetch(featureName);

  if (featureFetch.loading) {
    return <Progress />;
  }

  if (featureFetch.error) {
    return <GenericErrorMessage />;
  }

  if (featureFetch.isEnabled) {
    return enabledComponent;
  }

  if (disabledComponent) {
    return disabledComponent;
  }

  return <></>;
};

FeatureToggledComponent.propTypes = {
  featureName: PropTypes.string.isRequired,
  enabledComponent: PropTypes.element.isRequired,
  disabledComponent: PropTypes.element,
};

FeatureToggledComponent.defaultProps = {
  disabledComponent: null,
};

export default FeatureToggledComponent;
