import React from 'react';
import { EnhancedSearch, DateTimeCell, StatusIndicator } from 'taulia-ui';
import { useTranslation } from 'react-i18next';

const ConnectionDetails = () => {
  const { t } = useTranslation();

  const StatusColorCell = cell => {
    const { value } = cell;
    const statusColorMap = {
      New: 'yellow',
      Rejected: 'red',
      Submitted: 'green',
    };
    return (
      <span title={value}>
        <StatusIndicator color={statusColorMap[value]}>{value}</StatusIndicator>
      </span>
    );
  };

  const enhancedSearchProps = {
    uri: '/eui/ticket',
    defaultQuery: {
      select: ['document', 'documentType', 'dateCreated', 'status', 'message'],
      filters: [
        { field: 'document', values: [] },
        { field: 'documentType', operator: 'EQUAL', values: [] },
        { field: 'status', operator: 'EQUAL', values: [] },
        { field: 'dateCreated', values: [] },
      ],
    },
    fieldOverrides: {
      status: { column: { cell: StatusColorCell } },
      lastUpdated: { column: { cell: DateTimeCell, width: 180 } },
      dateCreated: { column: { cell: DateTimeCell, width: 180 } },
      id: { column: { width: 280 } },
      message: { column: { width: 300 } },
      document: { column: { fixedOrder: 1, width: 150 } },
    },
    excludedFields: ['buyerId'],
    tooltips: [
      'batchId',
      'clientId',
      'document',
      'id',
      'message',
      'object',
      'referenceId',
      'status',
    ],
  };

  return (
    <>
      <h3>{t('nav.buyer_area.settings.connection_details')}</h3>
      <EnhancedSearch {...enhancedSearchProps} />
    </>
  );
};

export default ConnectionDetails;
