import React from 'react';
import PropTypes from 'prop-types';
import { Button, FieldList, Modal } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { localizeDate } from '../../../Utils/localizeUtils';

function RoleModal({
  onRequestClose,
  open,
  setShowPermissionsModal,
  setShowRoleModal,
  setShowViewUserModal,
  statusToActionMap,
  statusIndicator,
  user,
}) {
  const { t } = useTranslation();

  const userRoleComponent = (
    <>
      {t(`userManager.roles.${user.role}`)}
      &nbsp;&#40;
      <Button
        data-testid="edit-role"
        onClick={() => {
          setShowViewUserModal(false);
          setShowRoleModal(true);
        }}
        theme="text"
      >
        {t('standard.edit')}
      </Button>
      &#41;
    </>
  );

  const businessUnitsComponent = (
    <>
      {user.hasRestrictionsPerUser
        ? `${user.userCompanyCodeAssignedCount} /
    ${user.numberOfAvailableCompanyCodes}`
        : t('userManager.noRestriction')}
      &nbsp;&#40;
      <Button
        data-testid="edit-permissions"
        onClick={() => {
          setShowViewUserModal(false);
          setShowPermissionsModal(true);
        }}
        theme="text"
      >
        {t('standard.edit')}
      </Button>
      &#41;
    </>
  );

  const fieldListData = {
    colOneData: [
      {
        dataTestId: 'user-manager-status',
        label: t('fields.status'),
        value: statusIndicator(user.status),
      },
      {
        dataTestId: 'user-manager-locale',
        label: t('supplierView.locale'),
        value: user.locale,
      },
      {
        dataTestId: 'user-manager-role',
        label: t('supplierView.role'),
        value: userRoleComponent,
      },
    ],
    colTwoData: [
      {
        dataTestId: 'user-manager-dateInvited',
        label: t('fields.dateInvited'),
        value: localizeDate(user.dateInvited),
      },
      {
        dataTestId: 'user-manager-lastLogin',
        label: t('fields.lastLogin'),
        value: localizeDate(user.lastLogin),
      },
      {
        dataTestId: 'user-manager-businessUnitAccess',
        label: t('userManager.businessUnitAccess'),
        value: businessUnitsComponent,
      },
    ],
    colGapWidth: 100,
    colOneWidth: 180,
    colTwoWidth: 180,
  };

  return (
    <Modal width={1100} open={open} onRequestClose={onRequestClose}>
      <h3>{user.name}</h3>
      <span className="subtext">{user.email}</span>
      <hr className="modal-divider" />
      <FieldList {...fieldListData} />
      {user.status && (
        <div style={{ marginTop: '20px', float: 'right' }}>
          {statusToActionMap[user.status].map(action => (
            <Button key={action.trans} onClick={() => action.func(user.id)}>
              {t(action.trans)}
            </Button>
          ))}
        </div>
      )}
    </Modal>
  );
}

RoleModal.propTypes = {
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  setShowPermissionsModal: PropTypes.func.isRequired,
  setShowRoleModal: PropTypes.func.isRequired,
  setShowViewUserModal: PropTypes.func,
  statusToActionMap: PropTypes.shape().isRequired,
  statusIndicator: PropTypes.func,
  user: PropTypes.shape({
    dateInvited: PropTypes.string,
    email: PropTypes.string,
    hasRestrictionsPerUser: PropTypes.bool,
    id: PropTypes.string,
    lastLogin: PropTypes.string,
    locale: PropTypes.string,
    name: PropTypes.string,
    numberOfAvailableCompanyCodes: PropTypes.number,
    role: PropTypes.string,
    status: PropTypes.string,
    userCompanyCodeAssignedCount: PropTypes.number,
  }).isRequired,
};

RoleModal.defaultProps = {
  setShowViewUserModal: () => null,
  statusIndicator: () => null,
  open: true,
};

export default RoleModal;
