import React, { useContext, useEffect, useState } from 'react';
import { Route, useLocation, useHistory, useParams } from 'react-router-dom';
import { Button, Progress, Tooltip } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import CONSTANTS from '../../globals';
import DeletedSegmentList from './DeletedSegmentList';
import SegmentList from './SegmentList';
import AddSupplierForm from './AddSupplierForm';
import { RequestContext } from '../../contexts';

const SegmentContainer = props => {
  const location = useLocation();
  const params = useParams();
  const history = useHistory();
  const { t } = useTranslation();
  const request = useContext(RequestContext);

  const [segments, setSegments] = useState([]);
  const [deletedSegments, setDeletedSegments] = useState([]);
  const [alerts, setAlerts] = useState([]);
  const [loading, setLoading] = useState(false);

  const getSegments = () => {
    request('GET', '/api/segments?includeSupplierCounts=true')
      .then(res => {
        setSegments(res.body.segments);
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
        setAlerts([
          {
            theme: 'danger',
            text: t('supplierSegment.alert.errorLoadingSegments'),
          },
        ]);
      });
  };

  const getDeletedArchivedSegments = archivedSegments => {
    return archivedSegments.map(archivedSegment => {
      const { segment } = archivedSegment;
      segment.dateArchived = archivedSegment.dateArchived;
      return segment;
    });
  };

  const getDeletedSegments = () =>
    request('GET', '/api/archivedSegments')
      .then(res => {
        setDeletedSegments(
          getDeletedArchivedSegments(res.body.archivedSegments)
        );
        setLoading(false);
      })
      .catch(() => {
        setLoading(false);
        setAlerts([
          {
            theme: 'danger',
            text: t('supplierSegment.alert.errorLoadingSegments'),
          },
        ]);
      });

  useEffect(() => {
    setLoading(true);
    if (
      location.pathname === CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST_DELETED
    ) {
      getDeletedSegments();
    } else {
      getSegments();
    }
  }, [location]);

  const setAlertsViewSegments = segmentAlerts => {
    setAlerts(segmentAlerts);
    history.push(CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST);
  };

  const clearAlerts = () => {
    setAlerts([]);
  };

  const viewSegments = () => {
    if (!segments.length) {
      getSegments();
    }
    history.push(CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST);
  };

  const viewDeletedSegments = () => {
    getDeletedSegments();
    history.push(CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST_DELETED);
  };

  const addSupplierResultsCallback = resultsData => {
    const { associationSummary, supplierSegment } = resultsData;

    const segment = segments.filter(
      item => item.id === supplierSegment.segmentId
    )[0];

    const segmentAlerts = [];
    const supplierCount = associationSummary.countNewAssociations;

    if (associationSummary.countNewAssociations > 0) {
      segmentAlerts.push({
        theme: 'success',
        text: t('supplierSegment.alert.suppliersAddedToSegment', {
          supplierCount,
          segmentName: segment.name,
        }),
      });
    }

    if (associationSummary.countExistingAssociations > 0) {
      segmentAlerts.push({
        theme: 'warn',
        text: t('supplierSegment.alert.suppliersAlreadyInSegment', {
          supplierCount,
          segmentName: segment.name,
        }),
      });
    }

    if (associationSummary.suppliersMissingDetails.length > 0) {
      const comp =
        associationSummary.suppliersMissingDetails.length > 1 ? (
          <div>
            <Tooltip theme="light" align="left">
              <Button theme="text">
                {t('supplierSegment.supplierSegmentList.viewNotFoundSuppliers')}
              </Button>
              <div>{associationSummary.suppliersMissingDetails.join(', ')}</div>
            </Tooltip>
          </div>
        ) : (
          undefined
        );

      const text = t('supplierSegment.alert.suppliersNotFound', {
        supplierCount,
        segmentName: segment.name,
      });

      segmentAlerts.push({
        theme: 'danger',
        text,
        comp,
      });
    }

    setAlertsViewSegments(segmentAlerts);
  };

  if (loading) {
    return <Progress />;
  }

  const childProps = {
    ...props,
    alerts,
    deletedSegments,
    segments,
    addSupplierResultsCallback,
    clearAlerts,
    loadSegments: getSegments,
    viewDeletedSegments,
    viewSegments,
  };

  return (
    <>
      <Route
        path={CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.ADD_SUPPLIERS}
        render={() => {
          return <AddSupplierForm params={params} {...childProps} />;
        }}
      />
      <Route
        path={CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST_DELETED}
        render={() => {
          return <DeletedSegmentList {...childProps} />;
        }}
      />
      <Route
        exact
        path={CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST}
        render={routeProps => {
          return <SegmentList {...routeProps} {...childProps} />;
        }}
      />
    </>
  );
};

export default SegmentContainer;
