/* eslint-disable no-unused-expressions  */

import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Modal, Radio, toast, TreeSelect } from 'taulia-ui';
import { useTranslation } from 'react-i18next';

import {
  fullAccessBusinessUnits,
  generateTreeSelectFormat,
  getSelectedBusinessUnits,
} from '../../../Utils/UserManagerUtils';
import { genericErrorToast } from '../../../Utils/utils';
import { RequestContext } from '../../../contexts';

function PermissionsModal({ businessUnits, open, onRequestClose, user }) {
  const { t } = useTranslation();
  const request = useContext(RequestContext);
  const [option, setOption] = useState('');
  const [currentRestrictions, setCurrentRestrictions] = useState({});
  const [restrictions, setRestrictions] = useState([]);
  const { email, id } = user;
  const treeSelectFormatBusinessUnits = generateTreeSelectFormat(
    businessUnits,
    currentRestrictions.hasRestrictions,
    currentRestrictions.businessUnitIds
  );
  const allAvailableBusinessUnitIds = fullAccessBusinessUnits(businessUnits);

  const getServerRestrictions = () => {
    request('GET', `/api/user-manager/user/${id}/permissions`)
      .then(res => {
        res.body.hasRestrictions &&
        res.body.businessUnitIds.length !== allAvailableBusinessUnitIds.length
          ? setOption('applyRestrictions')
          : setOption('fullAccess');
        return setCurrentRestrictions(res.body);
      })
      .catch(genericErrorToast);
  };

  useEffect(() => {
    if (id) {
      getServerRestrictions();
    }
  }, [user]);

  const savePermissions = () => {
    request('PUT', `/api/user-manager/user/${id}/permissions`)
      .send(
        option === 'fullAccess' ||
          restrictions.length === allAvailableBusinessUnitIds.length
          ? {
              hasRestrictions: false,
            }
          : { hasRestrictions: true, businessUnitIds: restrictions }
      )
      .then(() => {
        toast.success(
          t('userManager.toast.updateBusinessUnitsSuccess', {
            email: user.email,
          })
        );
      })
      .catch(() => {
        genericErrorToast();
      });
    onRequestClose();
  };

  return (
    <Modal width={800} open={open} onRequestClose={onRequestClose}>
      <strong>{t('userManager.editBusinessUnitFor', { email })}</strong>
      <hr className="modal-divider" />
      <Radio
        aria-label="fullAccess"
        checked={option === 'fullAccess'}
        onChange={event => setOption(event.target.value)}
        value="fullAccess"
      >
        <strong>{t('userManager.fullAccess')}</strong>
        <p style={{ marginBottom: '10px' }}>
          {t('userManager.fullAccessDesc')}
        </p>
      </Radio>
      <Radio
        aria-label="applyRestrictions"
        checked={option === 'applyRestrictions'}
        onChange={event => setOption(event.target.value)}
        value="applyRestrictions"
      >
        <strong>{t('userManager.applyRestrictions')}</strong>
        <p style={{ marginBottom: '10px' }}>
          {t('userManager.applyRestrictionsDesc')}
        </p>
      </Radio>
      {option === 'applyRestrictions' && (
        <TreeSelect
          id="business-unit-ids"
          name="businessUnitIds"
          data={treeSelectFormatBusinessUnits}
          onSelectionChange={selectedNodes =>
            setRestrictions(
              getSelectedBusinessUnits(
                selectedNodes,
                treeSelectFormatBusinessUnits
              )
            )
          }
        />
      )}
      <div style={{ marginTop: '20px', float: 'right' }}>
        <Button onClick={savePermissions} theme="primary">
          {t('userManager.savePermissions')}
        </Button>
      </div>
    </Modal>
  );
}

PermissionsModal.propTypes = {
  businessUnits: PropTypes.arrayOf(
    PropTypes.shape({
      availableBusinessUnits: PropTypes.arrayOf(
        PropTypes.shape({
          address: PropTypes.shape({
            city: PropTypes.string,
            county: PropTypes.string,
            name: PropTypes.string,
          }),
          buyerId: PropTypes.string,
          id: PropTypes.string,
          name: PropTypes.string,
          number: PropTypes.string,
        })
      ),
      companyId: PropTypes.string,
      companyName: PropTypes.string,
    })
  ),
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  user: PropTypes.shape({
    email: PropTypes.string,
    id: PropTypes.string,
  }),
};

PermissionsModal.defaultProps = {
  businessUnits: [],
  open: true,
  user: { email: '', id: '' },
};

export default PermissionsModal;
