/* eslint-disable no-param-reassign */
import React from 'react';
import qs from 'qs';
import { isEmpty } from 'lodash';
import { toast } from 'taulia-ui';
import { Trans } from 'react-i18next';
import CONSTANTS from '../globals';

export function parseStringToArray(value) {
  if (isEmpty(value)) {
    return [];
  }
  if (Array.isArray(value)) {
    value = value.join(',');
  }
  return value
    .trim()
    .replace(/\n|\s+/g, ',')
    .replace(/,+/g, ',')
    .split(',');
}

export function parseInput(value) {
  // Used to parse the text in the textareas in invites and announcements to return an array from string of ids
  let valueCopy = value;
  if (isEmpty(value)) {
    return [];
  }
  if (Array.isArray(value)) {
    valueCopy = value.join(',');
  }
  return valueCopy
    .trim()
    .replace(/\n|\s+/g, ',')
    .replace(/,+/g, ',')
    .split(',');
}

export function constructSearchQueryUrl(field, values, operator = 'EQUAL') {
  const query = {
    ...CONSTANTS.DEFAULT_QUERY,
    filters: [
      ...CONSTANTS.DEFAULT_QUERY.filters.filter(el => {
        return el.field !== field;
      }),
      { field, operator, values },
    ],
  };
  return `${CONSTANTS.SUPPLIERS.SEARCH_BETA}?${qs.stringify(query, {
    encode: true,
  })}`;
}

export function genericErrorToast() {
  return toast.error(
    <Trans
      i18nKey="app.error"
      components={[
        <a
          href="http://support.taulia.com"
          target="_blank"
          rel="noopener noreferrer"
        />,
      ]}
    />
  );
}

export function redirectTo404(history) {
  history.push(CONSTANTS.NOT_FOUND);
}

export function downloadBlob(blob, fileName) {
  if (window.navigator.msSaveOrOpenBlob) {
    window.navigator.msSaveOrOpenBlob(blob, fileName);
  } else {
    const downloadLink = document.createElement('a');
    downloadLink.href = URL.createObjectURL(blob);
    downloadLink.download = fileName;

    //  Add to the page, click it, and remove from the dom
    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);
  }
}

export function downloadSampleSupplierInviteList() {
  const sampleSupplierInviteList = `Supplier Number\n\n00001\n00002\n00003`;
  const resultsToDownload = new Blob([sampleSupplierInviteList], {
    type: 'text/csv;charset=utf-8',
  });
  downloadBlob(resultsToDownload, 'sample-supplier-invite-list.csv');
}
