import React, { useContext, useEffect, useReducer, useState } from 'react';
import PropTypes from 'prop-types';
import {
  Button,
  Col,
  Field,
  FieldErrorMap,
  FieldLabel,
  Grid,
  InfoTooltip,
  LocaleSelect,
  Localization,
  Modal,
  PasswordValidator,
  PhoneNumber,
  Select,
  setProfileCookie,
  Text,
  toast,
} from 'taulia-ui';
import { every } from 'lodash';
import validate from 'validate.js';
import { useTranslation } from 'react-i18next';
import { RequestContext, UserSettingsContext } from '../../contexts';

const Profile = () => {
  const { i18n, t } = useTranslation();
  const { email } = useContext(UserSettingsContext);
  const request = useContext(RequestContext);

  const INITIAL_STATE = {
    currencyCode: '',
    defaultCurrency: '',
    firstName: '',
    lastName: '',
    locale: i18n.language,
    newEmail: '',
    newPassword: '',
    oldEmail: '',
    oldPassword: '',
    workPhone: '',
    workTitle: '',
    errors: {},
    validations: {
      length: false,
      upper: false,
      lower: false,
      number: false,
      match: false,
    },
  };

  const reducer = (prevState, updatedProperty) => ({
    ...prevState,
    ...updatedProperty,
  });

  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [showEmailModal, setShowEmailModal] = useState(false);
  const [loading, setLoading] = useState(false);

  const [state, setState] = useReducer(reducer, INITIAL_STATE);

  const handleInputChange = e => {
    const { name, value } = e.target;
    setState({
      [name]: value,
      errors: { ...state.errors, [name]: '' },
    });
  };

  const getUserProfile = () => {
    request('GET', '/api/user-profile')
      .then(response => {
        const {
          defaultCurrency,
          firstName,
          lastName,
          locale,
          useCurrencyCode,
          workPhone,
          workTitle,
        } = response.body;
        setState({
          currencyCode: useCurrencyCode ? 'code' : 'symbol',
          defaultCurrency,
          firstName,
          lastName,
          locale,
          oldEmail: email,
          workPhone,
          workTitle,
        });
      })
      .catch(err => console.error(err)); // eslint-disable-line no-console
  };

  useEffect(() => {
    setProfileCookie(i18n.language);
    getUserProfile();
  }, []);

  useEffect(() => {
    // Clear modals on exit
    setState({
      oldPassword: '',
      newPassword: '',
      newEmail: '',
    });
  }, [showEmailModal, showPasswordModal]);

  const translateError = errorCode => {
    const errorMap = {
      POST_PROCESSING_FAILED: 'profile.errors.toast.generic',
      USER_ALREADY_EXISTS: 'profile.errors.toast.userExists',
      USER_ALREADY_EXISTS_FOR_DIFFERENT_COMPANY:
        'profile.errors.toast.userExists',
      USER_WITH_SAME_EMAIL: 'profile.errors.toast.userExists',
      EMAIL_INVALID_FORMAT: 'profile.errors.email.invalid',
      COMPANY_NOT_FOUND: 'profile.errors.toast.generic',
      INCORRECT_PASSWORD: 'profile.errors.toast.incorrectPassword',
      OLD_PASSWORD_MISMATCH: 'profile.errors.toast.passwordMismatch',
      PASSWORD_VALIDATION_FAILS: 'profile.errors.toast.invalidPassword',
    };
    const error = errorMap[errorCode] || 'profile.errors.toast.generic';
    return t(error);
  };

  const updateUserProfile = e => {
    e.preventDefault();
    setLoading(true);

    const {
      currencyCode,
      defaultCurrency,
      firstName,
      lastName,
      locale,
      workPhone,
      workTitle,
    } = state;

    request('PUT', '/api/user-profile')
      .send({
        defaultCurrency,
        firstName,
        lastName,
        locale,
        workPhone,
        workTitle,
        useCurrencyCode: currencyCode === 'code',
      })
      .then(() => {
        i18n.changeLanguage(locale);
        Localization.setLocale(locale);
        toast.success(t('profile.successToast'));
      })
      .catch(err =>
        toast.error(translateError(err?.response?.body?.failureReason))
      )
      .finally(() => {
        setLoading(false);
      });
  };

  const changePassword = e => {
    setLoading(true);
    e.preventDefault();
    const { oldPassword, newPassword } = state;
    request('PUT', '/api/password')
      .send({ oldPassword, newPassword })
      .then(() => {
        setShowPasswordModal(false);
        toast.success(t('profile.successToast'));
      })
      .catch(err =>
        toast.error(translateError(err?.response?.body?.failureReason))
      )
      .finally(() => {
        setLoading(false);
      });
  };

  const changeEmail = e => {
    const { newEmail, oldPassword } = state;
    setLoading(true);
    e.preventDefault();
    request('PUT', '/api/email')
      .send({
        newEmail,
        password: oldPassword,
      })
      .then(() => {
        setShowEmailModal(false);
        setState({ oldEmail: newEmail });
        toast.success(t('profile.successToast'));
      })
      .catch(err =>
        toast.error(translateError(err?.response?.body?.failureReason))
      )
      .finally(() => {
        setLoading(false);
      });
  };

  const validateOnBlur = event => {
    const { value, name } = event.target;
    const { newEmail } = state;
    let error;
    if (name === 'newEmail') {
      error = validate({ email: newEmail }, { email: { email: true } })
        ? 'invalid'
        : null;
      if (!error) {
        request('GET', `/api/email/${newEmail}`).then(response => {
          if (response.body.exists) {
            setState({ errors: { ...state.errors, [name]: 'inUse' } });
          }
        });
      }
    }
    if (!value.length) {
      error = 'required';
    }
    setState({ errors: { ...state.errors, [name]: error } });
  };

  const currencyCodeOptions = [
    {
      value: 'code',
      label: t('profile.currencyCode.options.code'),
    },
    {
      value: 'symbol',
      label: t('profile.currencyCode.options.symbol'),
    },
  ];

  const currencyDisplayOptions = [
    'AUD',
    'CAD',
    'EUR',
    'GBP',
    'local',
    'USD',
  ].map(value => ({
    value,
    label: t(`profile.currencyDisplay.options.${value}`),
  }));

  const validatePhone = () => {
    const { workPhone } = state;
    const valid = PhoneNumber.isValid(workPhone);
    setState({
      value: workPhone,
      errors: { ...state.errors, workPhone: valid ? null : 'invalid' },
    });
  };

  const renderEmailModal = () => {
    const { newEmail, oldEmail, oldPassword } = state;
    return (
      <Modal
        width={600}
        open={showEmailModal}
        title="Email modal"
        onRequestClose={() => setShowEmailModal(false)}
      >
        <h3>{t('profile.changeEmail')}</h3>
        <p style={{ paddingBottom: '10px' }}>
          {t('profile.emailSubtext')}
          <strong>
            &nbsp;
            {oldEmail}
          </strong>
          .
        </p>
        <form onSubmit={changeEmail}>
          <Field validationState={state.errors.oldPassword ? 'error' : null}>
            <FieldLabel htmlFor="current-password">
              {t('profile.oldPassword')}
            </FieldLabel>
            <Text
              id="current-password"
              validationState={state.errors.oldPassword ? 'error' : null}
              value={oldPassword}
              name="oldPassword"
              type="password"
              onChange={handleInputChange}
              onBlur={validateOnBlur}
            />
            <FieldErrorMap code={state.errors.oldPassword}>
              {t('error.requiredField')}
            </FieldErrorMap>
          </Field>
          <Field validationState={state.errors.newEmail ? 'error' : null}>
            <FieldLabel htmlFor="new-email">{t('profile.newEmail')}</FieldLabel>
            <Text
              id="new-email"
              validationState={state.errors.newEmail ? 'error' : null}
              value={newEmail}
              name="newEmail"
              onChange={handleInputChange}
              onBlur={validateOnBlur}
            />
            <FieldErrorMap code={state.errors.newEmail}>
              <span key="required">{t('error.requiredField')}</span>
              <span key="invalid">{t('profile.errors.email.invalid')}</span>
              <span key="inUse">{t('profile.errors.email.inUse')}</span>
            </FieldErrorMap>
          </Field>
          <div key="actions" style={{ float: 'right' }}>
            <Button theme="text" onClick={() => setShowEmailModal(false)}>
              {t('standard.cancel')}
            </Button>
            <Button
              data-testid="update-email-button"
              disabled={
                !oldPassword.length || !newEmail.length || state.errors.newEmail
              }
              spinning={loading}
              type="submit"
              theme="primary"
            >
              {t('profile.update')}
            </Button>
          </div>
        </form>
      </Modal>
    );
  };

  const renderPasswordModal = () => {
    return (
      <Modal
        width={600}
        open={showPasswordModal}
        title={t('profile.changePassword')}
        onRequestClose={() => setShowPasswordModal(false)}
      >
        <h3>{t('profile.changePassword')}</h3>
        <form onSubmit={changePassword}>
          <Field validationState={state.errors.oldPassword ? 'error' : null}>
            <FieldLabel htmlFor="current-password">
              {t('profile.oldPassword')}
            </FieldLabel>
            <Text
              id="current-password"
              validationState={state.errors.oldPassword ? 'error' : null}
              value={state.oldPassword}
              type="password"
              name="oldPassword"
              onChange={handleInputChange}
              onBlur={validateOnBlur}
            />
            <FieldErrorMap code={state.errors.oldPassword}>
              {t('error.requiredField')}
            </FieldErrorMap>
          </Field>
          <PasswordValidator
            changePassword
            email={{ value: state.oldEmail }}
            password={{
              value: state.newPassword,
              error: state.errors.newPassword,
              validations: state.validations,
            }}
            handleInputChange={({ value, validations }) =>
              setState({ newPassword: value, validations })
            }
          />
          <div key="actions" style={{ float: 'right' }}>
            <Button theme="text" onClick={() => setShowPasswordModal(false)}>
              {t('standard.cancel')}
            </Button>
            <Button
              data-testid="update-password-button"
              disabled={!(state.oldPassword.length && every(state.validations))}
              spinning={loading}
              theme="primary"
              type="submit"
            >
              {t('profile.update')}
            </Button>
          </div>
        </form>
      </Modal>
    );
  };

  const sm = 100;
  const md = 50;
  const lg = 50;
  return (
    <div className="profile">
      <h3>{t('nav.buyer_area.settings.profile')}</h3>
      <Grid gutter={100}>
        <Col sm={sm} md={md} lg={lg}>
          <form>
            <Field validationState={state.errors.firstName ? 'error' : null}>
              <FieldLabel htmlFor="first-name">
                {t('profile.firstName')}
              </FieldLabel>
              <Text
                id="first-name"
                name="firstName"
                onBlur={validateOnBlur}
                onChange={handleInputChange}
                validationState={state.errors.firstName ? 'error' : null}
                value={state.firstName}
              />
              <FieldErrorMap code={state.errors.firstName}>
                {t('error.requiredField')}
              </FieldErrorMap>
            </Field>
            <Field validationState={state.errors.lastName ? 'error' : null}>
              <FieldLabel htmlFor="last-name">
                {t('profile.lastName')}
              </FieldLabel>
              <Text
                id="last-name"
                validationState={state.errors.lastName ? 'error' : null}
                value={state.lastName}
                name="lastName"
                onChange={handleInputChange}
                onBlur={validateOnBlur}
              />
              <FieldErrorMap code={state.errors.lastName}>
                {t('error.requiredField')}
              </FieldErrorMap>
            </Field>
            <Field>
              <FieldLabel htmlFor="title-position">
                {t('profile.title')}
              </FieldLabel>
              <Text
                id="title-position"
                value={state.workTitle}
                name="workTitle"
                onChange={handleInputChange}
              />
            </Field>
            <Field validationState={state.errors.workPhone ? 'error' : null}>
              <FieldLabel htmlFor="phone">
                {t('profile.phoneNumber')}
              </FieldLabel>
              <PhoneNumber
                id="phone"
                defaultOption="US"
                name="phone"
                value={state.workPhone}
                validationState={state.errors.workPhone ? 'error' : null}
                onChange={value =>
                  setState({
                    workPhone: value,
                    errors: { ...state.errors, workPhone: '' },
                  })
                }
                onBlur={validatePhone}
              />
              <FieldErrorMap code={state.errors.workPhone}>
                {t('profile.errors.phoneNumber.invalid')}
              </FieldErrorMap>
            </Field>
            <LocaleSelect
              locale={{ value: state.locale }}
              updateLocale={locale => setState({ locale })}
            />
            <Field>
              <FieldLabel htmlFor="currency-code">
                {t('profile.currencyCode.label')}
              </FieldLabel>
              <InfoTooltip align="left" theme="light">
                {t('profile.currencyCode.tooltip')}
              </InfoTooltip>
              <Select
                name="currencyCode"
                enableSearch={false}
                multiple={false}
                placeholder={t('standard.defaultOption')}
                options={currencyCodeOptions}
                onChange={value => setState({ currencyCode: value })}
                value={state.currencyCode}
              />
            </Field>
            <Field>
              <FieldLabel htmlFor="currency-display">
                {t('profile.currencyDisplay.label')}
              </FieldLabel>
              <InfoTooltip align="left" theme="light">
                {t('profile.currencyDisplay.tooltip')}
              </InfoTooltip>
              <Select
                name="currencyDisplay"
                enableSearch={false}
                multiple={false}
                placeholder={t('standard.defaultOption')}
                options={currencyDisplayOptions}
                onChange={value => setState({ defaultCurrency: value })}
                value={state.defaultCurrency}
              />
            </Field>
            <Button
              data-testid="update-profile-button"
              disabled={
                state.errors.firstName ||
                state.errors.lastName ||
                state.errors.workPhone
              }
              onClick={updateUserProfile}
              theme="primary"
              type="submit"
            >
              {t('profile.update')}
            </Button>
          </form>
        </Col>
        <Col sm={sm} md={md} lg={lg}>
          <FieldLabel htmlFor="change-password-modal">
            {t('profile.changePassword')}
          </FieldLabel>
          <p>{t('profile.passwordSubtext')}</p>
          <Button
            data-testid="change-password-button"
            type="submit"
            theme="primary"
            onClick={() => setShowPasswordModal(true)}
          >
            {t('profile.changePassword')}
          </Button>
          {renderPasswordModal()}
          <FieldLabel htmlFor="change-email-modal">
            {t('profile.changeEmail')}
          </FieldLabel>
          <p>
            {t('profile.emailSubtext')}
            <strong>
              &nbsp;
              {state.oldEmail}
            </strong>
            .
          </p>
          <Button
            data-testid="change-email-button"
            type="submit"
            theme="primary"
            onClick={() => setShowEmailModal(true)}
          >
            {t('profile.changeEmail')}
          </Button>
          {renderEmailModal()}
        </Col>
      </Grid>
    </div>
  );
};

Profile.defaultProps = {
  i18n: { changeLanguage: null, language: 'en-US' },
};

Profile.propTypes = {
  i18n: PropTypes.shape({
    changeLanguage: PropTypes.func,
    language: PropTypes.string,
  }),
};

export default Profile;
