/* eslint-disable react/sort-comp */
/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
import React from 'react';
import scrollToComponent from 'react-scroll-to-component';
import CONSTANTS from '../../globals';

const IframeRoutes = {};
IframeRoutes[CONSTANTS.DOCUMENTS.INVOICES] = '/buyer/invoice/search';
IframeRoutes[CONSTANTS.DOCUMENTS.PAYMENTS] = '/buyer/paymentRecord/search';
IframeRoutes[CONSTANTS.DOCUMENTS.PURCHASE_ORDERS] =
  '/buyer/purchaseOrder/search';

IframeRoutes[CONSTANTS.SUPPLIERS.ANNOUNCEMENTS] = '/buyer/announcements';
IframeRoutes[CONSTANTS.SUPPLIERS.FAQS] =
  '/buyer/customerSpecificContent/showCustomerSpecificContentMaintain';
IframeRoutes[CONSTANTS.SUPPLIERS.LAUNCH] =
  '/buyer/vendorSetup/showVendorSearch?id=vendorSetup';
IframeRoutes[CONSTANTS.SUPPLIERS.COMPLIANCE] =
  '/buyer/supplierCenter/showComplianceReport';
IframeRoutes[CONSTANTS.SUPPLIERS.SEARCH] = '/buyer/supplierCenter/index';

IframeRoutes[CONSTANTS.SETTINGS.USER_MANAGER] = '/buyer/userManager/index';

IframeRoutes[CONSTANTS.CASH.GLOBAL_SETTINGS] =
  '/buyer/dynamicDiscounting/showGlobalSettings';
IframeRoutes[CONSTANTS.CASH.EARLY_PAYMENT_GROUPS] =
  '/buyer/dynamicDiscounting/showGroupSettings';
IframeRoutes[CONSTANTS.CASH.SUPPLIER_ASSIGNMENTS] =
  '/buyer/dynamicDiscounting/showSupplierAssignments';

export default class MonolithIFrame extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      iframeUrl: this.getUrl(props.location.pathname, true),
      key: '',
    };
    this.currentIframeUrl = null;
    this.iframe = null;
  }

  componentDidMount() {
    window.addEventListener('message', this.handleMessage);
  }

  componentDidUpdate(prevProps) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      let iframeUrl = this.getUrl(this.props.location.pathname);
      if (
        !this.currentIframeUrl ||
        !this.currentIframeUrl.includes(iframeUrl)
      ) {
        this.currentIframeUrl = null;
        iframeUrl = this.getUrl(this.props.location.pathname, true);
        this.setState({ iframeUrl, key: Math.random() }); // eslint-disable-line react/no-did-update-set-state
      }
    }
  }

  componentWillUnmount() {
    window.removeEventListener('message', this.handleMessage);
  }

  getUrl(pathname, addEmbedParam = false) {
    const baseUrl = `${this.props.portalUrl}${IframeRoutes[pathname]}`;
    let fullUrl = baseUrl;
    if (addEmbedParam) {
      if (baseUrl.includes('?')) {
        fullUrl = `${baseUrl}&embed=true`;
      } else {
        fullUrl = `${baseUrl}?embed=true`;
      }
    }
    return fullUrl;
  }

  handleFancyboxOpened() {
    scrollToComponent(this.iframe, { align: 'top' });
  }

  handleActivity() {
    if (this.props.registerActivity) {
      this.props.registerActivity();
    }
  }

  handleLocationChanged(msg) {
    if (msg.location !== this.state.iframeUrl) {
      this.currentIframeUrl = msg.location;
      Object.entries(IframeRoutes).forEach(route => {
        if (msg.location.includes(route[1])) {
          const { history } = this.props;
          history.push(route[0]);
        }
      });
    }
  }

  handleMessage = e => {
    switch (e.data.type) {
      case 'resize':
        this.handleResizeMsg(e.data);
        break;
      case 'fancyboxOpened':
        this.handleFancyboxOpened();
        break;
      case 'locationChange':
        this.handleLocationChanged(e.data);
        break;
      case 'activity':
        this.handleActivity();
        break;
      default: // do nothing
    }
  };

  handleResizeMsg(msg) {
    if (this.iframe) {
      this.iframe.style.height = `${msg.height}px`;
    }
  }

  render() {
    const { iframeUrl, key } = this.state;
    return (
      <iframe
        title="Monolith IFrame"
        key={key}
        ref={iframe => {
          this.iframe = iframe;
        }}
        src={iframeUrl}
        height="550"
        width="100%"
      />
    );
  }
}
