import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
import validate from 'validate.js';
import {
  Branding,
  Button,
  Col,
  Grid,
  Localization,
  Modal,
  ModalContainer,
  Progress,
  ToastContainer,
} from 'taulia-ui';
import { Router } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useFetch, useTimer, useDebounceSend } from '../../hooks';
import { GenericErrorMessage } from '../../Utils/GenericErrorMessage';
import { createRequest } from '../../Utils/RequestUtils';
import {
  AnalyticsConfigContext,
  RequestContext,
  UserSettingsContext,
} from '../../contexts';
import Footer from './Footer';
import Pages from './Pages';
import BuyerNotifications from './BuyerNotifications';
import HeaderWrapper from './HeaderWrapper';

const getInactiveWarningDuration = maxInactiveInterval => {
  const duration = maxInactiveInterval ?? 1800;
  return (duration - 120) * 1000;
};

const getInactiveLogoutDuration = maxInactiveInterval => {
  const duration = maxInactiveInterval ?? 1800;
  return (duration + 120) * 1000;
};

const App = ({ history }) => {
  const { t, i18n } = useTranslation();
  const request = useRef(createRequest());

  const {
    data: sessionData,
    error: sessionError,
    loading: sessionLoading,
  } = useFetch('/api/session', request.current);

  const {
    data: userInfoData,
    loading: userInfoLoading,
    error: userInfoError,
  } = useFetch('/api/user-info', request.current);

  const { data: regex } = useFetch(
    '/api/emailValidation/regex',
    request.current
  );

  const { error: activityError, doSend: doSendActivity } = useDebounceSend(
    'POST',
    '/api/activity',
    request.current,
    10000
  );

  const {
    complete: logoutTimerComplete,
    doReset: doResetLogoutTimer,
  } = useTimer(getInactiveLogoutDuration(sessionData?.maxInactiveInterval));

  const {
    complete: logoutWarningTimerComplete,
    doReset: doResetLogoutWarningTimer,
  } = useTimer(getInactiveWarningDuration(sessionData?.maxInactiveInterval));

  useEffect(() => {
    const { csrfConfig, settings } = sessionData ?? {};
    if (csrfConfig) {
      request.current = createRequest(sessionData?.csrfConfig);
    }
    if (settings && settings?.gaEnabled) {
      ReactGA.initialize(settings.gaAccount);
      ReactGA.pageview(
        `${history.location.pathname}+${history.location.search}`
      );
      history.listen(location => {
        ReactGA.pageview(`${location.pathname}+${location.search}`);
      });
    }
  }, [sessionData]);

  useEffect(() => {
    if (userInfoData) {
      const { locale } = userInfoData;
      i18n.changeLanguage(locale);
      Localization.setLocale(locale);
    }
  }, [userInfoData]);

  useEffect(() => {
    validate.validators.email = value => {
      if (value && value.match(regex.pattern)) {
        return null;
      }
      return '^email';
    };
  }, [regex]);

  useEffect(() => {
    if (activityError && activityError.response.statusCode === 403) {
      window.location.reload();
    }
  }, [activityError]);

  useEffect(() => {
    if (logoutTimerComplete) {
      // eslint-disable-next-line no-console
      console.log('[App] Session timer expired, logging out');
      window.location.reload();
    }
  }, [logoutTimerComplete]);

  const renderTitle = () => {
    // Take the path name, split it by '/' and '-', capitalize the first letter, and add '|' if has multiple sections
    if (history.location && history.location.pathname) {
      return history.location.pathname
        .split('/')
        .filter(word => word)
        .map(title =>
          title
            .split('-')
            .map(word => word.charAt(0).toUpperCase() + word.slice(1))
            .join(' ')
        )
        .join(' | ');
    }
    return t('app.title', {
      portalCompany: Branding.fetchDisplayName(),
    });
  };

  if (sessionError || userInfoError) {
    return (
      <div className="app-error">
        <GenericErrorMessage />
      </div>
    );
  }
  if (sessionLoading || userInfoLoading) {
    return (
      <div>
        <Progress />
      </div>
    );
  }

  const { settings, analyticsConfig } = {
    ...sessionData,
  };
  const childProps = { ...sessionData, request: request.current };
  return (
    <Router history={history}>
      <Grid
        onClick={() => {
          doResetLogoutTimer();
          doResetLogoutWarningTimer();
          doSendActivity({});
        }}
        className="app"
      >
        <Col sm={100} md={100} lg={100}>
          <Helmet
            title={renderTitle()}
            link={[
              { rel: 'shortcut icon', href: Branding.fetchImage('favicon') },
            ]}
            script={
              settings?.optimizelyUrl
                ? [{ src: settings.optimizelyUrl, type: 'text/javascript' }]
                : []
            }
          >
            <meta name="robots" content="noindex, nofollow, noarchive" />
          </Helmet>
          <RequestContext.Provider value={request.current}>
            <UserSettingsContext.Provider
              value={{
                buyerGroupPrimaryCompanyId:
                  userInfoData.buyerGroupPrimaryCompanyId,
                companyId: userInfoData.companyId,
                companyName: userInfoData.companyName,
                email: userInfoData.email,
                userId: userInfoData.id,
                userRoles: userInfoData.roles,
                userFirstName: userInfoData.firstName,
                userLastName: userInfoData.lastName,
              }}
            >
              <HeaderWrapper />
              <div className="body">
                <BuyerNotifications />
                <AnalyticsConfigContext.Provider value={analyticsConfig}>
                  <Pages {...childProps} />
                </AnalyticsConfigContext.Provider>
                <ModalContainer />
                <Modal
                  open={logoutWarningTimerComplete}
                  width={400}
                  onRequestClose={() => {}}
                >
                  <div>
                    <p>{t('app.stayLoggedIn')}</p>
                    <Button style={{ float: 'right' }}>
                      {t('app.continue')}
                    </Button>
                  </div>
                </Modal>
                <ToastContainer />
              </div>
              <Footer />
            </UserSettingsContext.Provider>
          </RequestContext.Provider>
        </Col>
      </Grid>
    </Router>
  );
};

App.propTypes = {
  history: PropTypes.shape().isRequired,
};

export default App;
