import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Link, Modal, Select } from 'taulia-ui';
import { useTranslation, Trans } from 'react-i18next';

function AddSegmentsModal({
  onAddSuppliersToSegment,
  open,
  onRequestClose,
  segments,
  selectedSuppliers,
}) {
  const { t } = useTranslation();
  const [selectedSegmentId, setSelectedSegmentId] = useState('');
  const supplierCount = t('supplierSearch.supplierCount', {
    count: selectedSuppliers.selectedCount,
  });

  useEffect(() => {
    if (segments.length) {
      setSelectedSegmentId(segments[0].id);
    }
  }, [segments]);

  const segmentName =
    segments.length &&
    selectedSegmentId &&
    segments.find(segment => segment.id === selectedSegmentId).name;
  const options = segments.map(segment => {
    return { value: segment.id, label: segment.name };
  });

  const addSuppliersToSegment = () => {
    onAddSuppliersToSegment({ id: selectedSegmentId, name: segmentName });
  };

  return (
    <Modal width={400} padding={0} open={open} onRequestClose={onRequestClose}>
      <div className="modal-export">
        <p>{t('supplierSearch.addSuppliersHeader', { supplierCount })}</p>
        <hr className="hr-actions-subMenu" />
        {segments.length ? (
          <section>
            <strong>{t('nav.buyer_area.suppliers.supplier_segments')}</strong>
            <Select
              multiple={false}
              noSearchResultsLabel={t(
                'bulkInvites.searchPage.noSearchResultsLabel'
              )}
              onChange={segment => setSelectedSegmentId(segment)}
              options={options}
              searchPlaceholder={t('bulkInvites.searchPage.searchPlaceholder')}
              value={selectedSegmentId}
            />
            {selectedSegmentId && (
              <p>
                <Trans
                  i18nKey="supplierSearch.addSuppliersModalText"
                  values={{ supplierCount, segmentName }}
                  components={[<strong />]}
                />
              </p>
            )}
            <div key="actions" style={{ float: 'right' }}>
              <Button theme="text" onClick={onRequestClose}>
                {t('standard.cancel')}
              </Button>
              <Button onClick={addSuppliersToSegment} theme="primary">
                {t('standard.submit')}
              </Button>
            </div>
          </section>
        ) : (
          <section>
            <p>
              <Trans
                i18nKey="supplierSearch.addSuppliersModalEmptyText"
                components={[<Link to="/suppliers/segments" />]}
              />
            </p>
            <div key="actions" style={{ float: 'right' }}>
              <Button theme="text" onClick={onRequestClose}>
                {t('standard.cancel')}
              </Button>
            </div>
          </section>
        )}
      </div>
    </Modal>
  );
}

AddSegmentsModal.propTypes = {
  onAddSuppliersToSegment: PropTypes.func.isRequired,
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  segments: PropTypes.arrayOf(PropTypes.shape()),
  selectedSuppliers: PropTypes.shape({
    all: PropTypes.bool,
    data: PropTypes.arrayOf(PropTypes.shape()),
    ids: PropTypes.arrayOf(PropTypes.string),
    selectedCount: PropTypes.number,
  }).isRequired,
};

AddSegmentsModal.defaultProps = {
  open: true,
  segments: [],
};

export default AddSegmentsModal;
