import React, { useContext, useEffect, useState } from 'react';
import {
  ActionsCell,
  Button,
  Field,
  FieldLabel,
  Link,
  ListView,
  Text,
  FieldErrorMap,
  Modal,
  Progress,
  StatusIndicator,
  toast,
} from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import Preview from './Preview';
import CONSTANTS from '../../globals';
import { genericErrorToast } from '../../Utils/utils';
import { localizeDate, localizeNumber } from '../../Utils/localizeUtils';
import { RequestContext, UserSettingsContext } from '../../contexts';

function AnnouncementsList() {
  const history = useHistory();
  const { companyName, email } = useContext(UserSettingsContext);
  const request = useContext(RequestContext);
  const [announcements, setAnnouncements] = useState();
  const [announcementToClone, setAnnouncementToClone] = useState();
  const [announcementToPreview, setAnnouncementToPreview] = useState();
  const [createAnnouncementLoading, setCreateAnnouncementLoading] = useState(
    false
  );
  const [name, setName] = useState({ value: '', error: null });
  const [newAnnouncement, setNewAnnouncement] = useState();
  const [paginationData, setPaginationData] = useState({
    page: 0,
    maxPerPage: 10,
    totalResults: 0,
  });
  const [showAnnouncementModal, setShowAnnouncementModal] = useState(false);
  const [showPreviewModal, setShowPreviewModal] = useState(false);
  const { t } = useTranslation();

  const createAnnouncement = e => {
    e.preventDefault();
    if (!name.value.length) {
      setName({ value: '', error: true });
    } else {
      setCreateAnnouncementLoading(true);
      const data = { title: name.value, message: '' };
      if (announcementToClone) {
        data.subject = announcementToClone.subject;
        data.message = announcementToClone.message;
      }
      request('PUT', '/api/announcement')
        .send(data)
        .then(res => {
          setShowAnnouncementModal(false);
          setAnnouncementToClone(null);
          setNewAnnouncement(res.body.id);
        })
        .catch(() => {
          setCreateAnnouncementLoading(false);
          setAnnouncementToClone(null);
          genericErrorToast();
        });
    }
  };

  const onCancelModal = () => {
    setName({ value: '', error: null });
    setShowAnnouncementModal(false);
  };

  const getAnnouncements = (currentPage = 0, pageSize = 10) => {
    request('GET', '/api/announcements')
      .query({
        includeTotal: true,
        maxPerPage: pageSize,
        page: currentPage,
      })
      .then(res => {
        if (res.body) {
          const { maxPerPage, page, totalResults } = res.body;
          setPaginationData({ maxPerPage, page, totalResults });
          setAnnouncements(res.body.announcements);
        }
      })
      .catch(() => {
        setAnnouncements([]);
        genericErrorToast();
      });
  };

  useEffect(() => {
    getAnnouncements();
  }, []);

  useEffect(() => {
    // Navigate to announcement page after modal is closed and new id is retrieved from server
    if (!showAnnouncementModal && newAnnouncement) {
      history.push(
        `${CONSTANTS.SUPPLIERS.ANNOUNCEMENTS}/${newAnnouncement}?new`
      );
    }
  }, [showAnnouncementModal, newAnnouncement]);

  const renderAnnouncementModal = () => {
    return (
      <Modal
        onRequestClose={onCancelModal}
        open={showAnnouncementModal}
        width={600}
      >
        <form onSubmit={e => createAnnouncement(e)}>
          <h2>{t('announcements.createAnAnnouncement')}</h2>
          <Field validationState={name.error ? 'error' : null}>
            <FieldLabel htmlFor="subject">{t('fields.name')}</FieldLabel>
            <Text
              id="subject"
              name="subject"
              onChange={e =>
                setName({
                  value: e.target.value,
                  error: !e.target.value.length,
                })
              }
              validationState={name.error ? 'error' : null}
              value={name.value}
            />
            <FieldErrorMap code="error">
              {t('error.requiredField')}
            </FieldErrorMap>
          </Field>
          <div style={{ float: 'right' }}>
            <Button onClick={onCancelModal} theme="text">
              {t('standard.cancel')}
            </Button>
            <Button
              data-testid="create-announcement"
              disabled={name.error}
              spinning={createAnnouncementLoading}
              theme="primary"
              type="submit"
            >
              {t('announcements.createAnnouncement')}
            </Button>
          </div>
        </form>
      </Modal>
    );
  };

  // List view

  const onPageChange = (page, pageSize) => {
    getAnnouncements(page, pageSize);
    setPaginationData({ ...paginationData, maxPerPage: pageSize, page });
  };

  const actions = [
    {
      label: `+ ${t('announcements.createAnnouncement')}`,
      allowEmptySelection: true,
      onClick: () => setShowAnnouncementModal(true),
    },
  ];

  const getActionsForCell = data => {
    const cellActions = [
      {
        label: t(`standard.${data.status === 'DRAFT' ? 'edit' : 'view'}`),
        action: () =>
          history.push(
            `${CONSTANTS.SUPPLIERS.ANNOUNCEMENTS}/${data.announcementId}`
          ),
      },
      {
        label: t('announcements.preview'),
        action: () => {
          if (data.announcementId === announcementToPreview?.announcementId) {
            setShowPreviewModal(true);
          } else {
            request(
              'GET',
              `/api/announcement/${data.announcementId}/attachments`
            ).then(res => {
              setAnnouncementToPreview({ ...data, files: res.body });
              setShowPreviewModal(true);
            });
          }
        },
      },
      {
        label: t('announcements.clone'),
        action: () => {
          setName({ value: data.title });
          setAnnouncementToClone(data);
          setShowAnnouncementModal(true);
        },
      },
    ];
    if (data.showReportDownload) {
      cellActions.push({
        label: t('announcements.downloadReport'),
        action: () => {
          request('PUT', `/api/announcement/${data.announcementId}/report`)
            .send()
            .then(() => {
              toast.success(
                <div>
                  <h4>{t('announcements.downloadReportHeading')}</h4>
                  <p>{t('announcements.downloadReportMsg', { email })}</p>
                </div>
              );
            })
            .catch(() => {
              genericErrorToast();
            });
        },
      });
    }
    return cellActions;
  };

  const getStatus = (status, endDate) => {
    if (status === 'DRAFT') {
      return { state: 'draft', color: 'yellow' };
    }
    const today = new Date().toISOString();
    if (endDate > today) {
      return { state: 'active', color: 'green' };
    }
    return { state: 'inactive', color: 'gray' };
  };

  const columns = [
    {
      key: 'announcement',
      label: t('announcements.announcement'),
      type: 'TEXT',
      width: 400,
      cell: data => {
        const { announcementId, dateCreated, title } = data.object;
        const date = localizeDate(dateCreated);
        return (
          <div className="table-group">
            <Link to={`/suppliers/announcements/${announcementId}`}>
              {title}
            </Link>
            <span>{t('announcements.createdOn', { date })}</span>
          </div>
        );
      },
    },
    {
      key: 'stats',
      label: t('fields.status'),
      type: 'TEXT',
      width: 250,
      cell: data => {
        const { endDate, startDate, status } = data.object;
        let dateText = '';
        if (startDate && endDate) {
          const date1 = localizeDate(startDate);
          const date2 = localizeDate(endDate);
          dateText = (
            <span style={{ display: 'block', fontSize: '14px' }}>
              {t('announcements.dateToDate', { date1, date2 })}
            </span>
          );
        }
        const { state, color } = getStatus(status, endDate);
        return (
          <>
            <StatusIndicator color={color}>
              {t(`announcements.${state}`)}
            </StatusIndicator>
            {dateText}
          </>
        );
      },
    },
    {
      key: 'publishedTo',
      label: t('announcements.publishedTo'),
      type: 'TEXT',
      cell: data => {
        if (data.object.status === 'DRAFT') {
          return '';
        }
        const count = data.object.report.countOfMessages;
        return (
          <div className="table-group">
            <span>{localizeNumber(count)}</span>
            <span>{t('documentExports.supplier', { count })}</span>
          </div>
        );
      },
    },
    {
      key: 'acknowledgedBy',
      label: t('announcements.acknowledgedBy'),
      type: 'TEXT',
      width: 200,
      cell: data => {
        if (data.object.status === 'DRAFT') {
          return '';
        }
        const count = data.object.report.countOfAcks;
        return (
          <div className="table-group">
            <span>{localizeNumber(count)}</span>
            <span>{t('documentExports.supplier', { count })}</span>
          </div>
        );
      },
    },
    {
      key: 'actions',
      label: '',
      cell: data => <ActionsCell items={getActionsForCell(data.object)} />,
    },
  ];

  if (!announcements) {
    return <Progress />;
  }

  return (
    <>
      <ListView
        actions={actions}
        actionsEnabled
        columns={columns}
        data={announcements}
        isResizable
        noResultsText={<p>{t('announcements.noAnnouncements')}</p>}
        onPageChange={onPageChange}
        page={paginationData?.page}
        pageSize={paginationData?.maxPerPage}
        paginationEnabled={!!paginationData}
        showColumnConfig={false}
        showCount
        totalCount={paginationData?.totalResults}
      />
      <Modal
        onRequestClose={() => {
          setShowPreviewModal(false);
        }}
        open={showPreviewModal}
        width={1000}
      >
        <Preview
          announcement={announcementToPreview}
          companyName={companyName}
        />
      </Modal>
      {renderAnnouncementModal()}
    </>
  );
}

export default AnnouncementsList;
