import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
  Modal,
  Field,
  FieldLabel,
  FieldErrorMap,
  Text,
  Button,
} from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import validate from 'validate.js';

function EditContactModal({
  open,
  data,
  onRequestClose,
  onSave,
  onSaveAndInvite,
}) {
  const { t } = useTranslation();
  const [emailAddress, setEmailAddress] = useState();
  const [firstName, setFirstName] = useState();
  const [lastName, setLastName] = useState();
  const [emailError, setEmailError] = useState();
  const saveEnabled = !emailError && emailAddress;

  useEffect(() => {
    setEmailAddress(data?.emailAddress ?? '');
    setFirstName(data?.firstName ?? '');
    setLastName(data?.lastName ?? '');
  }, [data]);

  const getContact = () => {
    return {
      ...data,
      emailAddress,
      firstName,
      lastName,
    };
  };

  const validateEmail = value => {
    let error = validate({ email: value }, { email: { email: true } })
      ? 'invalid'
      : null;

    if (!value.length) {
      error = 'required';
    }

    setEmailError(error);
  };

  return (
    <Modal width={500} open={open} onRequestClose={onRequestClose}>
      <div className="edit-contact-modal" data-testid="edit-contact-modal">
        {data ? (
          <h3>{t('supplierView.editContact')}</h3>
        ) : (
          <h3>{t('supplierView.addContact')}</h3>
        )}

        <Field
          validationState={emailError ? 'error' : null}
          className="contact-email-address"
        >
          <FieldLabel htmlFor="email">
            {t('supplierView.emailAddress')}
          </FieldLabel>
          <Text
            id="emailAddress"
            name="emailAddress"
            data-testid="contact-edit-email"
            value={emailAddress}
            onChange={evt => {
              setEmailAddress(evt.target.value);
              validateEmail(evt.target.value);
            }}
          />

          <FieldErrorMap code={emailError}>
            <span key="required">{t('error.requiredField')}</span>
            <span key="invalid">{t('profile.errors.email.invalid')}</span>
          </FieldErrorMap>
        </Field>

        <Field className="contact-first-name">
          <FieldLabel htmlFor="firstName">
            {t('supplierView.firstName')}
          </FieldLabel>
          <Text
            id="firstName"
            name="firstName"
            data-testid="contact-edit-first-name"
            value={firstName}
            onChange={evt => {
              setFirstName(evt.target.value);
            }}
          />
        </Field>

        <Field className="contact-last-name">
          <FieldLabel htmlFor="lastName">
            {t('supplierView.lastName')}
          </FieldLabel>
          <Text
            id="lastName"
            name="lastName"
            data-testid="contact-edit-last-name"
            value={lastName}
            onChange={evt => {
              setLastName(evt.target.value);
            }}
          />
        </Field>

        <div className="contact-button-container">
          <Button
            theme="text"
            data-testid="contact-cancel"
            onClick={() => {
              onRequestClose();
            }}
          >
            {t('standard.cancel')}
          </Button>
          <Button
            data-testid="contact-save"
            disabled={!saveEnabled}
            onClick={() => {
              onSave(getContact());
            }}
          >
            {t('standard.save')}
          </Button>
          <Button
            disabled={!saveEnabled}
            data-testid="contact-save-and-invite"
            theme="primary"
            onClick={() => {
              onSaveAndInvite(getContact());
            }}
          >
            {t('supplierView.saveAndInvite')}
          </Button>
        </div>
      </div>
    </Modal>
  );
}

export default EditContactModal;

EditContactModal.propTypes = {
  open: PropTypes.bool.isRequired,
  data: PropTypes.shape(),
  onRequestClose: PropTypes.func.isRequired,
  onSave: PropTypes.func,
  onSaveAndInvite: PropTypes.func,
};

EditContactModal.defaultProps = {
  data: null,
  onSave: () => {},
  onSaveAndInvite: () => {},
};
