import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import CONSTANTS from '../../globals';
import { Home, LookerDashboard } from '../Analytics';
import NotFound from './NotFound';
import Releases from './Releases';
import { SupplierSearchBeta, SupplierSearchContainer } from '../SupplierSearch';
import { MonolithIFrame } from '../MonolithIFrame';
import CompanySettings from '../Settings/CompanySettings';
import SecuritySettings from '../Settings/SecuritySettings';
import { AnnouncementsContainer, AnnouncementsView } from '../Announcements';
import { SupplierView } from '../Suppliers';
import { TermsAndConditions } from '../TermsAndConditions';
import { ConnectionDetails, Profile, UserManager } from '../Settings';
import { SegmentContainer } from '../SupplierSegment';
import { Exports } from '../Documents';
import {
  IndividualPage,
  ReviewPage,
  SearchPage,
  StartPage,
  SuccessPage,
} from '../BulkInvitesPages';
import { BulkInvitesProvider } from '../../contexts';

const Pages = props => {
  return (
    <Switch>
      <Route path={CONSTANTS.NOT_FOUND}>
        <NotFound />
      </Route>
      <Route
        path={CONSTANTS.DASHBOARD.HOME}
        render={routeProps => {
          return <Home {...routeProps} {...props} />;
        }}
      />

      <Route path={CONSTANTS.SUPPLIERS.BULK_INVITES.START}>
        <BulkInvitesProvider>
          <Route exact path={CONSTANTS.SUPPLIERS.BULK_INVITES.START}>
            <StartPage />
          </Route>

          <Route
            path={CONSTANTS.SUPPLIERS.BULK_INVITES.INDIVIDUAL}
            render={routeProps => {
              return <IndividualPage {...routeProps} {...props} />;
            }}
          />

          <Route
            path={CONSTANTS.SUPPLIERS.BULK_INVITES.SEARCH}
            render={routeProps => {
              return <SearchPage {...routeProps} {...props} />;
            }}
          />

          <Route
            path={CONSTANTS.SUPPLIERS.BULK_INVITES.REVIEW}
            render={routeProps => {
              return <ReviewPage {...routeProps} {...props} />;
            }}
          />

          <Route
            path={CONSTANTS.SUPPLIERS.BULK_INVITES.SUCCESS}
            render={routeProps => {
              return <SuccessPage {...routeProps} {...props} />;
            }}
          />
        </BulkInvitesProvider>
      </Route>

      <Route path={CONSTANTS.SUPPLIERS.SUPPLIER_VIEW}>
        <SupplierView />
      </Route>

      <Route
        path={CONSTANTS.SUPPLIERS.SEARCH}
        render={routeProps => {
          return <SupplierSearchContainer {...props} {...routeProps} />;
        }}
      />
      <Route
        path={CONSTANTS.SUPPLIERS.SEARCH_BETA}
        render={routeProps => {
          return <SupplierSearchBeta {...props} {...routeProps} />;
        }}
      />

      <Route
        path={[
          CONSTANTS.CASH.EARLY_PAYMENT_GROUPS,
          CONSTANTS.CASH.GLOBAL_SETTINGS,
          CONSTANTS.CASH.SUPPLIER_ASSIGNMENTS,
          CONSTANTS.DOCUMENTS.INVOICES,
          CONSTANTS.DOCUMENTS.PAYMENTS,
          CONSTANTS.DOCUMENTS.PURCHASE_ORDERS,
          CONSTANTS.SETTINGS.USER_MANAGER,
          CONSTANTS.SUPPLIERS.COMPLIANCE,
          CONSTANTS.SUPPLIERS.FAQS,
          CONSTANTS.SUPPLIERS.LAUNCH,
        ]}
        render={routeProps => {
          return <MonolithIFrame {...routeProps} {...props} />;
        }}
      />

      <Route
        path={CONSTANTS.SUPPLIERS.ANNOUNCEMENTS_VIEW}
        render={routeProps => {
          return <AnnouncementsView {...routeProps} {...props} />;
        }}
      />

      <Route
        path={CONSTANTS.SUPPLIERS.ANNOUNCEMENTS}
        render={routeProps => {
          return <AnnouncementsContainer {...routeProps} {...props} />;
        }}
      />

      <Route
        path={[
          CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.ADD_SUPPLIERS,
          CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.LIST,
        ]}
        render={routeProps => {
          return <SegmentContainer {...routeProps} {...props} />;
        }}
      />

      <Route
        path={[
          CONSTANTS.ANALYTICS.CASH_UNLOCKED,
          CONSTANTS.ANALYTICS.INVOICING,
          CONSTANTS.ANALYTICS.SUPPLY_CHAIN_ANALYTICS,
          CONSTANTS.CASH.DEPLOY,
          CONSTANTS.CASH.FORECAST,
          CONSTANTS.CASH.PLAN,
        ]}
        render={routeProps => {
          return <LookerDashboard {...routeProps} {...props} />;
        }}
      />

      <Route
        path={CONSTANTS.TERMS_AND_CONDITIONS}
        component={TermsAndConditions}
      >
        <TermsAndConditions {...props} />
      </Route>

      <Route path={CONSTANTS.RELEASES}>
        <Releases />
      </Route>

      <Route path={CONSTANTS.SETTINGS.PROFILE}>
        <Profile {...props} />
      </Route>

      <Route path={CONSTANTS.SETTINGS.COMPANY_SETTINGS}>
        <CompanySettings {...props} />
      </Route>

      <Route path={CONSTANTS.SETTINGS.SECURITY_SETTINGS}>
        <SecuritySettings {...props} />
      </Route>

      <Route path={CONSTANTS.DOCUMENTS.EXPORTS}>
        <Exports {...props} />
      </Route>

      <Route
        path={CONSTANTS.SETTINGS.USER_MANAGER_NEW}
        render={routeProps => {
          return <UserManager {...routeProps} {...props} />;
        }}
      />

      <Route path={CONSTANTS.SETTINGS.CONNECTION_DETAILS}>
        <ConnectionDetails />
      </Route>

      <Route exact path="/">
        <Redirect to={CONSTANTS.DASHBOARD.HOME} />
      </Route>
      <Redirect from="*" to={CONSTANTS.NOT_FOUND} />
    </Switch>
  );
};

Pages.propTypes = {};

export default Pages;
