import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Button } from 'taulia-ui';
import {
  createSupplierCsv,
  downloadCsvFile,
  createCsvFileName,
} from '../../Utils/SupplierUtils';
import { genericErrorToast } from '../../Utils/utils';
import { RequestContext } from '../../contexts';

const ActionCell = ({ segment }) => {
  const { t } = useTranslation();
  const request = useContext(RequestContext);

  const downloadSupplier = () => {
    request('GET', `/api/archivedSegments/${segment.id}/suppliers`)
      .then(res =>
        downloadCsvFile(
          createCsvFileName(segment.name),
          createSupplierCsv(res.body.suppliers)
        )
      )
      .catch(genericErrorToast);
  };

  if (segment.numSuppliers) {
    return (
      <Button
        data-testid={`download_${segment.id}_${segment.name}`}
        id={`download_${segment.id}_${segment.name}`}
        theme="text"
        onClick={downloadSupplier}
      >
        {t('supplierSegment.segmentAddSupplierForm.downloadToCsv')}
      </Button>
    );
  }
  return null;
};

ActionCell.propTypes = {
  segment: PropTypes.shape(),
};

ActionCell.defaultProps = {
  segment: {},
};

export default ActionCell;
