/* eslint-disable consistent-return */

import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { FieldList, GaugeIndicator, Modal } from 'taulia-ui';
import { Arrow } from '../../icons';
import {
  localizeDate,
  localizeDateTime,
  localizeTime,
} from '../../Utils/localizeUtils';
import { riskColorMap } from '../../Utils/SupplierUtils';

const EventLogModal = ({ data, onRequestClose, open }) => {
  const { t } = useTranslation();
  if (data?.id) {
    const {
      countryCode,
      dateCreated,
      eventType,
      ipAddress,
      regionName,
      riskLevel,
      riskScoreTotal,
      simEventLogRiskFactors,
      security = {},
      simEventLogUser: {
        is2FaEnabled,
        userDateCreated,
        userEmailAddress,
        userFirstName,
        userLastLogin,
        userLastName,
        userLastPasswordResetRequest,
      },
    } = data;

    const bankChangeTitleDictionary = {
      BANK_ADD: t('supplierView.BANK_ADD'),
      BANK_CHANGE: t('supplierView.BANK_CHANGE'),
      BANK_DELETE: t('supplierView.BANK_DELETE'),
    };
    const sortedbyRiskFactorScore = simEventLogRiskFactors.sort(
      (a, b) => Number(b.riskScore) - Number(a.riskScore)
    );
    const createThreatTypesArray = () => {
      const sortedArr = security.threatTypes?.sort();
      if (
        sortedArr.indexOf('attack_source') > -1 &&
        sortedArr.indexOf('attack_source_http') > -1
      ) {
        sortedArr.splice(sortedArr.indexOf('attack_source'), 1);
      }
      return sortedArr.map((item, idx) => (
        <span key={item}>
          {t(`supplierView.ipThreatTypes.enumValues.${item}`)}
          {idx < security.threatTypes?.length - 1 && <>,&nbsp;</>}
        </span>
      ));
    };

    const fieldListData = {
      colOneData: [
        {
          dataTestId: 'event-log-modal-name',
          label: t('fields.name'),
          value: `${userFirstName} ${userLastName}`,
        },
        {
          dataTestId: 'event-log-modal-email',
          label: t('fields.email'),
          value: userEmailAddress,
        },
        {
          dataTestId: 'event-log-modal-user-date-created',
          label: t('fields.dateCreated'),
          value: localizeDateTime(userDateCreated),
        },
        {
          dataTestId: 'event-log-modal-lastlogin',
          label: t('fields.lastLogin'),
          value: localizeDateTime(userLastLogin),
        },
        {
          dataTestId: 'event-log-modal-lastPasswordReset',
          label: t('supplierView.lastPasswordReset'),
          value: localizeDateTime(userLastPasswordResetRequest),
        },
        {
          dataTestId: 'event-log-modal-twoFactorAuthEnabled',
          label: t('supplierView.twoFactorAuthEnabled'),
          value: is2FaEnabled
            ? t('filters.trueLabel')
            : t('filters.falseLabel'),
        },
      ],
      colTwoData: [
        {
          dataTestId: 'event-log-modal-ipAddress',
          label: t('fields.ipAddress'),
          value: ipAddress,
        },
        {
          dataTestId: 'event-log-modal-ipCountry',
          label: t('fields.ipCountry'),
          value: countryCode,
        },
        {
          dataTestId: 'event-log-modal-ipRegion',
          label: t('fields.ipRegion'),
          value: regionName,
        },
        {
          dataTestId: 'event-log-modal-ipThreatLevel',
          label: t('supplierView.ipThreatLevel'),
          value: security.threatLevel
            ? t(`supplierView.${security.threatLevel}`)
            : '',
        },
        {
          dataTestId: 'event-log-modal-threatTypes',
          label: t('supplierView.ipThreatTypes.label'),
          value:
            security.threatTypes?.length > 0 ? createThreatTypesArray() : '',
        },
      ],
      colOneWidth: 180,
      colTwoWidth: 180,
      header: t('supplierView.userInformation'),
    };

    const scoringCriteriaDictionary = {
      '01': {
        color: 'green',
        description: t('supplierView.2faIsEnabled'),
      },
      '02': {
        color: 'red',
        description: t('supplierView.2faIsNotEnabled'),
      },
      '03': {
        color: 'red',
        description: t('supplierView.bankChangeReqNearHoliday'),
      },
      '04': {
        color: 'red',
        description: t('supplierView.ipAddressDifferentCountry'),
      },
      '05': {
        color: 'red',
        description: t('supplierView.ipAddressSuspiciousLocation'),
      },
      '06': {
        color: 'green',
        description: t('supplierView.ipAddressUsedPreviously'),
      },
      '07': { color: 'red', description: t('supplierView.oneUserOnAccount') },
      '08': {
        color: 'red',
        description: t('supplierView.passwordResetInLastWeek'),
      },
      '09': {
        color: 'red',
        description: t('supplierView.userNotLoggedInRecently'),
      },
      '10': {
        color: 'red',
        description: t('supplierView.userCreatedWithinLastWeek'),
      },
      '11': {
        color: 'red',
        description: t('supplierView.ipAddressHigh'),
      },
      '12': {
        color: 'red',
        description: t('supplierView.ipAddressMedium'),
      },
    };

    return (
      <Modal
        data-testid="supplier-view-event-log-modal"
        onRequestClose={onRequestClose}
        open={open}
        padding={0}
        style={{ minWidth: '455px' }}
        title="Event log modal"
        width={1000}
      >
        <div className="event-log-modal-body">
          <div className="event-log-modal-header">
            <div className="event-log-modal-header-icon">
              <GaugeIndicator
                fillColor={riskColorMap[riskLevel] || 'gray'}
                value={riskScoreTotal || 0}
                width={120}
              />
              <p className="event-log-modal-header-icon-label">
                {riskLevel
                  ? t(`fields.riskLevel.enumValues.${[riskLevel]}`)
                  : ''}
              </p>
            </div>
            <div className="event-log-modal-header-text">
              <h4 className="event-log-modal-header-title">
                {bankChangeTitleDictionary[eventType]}
              </h4>
              <h4 style={{ fontWeight: '400', fontStyle: 'italic' }}>
                {t('supplierView.bankChangeHeader', {
                  date_of_change: localizeDate(dateCreated),
                  time_of_change: localizeTime(dateCreated),
                  email_address: userEmailAddress || '',
                })}
              </h4>
            </div>
          </div>
          <div className="event-log-modal-scoring">
            <h4 className="event-log-modal-sub-header">
              {t('supplierView.scoringCriteria')}
            </h4>
            {sortedbyRiskFactorScore?.map(({ id, riskScore, riskType }) => {
              if (riskType === '10' && riskScore < 10) {
                return;
              }
              return (
                <div
                  className="event-log-modal-criteria"
                  key={`event-log-${id}-${riskType}`}
                >
                  <div>
                    <Arrow
                      style={{
                        height: '25px',
                        transform:
                          scoringCriteriaDictionary[riskType].color === 'green'
                            ? 'none'
                            : 'rotate(180deg)',
                      }}
                      fill={
                        scoringCriteriaDictionary[riskType].color === 'green'
                          ? riskColorMap.LOW
                          : riskColorMap.HIGH
                      }
                    />
                  </div>
                  <h4 style={{ paddingTop: '5px' }}>
                    {scoringCriteriaDictionary[riskType].description}
                  </h4>
                </div>
              );
            })}
          </div>
          <div className="event-log-modal-user-info">
            <FieldList {...fieldListData} />
          </div>
        </div>
      </Modal>
    );
  }
  return null;
};

export default EventLogModal;

EventLogModal.propTypes = {
  data: PropTypes.shape({
    countryCode: PropTypes.string,
    dateCreated: PropTypes.string,
    eventType: PropTypes.string,
    id: PropTypes.string,
    ipAddress: PropTypes.string,
    regionName: PropTypes.string,
    riskLevel: PropTypes.string,
    riskScoreTotal: PropTypes.number,
    security: PropTypes.shape({
      threatLevel: PropTypes.string,
      threatTypes: PropTypes.arrayOf(PropTypes.string),
    }),
    simEventLogRiskFactors: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        riskScore: PropTypes.number,
        riskType: PropTypes.string,
      })
    ),
    simEventLogUser: PropTypes.shape({
      is2FaEnabled: PropTypes.bool,
      userDateCreated: PropTypes.string,
      userEmailAddress: PropTypes.string,
      userFirstName: PropTypes.string,
      userId: PropTypes.string,
      userLastLogin: PropTypes.string,
      userLastName: PropTypes.string,
      userLastPasswordResetRequest: PropTypes.string,
    }),
  }),
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
};

EventLogModal.defaultProps = {
  data: {},
};
