/* eslint-disable no-console */
import React, { useContext, useEffect, useReducer, useState } from 'react';
import PropTypes from 'prop-types';
import { Progress } from 'taulia-ui';
import qs from 'qs';
import {
  AnalyticsConfigContext,
  RequestContext,
  UserSettingsContext,
} from '../../contexts';
import AccessDenied from '../../Utils/AccessDenied';
import CONSTANTS from '../../globals';
import hasAccess from '../../Utils/PageUtil';
import { GenericErrorMessage } from '../../Utils/GenericErrorMessage';

const lookerURLMap = {};
lookerURLMap[CONSTANTS.ANALYTICS.CASH_UNLOCKED] = ['anDeployCashSCF'];
lookerURLMap[CONSTANTS.ANALYTICS.INVOICING] = ['anInvoicing'];
lookerURLMap[CONSTANTS.ANALYTICS.SUPPLY_CHAIN_ANALYTICS] = [
  'supplyChainAnalytics',
];
lookerURLMap[CONSTANTS.CASH.DEPLOY] = ['buyerDeployCash'];
lookerURLMap[CONSTANTS.CASH.FORECAST] = ['cashForecasting'];
lookerURLMap[CONSTANTS.CASH.PLAN] = [
  'scenarioPlannerIndustryBenchmarking',
  'scenarioPlannerOpportunityAssessment',
  'scenarioPlannerSupplierAdoption',
  'scenarioPlannerCoreSupplierList',
  'scenarioPlannerInternalCoreSupplierList',
  'scenarioPlannerInternalExecutableProgramParameters',
];
lookerURLMap[CONSTANTS.DASHBOARD.HOME] = [
  'landingPageFinancial',
  'landingPageInvoicing',
];

const kebabit = str => str.replace(/\//g, '-');

const generateId = data =>
  data?.page
    ? kebabit(data.page.url.split('?')[0])
    : kebabit(data.dashboard.url.split('?')[0]);

const LookerDashboard = ({ location }) => {
  const dashboardConfigs = useContext(AnalyticsConfigContext)?.dashboards;
  const request = useContext(RequestContext);
  const userSettings = useContext(UserSettingsContext);

  const [showDashboardParams, setShowDashboardParams] = useState([]);
  const [filtersChangedFlag, setfiltersChangedFlag] = useState(false);
  const [initialLoadFlag, setInitialLoadFlag] = useState(true);

  const embedResourceReducer = (state, action) => {
    if (action.type === 'resourceLoaded') {
      return [...state, action.payload].sort((a, b) => {
        return a.index - b.index;
      });
    }
    return [];
  };

  const [lookerEmbedResources, resourceLoaded] = useReducer(
    embedResourceReducer,
    []
  );

  const embedResizeReducer = (state, resizeData) => {
    const newEmbedSizes = { ...state };
    newEmbedSizes[resizeData.id] =
      resizeData.height > 0 ? resizeData.height + 30 : 800;
    return newEmbedSizes;
  };

  const [embedSizes, handleResize] = useReducer(embedResizeReducer, {});

  const dashboardTitleReducer = (state, titleData) => {
    let title;
    if (titleData?.title) {
      title = titleData.title.split(':').pop();
    } else {
      return state;
    }
    return { ...state, [titleData.id]: title };
  };

  const [dashboardTitles, handleTitleUpdate] = useReducer(
    dashboardTitleReducer,
    {}
  );

  const changeDashboardFilters = ({ id, newFilters, targetOrigin }) => {
    lookerEmbedResources.forEach(dashboard => {
      const { name, filterResource } = dashboard;
      if (filterResource === id) {
        console.log('Changing related dashboard filters for', name);
        const activeFilters = JSON.stringify({
          type: 'dashboard:filters:update',
          filters: newFilters,
        });
        const dashboardIframe = document.getElementById(name);
        if (dashboardIframe) {
          dashboardIframe.contentWindow.postMessage(
            activeFilters,
            targetOrigin
          );
        }
      }
    });
  };

  const runRelatedDashboard = ({ id, targetOrigin }) => {
    lookerEmbedResources.forEach(dashboard => {
      const { name, filterResource } = dashboard;
      if (filterResource === id) {
        console.log('Running related dashboard for', name);
        const dashboardRun = JSON.stringify({
          type: 'dashboard:run',
        });
        const dashboardIframe = document.getElementById(name);
        dashboardIframe.contentWindow.postMessage(dashboardRun, targetOrigin);
      }
    });
    setfiltersChangedFlag(false);
  };

  const setSavedFilters = ({ targetOrigin }) => {
    lookerEmbedResources.forEach(dashboard => {
      const { name } = dashboard;
      const savedFilters = localStorage.getItem(
        `dashboardFilters-${userSettings.companyId}${name}`
      );
      if (savedFilters) {
        // Set saved filters
        console.log(`Setting saved filters for dashboard id ${name}`);
        const filterAction = JSON.stringify({
          type: 'dashboard:filters:update',
          filters: JSON.parse(savedFilters),
        });
        const dashboardAction = JSON.stringify({ type: 'dashboard:run' });
        const dashboardIframe = document.getElementById(name);
        if (dashboardIframe) {
          dashboardIframe.contentWindow.postMessage(filterAction, targetOrigin);
        }
        // Run with saved filters
        dashboardIframe.contentWindow.postMessage(
          dashboardAction,
          targetOrigin
        );
      }
    });
    setInitialLoadFlag(false);
  };

  const saveDashboardFilters = ({ id, newFilters }) => {
    // Save current dashboard filters
    console.log(`saving dashboard filters for current dashboard id ${id}`);
    localStorage.setItem(
      `dashboardFilters-${userSettings.companyId}${id}`,
      JSON.stringify(newFilters)
    );
    // Save any dashboard filters that have the current dashboard as a filter resource
    lookerEmbedResources.forEach(dashboard => {
      const { name, filterResource } = dashboard;
      if (filterResource === id) {
        console.log(
          `Saving dashboard filters for related dashboard id ${name}`
        );
        localStorage.setItem(
          `dashboardFilters-${userSettings.companyId}${name}`,
          JSON.stringify(newFilters)
        );
      }
    });
  };

  const handleMessage = event => {
    let data;
    try {
      data = JSON.parse(event?.data);
    } catch (e) {
      return;
    }
    const id = generateId(data);
    if (data?.type === 'dashboard:loaded') {
      handleTitleUpdate({ id, title: data?.dashboard?.title });
    }
    if (data?.type === 'page:properties:changed') {
      console.log(`${id} height = ${data.height}`);
      handleResize({ id, height: data.height });
    }
    if (data?.type === 'dashboard:filters:changed') {
      setfiltersChangedFlag(true);
      const targetOrigin = data?.dashboard.absoluteUrl.replace(
        data?.dashboard.url,
        ''
      );
      const newFilters = data?.dashboard.dashboard_filters;
      console.log(`Dashboard filters have changed for dashboard id ${id}`);
      changeDashboardFilters({
        id,
        newFilters,
        targetOrigin,
      });
      saveDashboardFilters({ id, newFilters });
    }
    if (data?.type === 'dashboard:run:start') {
      console.log(`Dashboard has run for dashboard id ${id}`);
      const targetOrigin = data?.dashboard.absoluteUrl.replace(
        data?.dashboard.url,
        ''
      );
      if (initialLoadFlag) {
        setSavedFilters({ targetOrigin });
      }
      if (filtersChangedFlag) {
        runRelatedDashboard({
          id,
          targetOrigin,
        });
      }
    }
  };

  const getLookerResource = (
    lookerResource,
    lookerResourceKabab,
    filterResourceKabab,
    config,
    index
  ) => {
    request('GET', `/api/looker/url?resource=${lookerResource}`)
      .then(resp => {
        resourceLoaded({
          type: 'resourceLoaded',
          payload: {
            name: lookerResourceKabab,
            url: resp.body.url,
            allowedRoles: config?.allowedRoles,
            filterResource: filterResourceKabab,
            showTitle: config?.showTitle ?? false,
            index,
          },
        });
      })
      .catch(e => {
        if (e.message === 'SE_403') {
          resourceLoaded({
            type: 'resourceLoaded',
            payload: {
              name: lookerResourceKabab,
              allowedRoles: config?.allowedRoles,
              filterResource: filterResourceKabab,
              accessError: true,
              index,
            },
          });
        } else {
          resourceLoaded({
            type: 'resourceLoaded',
            payload: {
              name: lookerResourceKabab,
              allowedRoles: config?.allowedRoles,
              filterResource: filterResourceKabab,
              error: true,
              index,
            },
          });
        }
      });
  };

  const checkUiAccess = (
    lookerResource,
    lookerResourceKabab,
    filterResourceKabab,
    config,
    index
  ) => {
    if (hasAccess(config.allowedRoles, userSettings.userRoles)) {
      const query = qs.stringify(
        {
          buyerSettings: config.requiredBuyerSettings?.length
            ? config.requiredBuyerSettings
            : null,
          systemSettings: config.requiredSystemSettings?.length
            ? config.requiredSystemSettings
            : null,
        },
        { arrayFormat: 'brackets' }
      );
      request('GET', `/api/setting/enabled-settings?${query}`).then(resp => {
        if (resp.body === true) {
          getLookerResource(
            lookerResource,
            lookerResourceKabab,
            filterResourceKabab,
            config,
            index
          );
        }
      });
    }
  };

  useEffect(() => {
    resourceLoaded({ type: 'init' });
    const lookerConfigNames = lookerURLMap[location.pathname];
    lookerConfigNames.forEach((configName, index) => {
      const config = dashboardConfigs[configName];
      const lookerResource = config?.lookerResource;
      const filterResource = config?.filterResource;

      if (!lookerResource) {
        console.error(`No looker resource defined for ${configName}`);
      } else {
        const lookerResourceKabab = kebabit(lookerResource);
        const filterResourceKabab = filterResource
          ? kebabit(filterResource)
          : null;
        if (
          lookerEmbedResources.find(it => {
            return it.name === lookerResourceKabab;
          })
        ) {
          return;
        }

        if (config.checkUiAccess) {
          checkUiAccess(
            lookerResource,
            lookerResourceKabab,
            filterResourceKabab,
            config,
            index
          );
        } else {
          getLookerResource(
            lookerResource,
            lookerResourceKabab,
            filterResourceKabab,
            config,
            index
          );
        }
      }
    });
  }, [dashboardConfigs, location]);

  useEffect(() => {
    window.addEventListener('message', handleMessage);
    return () => {
      window.removeEventListener('message', handleMessage);
    };
  }, [filtersChangedFlag, initialLoadFlag, lookerEmbedResources]);

  useEffect(() => {
    const params = qs.parse(location.search, {
      ignoreQueryPrefix: true,
    });
    if (params.displaySections) {
      const displaySections = params.displaySections.split(',');
      if (displaySections.length) {
        setShowDashboardParams(params.displaySections.split(','));
      }
    }
  }, []);
  return (
    <div className="looker-dashboard">
      {!lookerEmbedResources.length && <Progress position="relative" />}
      {lookerEmbedResources.map((resource, index) => {
        if (
          showDashboardParams.length &&
          !showDashboardParams.find(it => {
            return it === (index + 1).toString();
          })
        ) {
          return null;
        }
        if (resource.accessError) {
          return <AccessDenied key={`access-denied-${resource.name}`} />;
        }
        if (resource.error) {
          return <GenericErrorMessage key={`error-${resource.name}`} />;
        }
        return (
          <div
            className="looker-dashboard-container"
            key={`container-${resource.name}`}
          >
            {resource.showTitle && dashboardTitles[resource.name] && (
              <h4 style={{ marginLeft: '60px', marginBottom: '0' }}>
                {dashboardTitles[resource.name]}
              </h4>
            )}
            <iframe
              title={resource.name}
              key={resource.name}
              id={resource.name}
              width="100%"
              height={embedSizes[resource.name] ?? 800}
              src={resource.url}
            />
          </div>
        );
      })}
    </div>
  );
};

LookerDashboard.propTypes = {
  location: PropTypes.shape().isRequired,
};

LookerDashboard.defaultProps = {};

export default LookerDashboard;
