import React from 'react';
import PropTypes from 'prop-types';
import { Button, Col, Grid, Logo, Text } from 'taulia-ui';
import { useTranslation } from 'react-i18next';
import dompurify from 'dompurify';
import { localizeDate } from '../../Utils/localizeUtils';

function Preview({ companyName, announcement }) {
  const {
    alternateRecipientEnabled,
    acceptButtonLabel,
    declineButtonLabel,
    files,
    message,
    requiresAck,
    subject,
  } = announcement;
  const { t } = useTranslation();

  return (
    <div className="announcement-preview monolith">
      <div className="top-section">
        {t('announcements.messageFrom', { companyName })}
      </div>
      <Logo />
      <h3>{subject}</h3>
      <div
        // eslint-disable-next-line react/no-danger
        dangerouslySetInnerHTML={{
          __html: dompurify.sanitize(message),
        }}
      />
      {files?.length ? (
        <>
          <strong>{t('announcements.attachments')}</strong>
          <ul>
            {files.map(file => (
              <li key={file.attachmentId}>{file.fileName}</li>
            ))}
          </ul>
        </>
      ) : (
        ''
      )}
      {requiresAck && (
        <>
          <Grid gutter={100}>
            <Col sm={50} md={50} lg={50}>
              <div className="strong">
                {t('supplierSegment.supplierSegmentList.name')}
              </div>
              <div className="strong">{t('profile.email')}</div>
              <div className="strong" style={{ marginBottom: '45px' }}>
                {t('profile.title')}
              </div>
              <div className="strong">{t('announcements.date')}</div>
              {alternateRecipientEnabled && (
                <div className="strong">
                  {t('announcements.alternateRecipientField')}
                </div>
              )}
            </Col>
            <Col sm={50} md={50} lg={50}>
              <div>Joanna Smith</div>
              <div>joanna.smith@suppliercompany.com</div>
              <Text readOnly />
              <div>{localizeDate(new Date())}</div>
              {alternateRecipientEnabled && (
                <>
                  <Text readOnly placeholder={t('profile.firstName')} />
                  <Text readOnly placeholder={t('profile.lastName')} />
                  <Text readOnly placeholder={t('profile.email')} />
                  <Text readOnly placeholder={t('profile.title')} />
                </>
              )}
            </Col>
          </Grid>
          <p>{t('announcements.previewText')}</p>
          <div className="button-container">
            <Button>{declineButtonLabel?.value || declineButtonLabel}</Button>
            <Button theme="primary">
              {acceptButtonLabel?.value || acceptButtonLabel}
            </Button>
          </div>
        </>
      )}
      <span className="preview">{t('announcements.preview')}</span>
    </div>
  );
}

Preview.defaultProps = {
  announcement: {},
  companyName: 'Acme Birdseed',
};

Preview.propTypes = {
  announcement: PropTypes.shape({
    acceptButtonLabel: PropTypes.string,
    alternateRecipientEnabled: PropTypes.bool,
    declineButtonLabel: PropTypes.string,
    files: PropTypes.arrayOf(PropTypes.shape()),
    message: PropTypes.string,
    requiresAck: PropTypes.bool,
    subject: PropTypes.string,
  }),
  companyName: PropTypes.string,
};

export default Preview;
