import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { RequestContext } from './RequestContext';
import { genericErrorToast } from '../Utils/utils';

const DEFAULT_SUMMARY = {
  activeSuppliers: 0,
  activeUsers: 0,
  inactiveUsers: 0,
  invitableContacts: 0,
  nonInvitableContacts: 0,
  previouslyInvitedSuppliers: 0,
  suppliersPendingInvite: 0,
  totalSuppliers: 0,
  totalUsers: 0,
};

export const BulkInvitesContext = React.createContext();

const BulkInvitesProvider = ({ children }) => {
  const request = useContext(RequestContext);
  const [summaryLoading, setSummaryLoading] = useState(false);
  const [searchOptions, setSearchOptions] = useState();
  const [summary, setSummary] = useState(DEFAULT_SUMMARY);

  useEffect(() => {
    request('GET', '/api/bulkInvites/criteria/searchOptions')
      .then(res => setSearchOptions(res.body.searchOptions))
      .catch(genericErrorToast);
  }, []);

  const fetchSummary = ({
    supplierNumbers = [],
    businessUnitIds = [],
    countries = [],
    segmentIds = [],
  }) => {
    setSummaryLoading(true);
    request('POST', '/api/bulkInvites/summary')
      .send({ businessUnitIds, countries, segmentIds, supplierNumbers })
      .then(res => {
        setSummary({ ...DEFAULT_SUMMARY, ...res.body.summary });
        setSummaryLoading(false);
      })
      .catch(() => {
        setSummaryLoading(false);
      });
  };

  return (
    <BulkInvitesContext.Provider
      value={{
        fetchSummary,
        searchOptions,
        summaryLoading,
        summary,
      }}
    >
      {children}
    </BulkInvitesContext.Provider>
  );
};

BulkInvitesProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.element, PropTypes.array])
    .isRequired,
};

export default BulkInvitesProvider;
