import React, { useContext, useEffect, useReducer } from 'react';
import PropTypes from 'prop-types';
import { Trans, useTranslation } from 'react-i18next';
import validate from 'validate.js';
import {
  Button,
  Col,
  Field,
  FieldErrorMap,
  FieldLabel,
  Grid,
  Link,
  LocaleSelect,
  Modal,
  Radio,
  Select,
  Text,
  toast,
  TreeSelect,
} from 'taulia-ui';

import {
  fullAccessBusinessUnits,
  generateTreeSelectFormat,
  getSelectedBusinessUnits,
} from '../../../Utils/UserManagerUtils';
import { genericErrorToast } from '../../../Utils/utils';
import { RequestContext } from '../../../contexts';

function InviteUserModal({ businessUnits, open, onRequestClose, roles }) {
  const { i18n, t } = useTranslation();
  const request = useContext(RequestContext);

  const roleOptions = roles.map(role => {
    return { value: role, label: t(`userManager.roles.${role}`) };
  });

  const INITIAL_STATE = {
    email: '',
    firstName: '',
    lastName: '',
    locale: i18n.language,
    permissions: 'fullAccess',
    restrictions: [],
    role: roles.length ? roles[0] : '',
    errors: {},
  };

  const reducer = (prevState, updatedProperty) => ({
    ...prevState,
    ...updatedProperty,
  });
  const [state, setState] = useReducer(reducer, INITIAL_STATE);
  const treeSelectFormatBusinessUnits = generateTreeSelectFormat(businessUnits);

  useEffect(() => {
    if (roles.length) setState({ role: roles[0] });
  }, [roles]);

  const validateOnBlur = event => {
    const { value, name } = event.target;
    let error;
    if (name === 'email') {
      error = validate({ email: state.email }, { email: { email: true } })
        ? 'invalid'
        : null;
      if (!error) {
        request('GET', `/api/email/${state.email}`).then(response => {
          if (response.body.exists) {
            setState({ errors: { ...state.errors, [name]: 'inUse' } });
          }
        });
      }
    }
    if (!value.length) {
      error = 'required';
    }
    setState({ errors: { ...state.errors, [name]: error } });
  };

  const handleInputChange = event => {
    const { name, value } = event.target;
    setState({
      [name]: value,
      errors: { ...state.errors, [name]: '' },
    });
  };

  const checkForEmptyFields = () => {
    const fields = ['firstName', 'lastName', 'email'];
    const errors = {};
    fields.forEach(field => {
      if (!state[field].length) {
        errors[field] = 'required';
      }
    });
    setState({ errors });
    return Object.entries(errors).length;
  };

  const inviteUser = event => {
    event.preventDefault();
    const {
      email,
      firstName,
      lastName,
      locale,
      permissions,
      restrictions,
      role,
    } = state;
    if (checkForEmptyFields()) return;
    request('POST', '/api/user-manager/invite/users')
      .send({
        firstName,
        lastName,
        email,
        userRole: role,
        locale,
        businessUnits:
          permissions === 'fullAccess'
            ? fullAccessBusinessUnits(businessUnits)
            : restrictions,
      })
      .then(() => {
        toast.success(t('userManager.toast.inviteUserSuccess', { email }));
      })
      .catch(() => {
        genericErrorToast();
      });
    onRequestClose();
    setState(INITIAL_STATE);
  };

  return (
    <Modal width={800} open={open} onRequestClose={onRequestClose}>
      <h2>{t('userManager.inviteAUser')}</h2>
      <form onSubmit={inviteUser}>
        <Field validationState={state.errors.email ? 'error' : null}>
          <FieldLabel htmlFor="email">{t('profile.email')}</FieldLabel>
          <Text
            id="email"
            name="email"
            onBlur={validateOnBlur}
            onChange={handleInputChange}
            validationState={state.errors.email ? 'error' : null}
            value={state.email}
          />
          <FieldErrorMap code={state.errors.email}>
            <span key="required">{t('error.requiredField')}</span>
            <span key="invalid">{t('profile.errors.email.invalid')}</span>
            <span key="inUse">{t('profile.errors.email.inUse')}</span>
          </FieldErrorMap>
        </Field>
        <Grid gutter={20} style={{ marginBottom: '10px' }}>
          <Col sm={50} md={50} lg={50}>
            <Field validationState={state.errors.firstName ? 'error' : null}>
              <FieldLabel htmlFor="first-name">
                {t('profile.firstName')}
              </FieldLabel>
              <Text
                id="first-name"
                name="firstName"
                onBlur={validateOnBlur}
                onChange={handleInputChange}
                validationState={state.errors.firstName ? 'error' : null}
                value={state.firstName}
              />
              <FieldErrorMap code={state.errors.firstName}>
                {t('error.requiredField')}
              </FieldErrorMap>
            </Field>
            <FieldLabel htmlFor="role">{t('supplierView.role')}</FieldLabel>
            <Select
              enableSearch={false}
              id="role"
              name="role"
              multiple={false}
              options={roleOptions}
              onChange={role => setState({ role })}
              value={state.role}
            />
          </Col>
          <Col sm={50} md={50} lg={50}>
            <Field validationState={state.errors.lastName ? 'error' : null}>
              <FieldLabel htmlFor="last-name">
                {t('profile.lastName')}
              </FieldLabel>
              <Text
                id="last-name"
                validationState={state.errors.lastName ? 'error' : null}
                value={state.lastName}
                name="lastName"
                onChange={handleInputChange}
                onBlur={validateOnBlur}
              />
              <FieldErrorMap code={state.errors.lastName}>
                {t('error.requiredField')}
              </FieldErrorMap>
            </Field>
            <LocaleSelect
              locale={{ value: state.locale }}
              updateLocale={locale => setState({ locale })}
            />
          </Col>
        </Grid>
        <p style={{ marginBottom: '20px' }}>
          <Trans
            i18nKey="userManager.roleDesc"
            components={[
              <Link to="https://customersupport.taulia.com/articles/en_US/Agent_Knowledge_Base/Q-How-can-I-add-or-remove-users-in-the-Buyer-UI-000008765/" />,
            ]}
          />
        </p>
        <Radio
          aria-label="fullAccess"
          checked={state.permissions === 'fullAccess'}
          onChange={event => setState({ permissions: event.target.value })}
          value="fullAccess"
        >
          <strong>{t('userManager.fullAccess')}</strong>
          <p style={{ marginBottom: '10px' }}>
            {t('userManager.fullAccessDesc')}
          </p>
        </Radio>
        <Radio
          aria-label="applyRestrictions"
          checked={state.permissions === 'applyRestrictions'}
          onChange={event => setState({ permissions: event.target.value })}
          value="applyRestrictions"
        >
          <strong>{t('userManager.applyRestrictions')}</strong>
          <p style={{ marginBottom: '10px' }}>
            {t('userManager.applyRestrictionsDesc')}
          </p>
        </Radio>
        {state.permissions === 'applyRestrictions' && (
          <TreeSelect
            id="business-unit-ids"
            name="businessUnitIds"
            data={treeSelectFormatBusinessUnits}
            onSelectionChange={selectedNodes =>
              setState({
                restrictions: getSelectedBusinessUnits(
                  selectedNodes,
                  treeSelectFormatBusinessUnits
                ),
              })
            }
          />
        )}
        <div style={{ marginTop: '20px', float: 'right' }}>
          <Button
            disabled={
              state.errors.firstName ||
              state.errors.lastName ||
              state.errors.email
            }
            type="submit"
            theme="primary"
          >
            {t('userManager.inviteUser')}
          </Button>
        </div>
      </form>
    </Modal>
  );
}

InviteUserModal.propTypes = {
  businessUnits: PropTypes.arrayOf(
    PropTypes.shape({
      availableBusinessUnits: PropTypes.arrayOf(
        PropTypes.shape({
          address: PropTypes.shape({
            city: PropTypes.string,
            county: PropTypes.string,
            name: PropTypes.string,
          }),
          buyerId: PropTypes.string,
          id: PropTypes.string,
          name: PropTypes.string,
          number: PropTypes.string,
        })
      ),
      companyId: PropTypes.string,
      companyName: PropTypes.string,
    })
  ),
  onRequestClose: PropTypes.func.isRequired,
  open: PropTypes.bool,
  roles: PropTypes.arrayOf(PropTypes.string),
};

InviteUserModal.defaultProps = {
  businessUnits: [],
  open: true,
  roles: ['BUYER_ADMIN', 'BUYER_SEARCH_ONLY'],
};

export default InviteUserModal;
