/* eslint-disable react/prop-types */
import React from 'react';
import { Link } from 'react-router-dom';
import { Progress } from 'taulia-ui';
import { withTranslation } from 'react-i18next';
import { kebabCase } from 'lodash';
import LookerDashboard from './LookerDashboard';
import { UserSettingsContext } from '../../contexts';

class Home extends React.Component {
  static defaultProps = {
    settings: {
      isRequestEpEnabled: false,
      isInvoiceSubmitEnabled: false,
    },
  };

  state = {
    quickLinksNav: null,
  };

  componentDidMount() {
    if (this.shouldShowQuicklinks()) {
      const { request } = this.props;
      request('GET', '/api/menu/buyer_area_dashboard')
        .send()
        .then(response => {
          this.setState({ quickLinksNav: response.body });
        });
    }
  }

  hasAnalystRole = () => {
    const { userRoles } = this.context;
    return !!userRoles.find(el => {
      return (
        el === 'INTERNAL_ANALYST' ||
        el === 'BUYER_ADMIN' ||
        el === 'BUYER_CASH_MANAGER'
      );
    });
  };

  shouldShowQuicklinks = () => {
    const { settings } = this.props;
    return (
      !this.hasAnalystRole() ||
      (!settings.isRequestEpEnabled && !settings.isInvoiceSubmitEnabled)
    );
  };

  static contextType = UserSettingsContext;

  renderQuickLinks = () => {
    const { quickLinksNav } = this.state;
    const { t } = this.props;
    const menuItems = quickLinksNav ? quickLinksNav.children : [];
    const width = Math.round((1 / menuItems.length) * 100);
    const dasboardMenu = menuItems.map(menuItem => {
      return (
        <li
          style={{ width: `${width}%` }}
          className={`dashboard-menu-item ${kebabCase(menuItem.name)}`}
          key={menuItem.name}
        >
          <Link to={menuItem.path}>
            <span>{t(menuItem.label)}</span>
          </Link>
        </li>
      );
    });
    return <ul>{dasboardMenu}</ul>;
  };

  renderAnalytics = () => {
    return <LookerDashboard {...this.props} />;
  };

  render() {
    const { quickLinksNav } = this.state;
    return (
      <div>
        {this.shouldShowQuicklinks() ? (
          <div className="home">
            {!quickLinksNav ? <Progress /> : this.renderQuickLinks()}
          </div>
        ) : (
          this.renderAnalytics()
        )}
      </div>
    );
  }
}

export default withTranslation()(Home);
