import { isEmpty } from 'lodash';

export function createSupplierCsv(data) {
  if (isEmpty(data)) {
    return null;
  }

  function checkForUndefined(arg) {
    if (arg == null) {
      return ' ';
    }
    return arg;
  }

  const headers =
    'Supplier Number,Supplier Name,Enrollment State,Address Line 1,City,Country,Region,Zip Code,Supplier Address Type';
  let csv = headers.concat('\r\n');
  data.forEach(supplier => {
    csv += '"' + checkForUndefined(supplier.supplierNumber) + '",';
    csv += '"' + checkForUndefined(supplier.supplierName) + '",';
    csv += '"' + checkForUndefined(supplier.enrollmentState) + '",';
    csv += '"' + checkForUndefined(supplier.address.addressLine1) + '",';
    csv += '"' + checkForUndefined(supplier.address.city) + '",';
    csv += '"' + checkForUndefined(supplier.address.country) + '",';
    csv += '"' + checkForUndefined(supplier.address.region) + '",';
    csv += '"' + checkForUndefined(supplier.address.zipCode) + '",';
    csv += '"' + checkForUndefined(supplier.address.supplierAddressType) + '"';
    csv += '\n';
  });

  return csv;
}

export function downloadCsvFile(fileName, csv) {
  if (csv == null) {
    return;
  }

  if (isEmpty(fileName)) {
    fileName = `download-${new Date().getTime()}.csv`;
  }

  if (!csv.match(/^data:text\/csv/i)) {
    csv = 'data:text/csv;charset=utf-8,'.concat(csv);
  }

  const data = encodeURI(csv);
  const link = document.createElement('a');
  link.setAttribute('href', data);
  link.setAttribute('download', fileName);
  document.body.appendChild(link); // Required for FF
  link.click();
}

export function createCsvFileName(stringValue) {
  const MAX_STRING_LEN = 30;
  const now = new Date();

  if (stringValue.length > MAX_STRING_LEN) {
    stringValue = stringValue.substring(0, MAX_STRING_LEN);
  }
  return `${stringValue.trim()} - ${now.getFullYear()}${now.getMonth() +
    1}${now.getDate()}.csv`;
}

export function userCanEditSuppliers(userRoles) {
  const editRoles = [
    'BUYER_ADMIN',
    'BUYER_SUPPLIER_LAUNCH_SEARCH',
    'BUYER_USER',
  ];
  return editRoles.some(role => {
    return userRoles.includes(role);
  });
}

