import { i18next } from 'taulia-ui';

// Currency
export function localizeCurrency(value, currency = 'USD') {
  if (value === null || Number.isNaN(Number(value))) {
    return '';
  }
  return Number(value).toLocaleString(i18next.language, {
    style: 'currency',
    currency,
  });
}

// Number
export function localizeNumber(value) {
  if (value === null || Number.isNaN(Number(value))) {
    return '';
  }
  return Number(value).toLocaleString(i18next.language);
}

// Time
export function localizeDate(date) {
  let formattedDate;
  if (date) {
    formattedDate = new Date(date).toLocaleDateString(i18next.language);
  }
  if (!formattedDate || formattedDate === 'Invalid Date') {
    return '';
  }
  return formattedDate;
}

export function localizeTime(date) {
  let formattedTime;
  if (date) {
    formattedTime = new Date(date).toLocaleTimeString(i18next.language);
  }
  if (!formattedTime || formattedTime === 'Invalid Date') {
    return '';
  }
  return formattedTime;
}

export function localizeDateTime(date) {
  let formattedTime;
  if (date) {
    formattedTime = new Date(date).toLocaleString(i18next.language);
  }
  if (!formattedTime || formattedTime === 'Invalid Date') {
    return '';
  }
  return formattedTime;
}
