import React, { useContext, useState } from 'react';
import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import {
  ActionsCell,
  Alert,
  Button,
  Link,
  ListView,
  Modal,
  toast,
} from 'taulia-ui';
import SegmentForm from './SegmentForm';
import {
  createCsvFileName,
  createSupplierCsv,
  downloadCsvFile,
  userCanEditSuppliers,
} from '../../Utils/SupplierUtils';
import { constructSearchQueryUrl } from '../../Utils/utils';
import CONSTANTS from '../../globals';
import { RequestContext, UserSettingsContext } from '../../contexts';

const SegmentsList = ({
  alerts,
  clearAlerts,
  loadSegments,
  segments,
  viewDeletedSegments,
}) => {
  const initialState = {
    showCreateModal: false,
    showDeleteModal: false,
    deleteSegmentId: null,
    segmentToEdit: null,
  };

  const { t } = useTranslation();
  const history = useHistory();
  const [state, setState] = useState(initialState);
  const request = useContext(RequestContext);
  const { userRoles } = useContext(UserSettingsContext);

  const {
    deleteSegmentId,
    segmentToEdit,
    showCreateModal,
    showDeleteModal,
  } = state;

  const addSuppliers = segmentId => {
    history.push(
      CONSTANTS.SUPPLIERS.SUPPLIER_SEGMENTS.ADD_SUPPLIERS.replace(
        ':id',
        segmentId
      )
    );
  };

  const openCreateEditModal = selectedSegment => {
    setState({
      segmentToEdit: selectedSegment,
      showCreateModal: true,
    });
  };

  const confirmDeleteSegment = segmentId => {
    setState({
      showDeleteModal: true,
      deleteSegmentId: segmentId,
    });
  };

  const cancelDeleteSegment = () => {
    setState({
      showDeleteModal: false,
      deleteSegmentId: null,
    });
  };

  const closeCreateSegmentModal = () => {
    setState({
      showCreateModal: false,
      segmentToEdit: null,
    });
  };

  const deleteSegment = () => {
    const segment = segments.filter(s => s.id === deleteSegmentId);
    const segmentName = segment[0].name;

    request('DELETE', `/api/segment/${deleteSegmentId}`)
      .then(() => {
        setState({
          deleteSegmentId: null,
          showDeleteModal: false,
        });
        loadSegments();
        toast.success(
          t('supplierSegment.supplierSegmentList.segmentWasDeleted', {
            segmentName,
          })
        );
      })
      .catch(() => {
        setState({
          showDeleteModal: false,
          deleteSegmentId: null,
        });
        toast.error(
          t('supplierSegment.alert.errorDeletingSegments', { segmentName })
        );
      });
  };

  const downloadToCSV = (id, name) => {
    const fileName = createCsvFileName(name);
    request('GET', `/api/segment/${id}/suppliers`).then(res => {
      downloadCsvFile(fileName, createSupplierCsv(res.body.suppliers));
    });
  };

  const getActionsCellActions = data => {
    if (!userCanEditSuppliers(userRoles)) {
      return [];
    }
    const items = [
      {
        action: () => openCreateEditModal(data),
        label: t('standard.edit'),
      },
      {
        action: () => addSuppliers(data.id),
        label: t('supplierSegment.supplierSegmentList.addSuppliers'),
      },
      {
        action: () => confirmDeleteSegment(data.id),
        label: t(t('standard.delete')),
      },
    ];
    if (data.numSuppliers > 0) {
      items.push({
        action: () => downloadToCSV(data.id, data.name),
        label: t('supplierSegment.segmentAddSupplierForm.downloadToCsv'),
      });
    }
    return items;
  };

  const linkedCell = ({ id, value }) => (
    <Link to={constructSearchQueryUrl('segments', [id])}>{value}</Link>
  );

  const getFieldsConfig = () => {
    return [
      {
        key: 'name',
        label: t('supplierSegment.supplierSegmentList.name'),
        type: 'TEXT',
        width: 350,
        cell: linkedCell,
      },
      {
        key: 'description',
        label: t('supplierSegment.supplierSegmentList.description'),
        type: 'TEXT',
        width: 350,
      },
      {
        key: 'createdBy',
        label: t('supplierSegment.supplierSegmentList.createdBy'),
        type: 'TEXT',
        width: 250,
      },
      {
        key: 'numSuppliers',
        label: t('bulkInvites.supplierSummary.suppliers.title'),
        type: 'NUMBER',
        align: 'center',
        width: 150,
      },
      {
        key: 'actions',
        label: '',
        type: 'TEXT',
        width: 150,
        // eslint-disable-next-line react/prop-types
        cell: ({ object }) => {
          return <ActionsCell items={getActionsCellActions(object)} />;
        },
      },
    ];
  };

  const renderAlert = () => {
    if (!alerts || alerts.length === 0) {
      return null;
    }

    const alertItems = alerts.map(alert => (
      <div key={alert.text} style={{ paddingTop: '5px', paddingButton: '5px' }}>
        <Alert theme={alert.theme}>
          <p>{alert.text}</p>
          {alert.comp}
        </Alert>
      </div>
    ));
    setTimeout(() => clearAlerts(), 15000);
    return <div className="alerts">{alertItems}</div>;
  };

  const renderCreateEditModal = () => (
    <Modal
      open={showCreateModal}
      width={600}
      onRequestClose={closeCreateSegmentModal}
    >
      <div id="createModal" style={{ padding: '15px' }}>
        <SegmentForm
          cancelAction={closeCreateSegmentModal}
          closeCreateSegmentModal={closeCreateSegmentModal}
          loadSegments={loadSegments}
          request={request}
          segmentToEdit={segmentToEdit || undefined}
          segments={segments}
          t={t}
        />
      </div>
    </Modal>
  );

  const renderConfirmDeleteModal = () => {
    return (
      <Modal
        open={showDeleteModal}
        width={400}
        onRequestClose={cancelDeleteSegment}
      >
        <div id="deleteModal" style={{ padding: '15px' }}>
          <h3>{t('supplierSegment.supplierSegmentList.modal.title')}</h3>
          <p>{t('supplierSegment.supplierSegmentList.modal.info')}</p>
        </div>
        <div style={{ float: 'right' }}>
          <Button
            theme="text"
            onKeyDown={cancelDeleteSegment}
            onClick={cancelDeleteSegment}
          >
            {t('standard.cancel')}
          </Button>
          <Button
            data-testid="delete-segment-button"
            onClick={deleteSegment}
            theme="primary"
          >
            {t('standard.delete')}
          </Button>
        </div>
      </Modal>
    );
  };

  const fieldsConfig = getFieldsConfig();
  const data = segments;

  return (
    <div className="supplier-segment">
      {renderAlert()}
      <p>{t('supplierSegment.supplierSegmentList.overview')}</p>
      {userCanEditSuppliers(userRoles) && (
        <div style={{ margin: '10px 0' }} id="createSegmentButton">
          <Button
            theme="primary"
            onClick={() => setState({ showCreateModal: true })}
          >
            {t('supplierSegment.supplierSegmentList.createSegment')}
          </Button>
        </div>
      )}
      <ListView
        columns={fieldsConfig}
        data={data}
        id="segmentListView"
        isResizable={false}
        noResultsText={
          <p>{t('supplierSegment.supplierSegmentList.emptyResults')}</p>
        }
        showColumnConfig={false}
        showCount={false}
        showDataLength={false}
      />
      <Button
        id="viewDeletedSegmentsLink"
        onClick={viewDeletedSegments}
        style={{ marginTop: '10px' }}
        theme="text"
      >
        {t('supplierSegment.supplierSegmentList.viewDeletedSegments')}
      </Button>
      {renderConfirmDeleteModal()}
      {renderCreateEditModal()}
    </div>
  );
};

export default SegmentsList;

SegmentsList.propTypes = {
  alerts: PropTypes.arrayOf(PropTypes.shape()),
  clearAlerts: PropTypes.func,
  loadSegments: PropTypes.func,
  segments: PropTypes.arrayOf(PropTypes.shape()),
  viewDeletedSegments: PropTypes.func,
};

SegmentsList.defaultProps = {
  alerts: [],
  clearAlerts: null,
  loadSegments: null,
  segments: [],
  viewDeletedSegments: null,
};
