import React from 'react';
import PropTypes from 'prop-types';
import { isEmpty } from 'lodash';
import {
  Button,
  Field,
  FieldLabel,
  FieldErrorMap,
  FileUpload,
  Link,
  Progress,
  Tabs,
  Textarea,
} from 'taulia-ui';
import { withTranslation, Trans } from 'react-i18next';
import SupplierSummary from './SupplierSummary';
import CONSTANTS from '../../globals';
import {
  parseInput,
  downloadSampleSupplierInviteList,
} from '../../Utils/utils';
import { BulkInvitesContext } from '../../contexts';

class BulkInvitesIndividualPage extends React.Component {
  state = {
    file: null,
    input: { value: '' },
    loading: true,
    option: 'upload',
    upload: { value: null, error: null },
  };

  static propTypes = {
    request: PropTypes.func,
    t: PropTypes.func,
    history: PropTypes.shape().isRequired,
  };

  static defaultProps = {
    request: null,
    t: null,
  };

  componentDidMount() {
    const { request } = this.props;
    request('GET', '/api/bulkInvites/criteria')
      .then(res => {
        const { criteria } = res.body;
        if (criteria && criteria.supplierNumbers) {
          const value = parseInput(criteria.supplierNumbers).join(', ');
          this.setState(
            {
              loading: false,
              input: { value },
              option: value ? 'input' : 'upload',
            },
            this.fetchSummaryInitial
          );
        } else {
          this.setState({ loading: false });
        }
      })
      .catch(() => {
        this.setState({ loading: false });
      });
  }

  onUploadChange = file => {
    this.setState({
      file,
      loading: false,
      upload: { value: file.name, error: null },
    });
  };

  onUploadSubmit = event => {
    const { request } = this.props;
    const { file, upload } = this.state;
    event.preventDefault();
    this.setState({ loading: true }, () => {
      const data = new FormData();
      data.append('file', file);
      request('POST', '/api/bulkInvites/criteria/supplierNumbersUpload')
        .send(data)
        .then(() => this.navigateToNext())
        .catch(err => {
          this.setState({
            upload: {
              value: upload.value,
              error: err.response.body.code,
            },
            loading: false,
          });
        });
    });
  };

  onInputChange = event => {
    this.setState({
      input: { value: event.target.value },
      loading: false,
    });
  };

  onInputBlur = () => {
    const { input } = this.state;
    this.setState(
      {
        input: { value: parseInput(input.value).join(', ') },
      },
      this.fetchSummary
    );
  };

  onInputSubmit = event => {
    const { request } = this.props;
    const { input } = this.state;
    event.preventDefault();
    this.setState({ loading: true }, () => {
      request('POST', '/api/bulkInvites/criteria')
        .send({ supplierNumbers: parseInput(input.value) })
        .then(() => this.navigateToNext())
        .catch(() => this.navigateToNext());
    });
  };

  fetchSummaryInitial = () => {
    const { input } = this.state;
    if (input.value.length > 0) {
      this.fetchSummary();
    }
  };

  fetchSummary = () => {
    const { fetchSummary } = this.context;
    const { input } = this.state;

    fetchSummary({
      supplierNumbers: parseInput(input.value),
    });
  };

  navigateToNext = () => {
    const { history } = this.props;
    history.push(CONSTANTS.SUPPLIERS.BULK_INVITES.REVIEW);
  };

  static contextType = BulkInvitesContext;

  renderSubmit = () => {
    const { t } = this.props;
    const { input, option, upload } = this.state;
    const selectedOption = option === 'input' ? input : upload;
    const disabled = isEmpty(selectedOption.value) || selectedOption.error;
    return (
      <div className="invites-button-container">
        <Link to={CONSTANTS.SUPPLIERS.BULK_INVITES.START}>
          &lt;&nbsp;
          {t('bulkInvites.individualPage.back')}
        </Link>
        <Button type="submit" theme="primary" disabled={disabled}>
          {t('bulkInvites.individualPage.submit')}
        </Button>
      </div>
    );
  };

  render() {
    const { summary } = this.context;
    const { t } = this.props;
    const { file, input, loading, option, upload } = this.state;

    const tabData = ['upload', 'input'].map(tab => ({
      active: tab === option,
      callback: () => this.setState({ option: tab }),
      label: t(`bulkInvites.individualPage.options.${tab}.link`),
    }));

    if (loading) {
      return <Progress />;
    }

    return (
      <div className="individual-page">
        <h1>{t('bulkInvites.title')}</h1>
        <h3>{t('bulkInvites.individualPage.title')}</h3>
        <p>{t('bulkInvites.individualPage.text')}</p>
        <Tabs data={tabData} />
        {option === 'upload' && (
          <form className="page-form" onSubmit={this.onUploadSubmit}>
            <div className="form-content option-upload">
              <Field validationState={upload.error ? 'error' : null}>
                <FieldLabel htmlFor="file-upload">
                  {t('bulkInvites.individualPage.options.upload.label')}
                </FieldLabel>
                <FileUpload
                  acceptedFileTypes={['.csv', '.xls', '.xlsx']}
                  file={file}
                  onChange={this.onUploadChange}
                />
                <FieldErrorMap code={upload.error}>
                  <span key="invalidType">
                    {t(
                      'bulkInvites.individualPage.options.upload.error.invalidType'
                    )}
                  </span>
                  {t(
                    'bulkInvites.individualPage.options.upload.error.noContent'
                  )}
                </FieldErrorMap>
                <p>
                  <Trans
                    i18nKey="bulkInvites.individualPage.options.upload.info"
                    components={[
                      <Button
                        style={{ display: 'inline-block' }}
                        theme="text"
                        onClick={downloadSampleSupplierInviteList}
                      />,
                    ]}
                  />
                </p>
              </Field>
            </div>
            {this.renderSubmit()}
          </form>
        )}
        {option === 'input' && (
          <form className="page-form" onSubmit={this.onInputSubmit}>
            <div className="summary-container">
              <Field>
                <FieldLabel htmlFor="input">
                  {t('bulkInvites.individualPage.options.input.label')}
                </FieldLabel>
                <Textarea
                  id="input"
                  name="input"
                  onBlur={this.onInputBlur}
                  onChange={this.onInputChange}
                  value={input.value}
                />
              </Field>
              {summary && input.value.length > 0 && <SupplierSummary />}
            </div>
            {this.renderSubmit()}
          </form>
        )}
      </div>
    );
  }
}

export default withTranslation()(BulkInvitesIndividualPage);
